        <loading :state="loadState" />

        <div v-if="list.length > 0">
            <el-table :data="list" style="width: 100%" height="600px">
                <el-table-column label="结算" width="33%" header-align="center" align="center">
                    <el-table-column prop="SETTLE_income_time" label="时间" header-align="center" align="center"></el-table-column>
                    <el-table-column prop="SETTLE" label="金额(元)" header-align="center" align="center"></el-table-column>
                </el-table-column>
