<script>
export default {
    data() {
        return {
            custId: "1000770227",
            list: [],
            loadState: ""
        };
    },
    mounted() {
        if (this.$route.query.cust_id > 0) {
            this.ad_id = this.$route.query.cust_id;
            this.search();
        } else {
            this.randomSelectCustomer();
        }
    },
    methods: {
        search: function() {
            var reg = /^[0-9]+.?[0-9]*$/;

            if (this.custId == "") {
                this.$alert("请输入广告主id");

                return false;
            }

            if (!reg.test(this.custId)) {
                this.$alert("广告主id不能含有非数字字母");

                return false;
            }

            this.getChargeList();
        },
        async getChargeList() {
            this.list = [];

            this.loadState = "loading";

            let res = await this.$api.insights.getChargeList(this.custId);
            this.loadState = "";
            if (res.errcode != 0) {
                this.$alert("暂时获取不到该广告主的财务误差信息");
                return;
            }
            this.list = res.data;
            if (this.list.length == 0) {
                this.loadState = "暂无该广告主的充值流水信息";
            }
            this.$forceUpdate();
        },
        async randomSelectCustomer() {
            this.tableShow = false;

            let res = await this.$api.insights.getRandomSelectAdPlan();
            this.$set(this, "custId", res.data.customer_id);
            this.search();
        }
    }
};
</script>
