<template>
    <div>
        <el-row>
            <el-form :inline="true" style="padding-left:20px" label-width="80px">
                <el-col :span="7">
                    <el-form-item label="广告主ID">
                        <el-input
                            v-model="custId"
                            placeholder="请输入广告主ID"
                            @keyup.enter.native="search"
                        ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="广告ID">
                        <el-input v-model="adId" placeholder="请输入广告ID" @keyup.enter.native="search"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="ocpx状态">
                        <el-select v-model="ocpxStatus">
                            <el-option label="全部" value="0"></el-option>
                            <el-option label="ocpx-1" value="1"></el-option>
                            <el-option label="ocpx-2" value="2"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="营销目标">
                        <el-select v-model="promotionObjective">
                            <el-option label="全部" value="0"></el-option>
                            <el-option label="推广APP" value="88030004"></el-option>
                            <el-option label="收集销售线索" value="88030003"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="7" style="margin-left:40px">
                    <el-form-item>
                        <el-button @click="search" type="primary" style="width: 169px;">查 询</el-button>
                    </el-form-item>
                </el-col>
            </el-form>
        </el-row>

        <el-row>
            <el-table :data="list" stripe height="700px">
                <el-table-column label="计划ID">
                    <template slot-scope="scope">
                        <adlink :adId="scope.row.ad_id" />
                    </template>
                </el-table-column>
                <el-table-column label="ctr">
                    <template slot-scope="scope">
                        <el-button
                            type="primary"
                            size="mini"
                            @click="getAdStatChart(scope.row.ad_id, 'ctr')"
                        >ctr曲线</el-button>
                    </template>
                </el-table-column>
                <el-table-column label="广告主ID" prop="customer_id"></el-table-column>
                <el-table-column label="营销目标" prop="promotion_objective"></el-table-column>
                <el-table-column label="曝光量" prop="pv_count"></el-table-column>
                <el-table-column label="cpl转化量" prop="cpl_count"></el-table-column>
                <el-table-column label="app激活量" prop="activate_count"></el-table-column>
            </el-table>
            <el-pagination
                @current-change="pageChange"
                :current-page.sync="page"
                layout="total, prev, pager, next"
                :total="total"
            ></el-pagination>
        </el-row>

        <el-dialog :visible.sync="showCtrChart" width="70%">
            <p>ctr曲线</p>
            <el-divider></el-divider>
            <drawchart
                :chartData="adStatChartData"
                chartId="adStatCtrChartId"
                lengendPosition="top"
                percentItem="1"
            />
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            loadState: "",
            adId: "",
            custId: "",
            ocpxStatus: "0",
            promotionObjective: "0",
            total: 0,
            list: [],
            page: 1,
            showCtrChart: false,
            adStatChartData: {
                title: "",
                data: {},
                xAixs: []
            }
        };
    },
    mounted() {
        this.getOcpxAdStatList();
    },
    methods: {
        async getOcpxAdStatList() {
            this.list = [];

            var sendParam = {
                page: this.page,
                ad_id: this.adId,
                customer_id: this.custId,
                ocpx_status: this.ocpxStatus,
                promotion_objective: this.promotionObjective
            };
            let res = await this.$api.insights.getOcpxAdStatList(sendParam);

            if (res.errcode == 0) {
                this.list = res.data.list;
                this.total = res.data.total_page;
            } else {
                this.$message("暂无数据");
            }
        },
        pageChange(num) {
            this.page = num;
            this.getOcpxAdStatList();
        },
        search: function() {
            this.page = 1;
            this.getOcpxAdStatList();
        },
        async getAdStatChart(adId, dataType) {
            this.showCtrChart = true;
            let res = await this.$api.insights.getAdCtrChart(adId, dataType);
            if (res.errcode != 0) {
                this.adStatChartData = {
                    title: "",
                    data: {},
                    xAixs: []
                };
                return;
            }

            this.adStatChartData = res.data;
        }
    }
};
</script>
