<template>
    <div class="kepler-reserve-detail">
        <el-form :inline="true" class="demo-form-inline" style="margin:10px 0 0 20px">
            <el-form-item label="广告主ID:">
                <el-input v-model="custId" placeholder="请输入广告主ID"></el-input>
            </el-form-item>
            <el-form-item label="开始时间:">
                <el-date-picker v-model="currentDate" type="date" placeholder="选择日期"  value-format="yyyy-MM-dd"></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="search">查询</el-button>
            </el-form-item>
        </el-form>
        

        <div class="row" v-show="reserveTotalList.length > 0">
            <div class="col-md-12" style="overflow: scroll;">
                <h3>广告主每日预定量</h3>
                <table class="table table-bordered table-hover table-responsive">
                    <thead>
                        <tr class="info">
                            <th>预定日期</th>
                            <th>预定量</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item, index) in reserveTotalList" :key="index">
                            <td>{{ item.orderdate }}</td>
                            <td>{{ item.impression }}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>

        <el-row v-show="detailList.length > 0">
            <div class="col-md-12" style="overflow: scroll;">
                <h3>广告主预定详情</h3>
                <table class="table table-bordered table-hover table-responsive">
                    <thead>
                        <tr class="info">
                            <th>预定日期</th>
                            <th width="30%">订单ID</th>
                            <th>预定量</th>
                            <th>预定详情</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item, index) in detailList" :key="index">
                            <td>{{ item.order_date }}</td>
                            <td>{{ item.orderid }}</td>
                            <td>{{ item.impression }}</td>
                            <td
                                data-toggle="modal"
                                data-target="#myModal"
                                @click="originalData = JSON.parse(item.Dimension); copyDesc = '点击复制'"
                            >预定详情</td>
                        </tr>
                    </tbody>
                </table>
                <nav class="pagination-wrap">
                    <ul class="pagination">
                        <li
                            :class="currentPage === 1 ? 'disabled' : ''"
                            @click="changePage('prev', currentPage === 1)"
                        >
                            <a aria-label="Previous">
                                <span aria-hidden="true">&laquo;</span>
                            </a>
                        </li>
                        <li
                            :class="currentPage === item ? 'active' : ''"
                            v-for="item in totalPages"
                            :key="item"
                            @click="changePage(item)"
                        >
                            <a>{{ item }}</a>
                        </li>
                        <li
                            :class="currentPage === totalPages ? 'disabled' : ''"
                            @click="changePage('next', currentPage === totalPages)"
                        >
                            <a aria-label="Next">
                                <span aria-hidden="true">&raquo;</span>
                            </a>
                        </li>
                    </ul>
                </nav>
            </div>
        </el-row>
        <loading :state="loadState" />

        <div
            class="modal fade"
            tabindex="-1"
            role="dialog"
            aria-labelledby="myLargeModalLabel"
            id="myModal"
        >
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content">
                    <span class="copy-btn" @click="copy">{{ copyDesc }}</span>
                    <json-viewer :value="originalData" :expand-depth="3" boxed sort></json-viewer>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import * as CONST from "@/constant";
import JsonViewer from "vue-json-viewer";
import dateFormat from "@/utils/dateFormat";
const copy = require("clipboard-copy");
let totalData = [];

export default {
    components: {
        JsonViewer
    },
    data() {
        return {
            detailList: [],
            reserveTotalList: [],
            pageSize: 30,
            currentPage: 1,
            // 总页数
            totalPages: 0,
            loadState: "暂无数据",
            custId: "",
            currentDate: "",
            originalData: {},

            copyDesc: "点击复制"
        };
    },
    mounted() {
        this.currentDate = dateFormat(new Date().getTime());
    },
    methods: {
        //下两个函数后端无返回数据
        async getDetailList() {
            this.loadState = "loading";

            let res = await this.$api.insights.getDetailList(
                parseInt(this.custId),
                this.currentDate
            );
            let body = JSON.parse(res);
            console.log(body);
            if (body.errcode === 0 && body.data.cust_info) {
                this.loadState = "ok";
                let data = body.data.cust_info;
                this.totalPages = Math.ceil(data.length / this.pageSize);
                for (let i = 0; i < this.totalPages; i++) {
                    totalData.push(
                        data.slice(i * this.pageSize, (1 + i) * this.pageSize)
                    );
                }
                this.detailList = totalData[0];
                return;
            }
            this.loadState = "暂无数据";
        },

        async getReserveTotalList() {
            let res = await this.$api.insights.getReserveTotalList(
                parseInt(this.custId),
                this.currentDate
            );
            let body = JSON.parse(res);
            if (body.errcode === 0) {
                this.reserveTotalList = (body.data && body.data.date_cnt) || [];
                return;
            }
        },

        changePage(num, flag) {
            if (typeof num === "number") {
                this.currentPage = num;
                this.detailList = totalData[num - 1];
                return;
            }
            if (num === "prev" && !flag) {
                this.currentPage = this.currentPage - 1;
                this.detailList = totalData[this.currentPage - 1];
                return;
            }
            if (num === "next" && !flag) {
                this.currentPage = this.currentPage + 1;
                this.detailList = totalData[this.currentPage - 1];
            }
        },

        search: function() {
            this.currentPage = 1;
            this.getDetailList();
            this.getReserveTotalList();
        },

        copy() {
            copy(JSON.stringify(this.originalData));
            this.copyDesc = "已复制";
        }
    }
};
</script>

<style lang="less">
.kepler-reserve-detail {
    .pagination-wrap {
        text-align: center;
    }
    .jv-container .jv-code {
        min-height: 500px;
        overflow: auto;
    }
    .copy-btn {
        position: absolute;
        z-index: 1;
        right: 30px;
        top: 20px;
        color: #2dc169;
        cursor: pointer;
    }
}
</style>

