<template>
    <div class="lean-matrix-wrapper">
        <el-form
            :inline="true"
            label-width="70px"
            style="padding-top:20px"
        >
            <el-row>
                <el-col :span="9">
                    <el-form-item label="请求ID">
                        <el-input v-model="pvId" placeholder="请输入请求ID" style="width:240px"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="产品线">
                        <el-select v-model="defalut783">
                            <el-option label="超级粉丝通" value="783"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="5">
                    <el-button type="primary" @click="getMatrixData">
                        <span>查 询</span>
                    </el-button>
                    <el-button type="success" @click="getOnePvId()">手气不错</el-button>
                </el-col>
            </el-row>
        </el-form>

        <div class="page-header">
            <h4>精益矩阵</h4>
        </div>
        <div v-show="sectionBarState.matDetail">
            <div class="matrix-layout" v-show="!isEmpty && !isLoading">

                <LeanMatrixComponent
                    :theFunnelData="funnelData"
                    :componentName="componentName"
                    @showMatrixFilter="showMatrixFilter"
                >
                    <template slot-scope="detail">
                        <span style="font-size: 12px">剩余数:{{detail.funnelDetail.cnt}}</span>
                    </template>
                </LeanMatrixComponent>
            </div>
        </div>
        <loading :state="loadState" />

        <el-dialog title="详情" :visible.sync="bidTypeScatId" width="75%">
            <div class="matrix-layout-item-tooltips">
                <div class="item">
                    <p class="remain">剩余数:</p>
                    <span>{{modalData.cnt}}</span>
                </div>
                <div class="item">
                    <p class="filter">过滤数:</p>
                    <span>{{modalData.filter_cnt}}</span>
                </div>
                <div class="item">
                    <p class="filter-rate">过滤占比:</p>
                    <span>{{Number(modalData.filter_ratio * 100).toFixed(2)}}%</span>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
// 引入 ECharts 主模块

import sceneFlow from "@/config/SceneFlow.js";
import lodash from "lodash";
import LeanMatrixComponent from "@/Insight/View/LeanMatrix";

export default {
    components: {
        LeanMatrixComponent
    },
    data() {
        return {
            componentName: "LeanMatrixTraffic",
            funnelData: [],
            funnelNum: 30,
            sceneFlow: sceneFlow,
            sectionBarState: {
                depDetail: true,
                tolDetail: true,
                matDetail: true,
                adDetail: true
            },
            bidTypeScatData: {
                data: {},
                title: "",
                xAxis: []
            },
            loadState: "",
            isLoading: false,
            isEmpty: false,
            modalData: {},
            useDate: "",
            hoverd: {},
            showHistoryBar: false,
            pvId: "",
            defalut783: "783",
            bidTypeScatId:false
        };
    },
    mounted() {
        this.getOnePvId();
        this.getMatrixData();
    },
    methods: {
        showMatrixFilter(modalData) {
            // $("#bidTypeScatId").modal("show");
            this.bidTypeScatId=true;
            this.modalData = modalData;
        },
        async getMatrixData() {
            this.loadState="loading"
            // 加载整体数据
            this.isLoadTotal = true;
            this.isLoading = true; //数据正在加载，控制加载图
            this.isEmpty = false;
            if (this.pvId != "") {
                let res = await this.$api.insights.getMatrixTrafficData(
                    this.pvId
                );
                
                let mxData = res;
                if (mxData.data) {
                    this.funnelData = mxData.data;
                } else {
                    this.isEmpty = true;
                }
                this.loadState="ok";
                this.isLoading = false;
            }
        },
        async getOnePvId() {
            let res = await this.$api.insights.getRandomPvid();
            this.pvId = res;
            this.getMatrixData();
        }
    }
};
</script>


