<template>
    <el-row>
        <el-form :inline="true" class="demo-form-inline" style="padding:20px">
            <el-form-item label="用户名：">
                <el-input
                    v-model="userName"
                    placeholder="请输入用户名"
                    @keyup.enter.native="getUserAccess"
                ></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="default" @click="getUserAccess">查询查看权限</el-button>
            </el-form-item>
            <el-form-item v-show="showSelectAllBtn">
                <el-button type="default" @click="selectAll">全部选择</el-button>
            </el-form-item>
            <el-form-item v-show="showUpdateBtn">
                <el-button type="primary" @click="addUserAccess">更新权限</el-button>
            </el-form-item>
        </el-form>

        <el-row :span="24" v-show="realMenu.length > 0">
            <template v-for="(mainMenuItem, index) in realMenu">
                <el-col :span="5" style="margin:20px">
                    <el-card class="box-card" style="height:250px">
                        <div slot="header" class="clearfix">
                            <span>{{mainMenuItem.mainMenuName}}</span>
                            <el-checkbox
                                :value="mainMenuItem.mainMenuName"
                                v-model="realMenu[index].select"
                                @change="changeFirstMenu(index)"
                                style="float: right; padding: 3px 0"
                            >全选</el-checkbox>
                        </div>
                        <template v-for="(secondMenuItem, idx) in mainMenuItem.secondLevelMenu">
                            <div class="checkbox">
                                <label>
                                    <input
                                        type="checkbox"
                                        v-model="realMenu[index].secondLevelMenu[idx].select"
                                        @change="changeSencondMenu(index, idx)"
                                    />
                                    {{ secondMenuItem.secondLevelMenuName}}
                                </label>
                            </div>
                        </template>
                    </el-card>
                </el-col>
            </template>
        </el-row>
    </el-row>
</template>

<script>
import lodash from "lodash";
import moment from "moment";
import InsightsMenuAll from "@/config/InsightMenu";

export default {
    data() {
        return {
            pageList: [],
            userName: "",
            realMenu: [],
            showUpdateBtn: false,
            showSelectAllBtn: false
        };
    },
    methods: {
        changeFirstMenu: function(index, from) {
            for (var i in this.realMenu[index].secondLevelMenu) {
                this.$set(
                    this.realMenu[index].secondLevelMenu[i],
                    "select",
                    this.realMenu[index].select
                );
            }
            this.delayUpdateAccess(from);
        },
        changeSencondMenu: function(index, idx) {
            if (
                this.realMenu[index].select &&
                !this.realMenu[index].secondLevelMenu[idx].select
            ) {
                this.realMenu[index].select = false;
            }

            if (this.realMenu[index].secondLevelMenu[idx].select) {
                for (var i in this.realMenu[index].secondLevelMenu) {
                    if (!this.realMenu[index].secondLevelMenu[i].select) {
                        this.delayUpdateAccess();
                        return;
                    }
                }
                this.realMenu[index].select = true;
            }

            this.delayUpdateAccess();
        },
        delayUpdateAccess: function(from) {
            if (from == "auto") {
                return;
            }
            let that = this;
            this.showUpdateBtn = true;
        },
        async addUserAccess() {
            if (lodash.trim(this.userName) == "") {
                this.$message.warning("请输入用户名");
                return;
            }

            let res = await this.$api.insights.addUserAccess(
                this.userName,
                this.realMenu
            );
            if (res.errcode == 0) {
                this.$message.success("更新成功");
            } else {
                this.$message.error(res.body.errmsg || "更新失败");
            }
        },
        selectAll: function() {
            for (var i in this.realMenu) {
                this.$set(this.realMenu[i], "select", true);
                this.changeFirstMenu(i);
            }
        },
        async getUserAccess() {
            console.log(11);
            this.realMenu = [];

            if (lodash.trim(this.userName) == "") {
                this.$message.warning("请输入用户名");
                return;
            }
            this.showUpdateBtn = false;
            this.showSelectAllBtn = true;

            let res = await this.$api.insights.getUserAccess(this.userName);
            console.log(res);
            if (res.errcode != 0) {
                return;
            }
            if (res.data.user_type == 10) {
                this.$alert("该用户为管理员，拥有全部页面查看权限");
                return;
            }

            if (res.data.user_type == 1) {
                this.pageList = res.data.page_list;
            } else {
                this.pageList = [];
            }

            this.setMenuChecked();
        },
        setMenuChecked: function() {
            this.realMenu = InsightsMenuAll;

            for (var i in this.realMenu) {
                if (this.pageList.indexOf(this.realMenu[i].mainMenuName) >= 0) {
                    this.$set(this.realMenu[i], "select", true);
                    this.changeFirstMenu(i, "auto");
                    continue;
                } else {
                    this.$set(this.realMenu[i], "select", false);
                }

                for (var idx in this.realMenu[i].secondLevelMenu) {
                    if (
                        this.pageList.indexOf(
                            this.realMenu[i].secondLevelMenu[idx].url
                        ) >= 0
                    ) {
                        this.$set(
                            this.realMenu[i].secondLevelMenu[idx],
                            "select",
                            true
                        );
                    } else {
                        this.$set(
                            this.realMenu[i].secondLevelMenu[idx],
                            "select",
                            false
                        );
                    }
                }
            }
        }
    }
};
</script>
