<template>
    <div class="lean-matrix-wrapper">
        <el-form
            :inline="true"
            class="demo-form-inline"
            label-width="80px"
            style="padding-top:20px"
        >
            <el-row>
                <el-col :span="8">
                    <el-form-item label="产品线">
                        <el-select v-model="defalut783">
                            <el-option label="超级粉丝通" value="783"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="竞价类型">
                        <el-select v-model="bidtype">
                            <el-option label="全部" value></el-option>
                            <el-option label="OCPX-1" value="1"></el-option>
                            <el-option label="CPV" value="2"></el-option>
                            <el-option label="CPM" value="4"></el-option>
                            <el-option label="CPE" value="8"></el-option>
                            <el-option label="CPC" value="12"></el-option>
                            <el-option label="OCPX-2" value="16"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="营销目标">
                        <el-select v-model="promotionObj">
                            <el-option label="全部" value="0"></el-option>
                            <el-option label="收集销售线索" value="88030003"></el-option>
                            <el-option label="推广APP" value="88030004"></el-option>
                            <el-option label="推广内容" value="88030002"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <!-- </el-form>
            <el-form :inline="true" class="demo-form-inline" label="80px">-->
            <el-row>
                <el-col :span="8">
                    <el-form-item label="日期">
                        <el-date-picker
                            v-model="useDate"
                            value-format="yyyy-MM-dd"
                            type="date"
                            placeholder="选择日期"
                        ></el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="10">
                    <el-form-item :label="(isExceptPsid ? '排' : '' )+'场景流'">
                        <el-select
                            v-model="psids"
                            multiple
                            clearable
                            placeholder="选择场景"
                            style="width:260px"
                        >
                            <template v-for="item in sceneFlow">
                                <template v-if="item.value > 0">
                                    <el-option
                                        :key="item.value"
                                        :value="item.value"
                                        :label="item.name"
                                        v-if="psids.indexOf(item.value) > -1"
                                    ></el-option>
                                    <el-option
                                        :value="item.value"
                                        :key="item.value"
                                        :label="item.name"
                                        v-else
                                    ></el-option>
                                </template>
                            </template>
                        </el-select>
                        <el-checkbox v-model="isExceptPsid">排除</el-checkbox>
                    </el-form-item>
                </el-col>
                <el-col :span="2">
                    <el-button type="primary"  @click="getMatrixData">查询</el-button>
                </el-col>
            </el-row>
        </el-form>

        <div class="page-header">
            <h4>精益矩阵</h4>
        </div>

        <loading :state="loadState" />

        <LeanMatrix
            :theFunnelData="funnelData"
            @showMatrixFilter="showMatrixFilter"
            v-show="sectionBarState.matDetail"
        ></LeanMatrix>

        <el-dialog title="详情" :visible.sync="bidTypeScatId" width="75%">
            <div style="padding:0 30px;display:flex;justly-content:center;" align="center">
                <el-card style="width:30%;margin:0 20px" align="center">
                    <p class="remain">剩余数:</p>
                    <span style="font-size:20px;color:rgb(0, 152, 217)">{{modalData.cnt}}</span>
                </el-card>
                <el-card style="width:30%;margin:0 20px" align="center">
                    <p class="filter">过滤数:</p>
                    <span style="font-size:20px;color:rgb(0, 152, 217)">{{modalData.filter_cnt}}</span>
                </el-card>
                <el-card style="width:30%;margin:0 20px" align="center">
                    <p class="filter-rate">过滤占比:</p>
                    <span
                        style="font-size:20px;color:rgb(0, 152, 217)"
                    >{{Number(modalData.filter_ratio * 100).toFixed(2)}}%</span>
                </el-card>
            </div>
            <div style="margin-top:50px">
                <div class="text-center">
                    <strong>竞价分布情况</strong>
                </div>
                <drawchart
                    chartId="bidTypeScatChartId"
                    :chartData="bidTypeScatData"
                    :showToolBox="false"
                />
            </div>
        </el-dialog>
    </div>
</template>

<script>
// 引入 ECharts 主模块

import sceneFlow from "@/config/SceneFlow.js";
import moment from "moment";
import lodash from "lodash";
import * as CONST from "@/constant.js";
import LeanMatrix from "@/Insight/View/LeanMatrix";

export default {
    components: {
        LeanMatrix
    },
    data() {
        return {
            /* 当前日期 */
            currentDate: "",

            funnelData: [],
            /* 提示数据 */
            // tipsData: [],
            tipsData: {},
            funnelNum: 30,

            psids: [],
            sceneFlow: sceneFlow,
            sectionBarState: {
                depDetail: true,
                tolDetail: true,
                matDetail: true,
                adDetail: true
            },
            bidTypeScatData: {
                data: {},
                title: "",
                xAxis: []
            },
            loadState: "",
            isLoading: false,
            modalData: {},
            startTime: [],
            endTime: "",
            time: [],
            useDate: "",
            hoverd: {},
            isExceptPsid: false,
            bidtype: "16",
            promotionObj : "0",
            defalut783: "783",
            bidTypeScatId: false
        };
    },
    mounted() {
        this.initialTime();
        this.getMatrixData();
    },
    methods: {
        choosePsid: function(option, checked) {
            let val = parseInt($(option).val());
            if (checked) {
                this.psids.push(val);
            } else {
                this.$lodash.pull(this.psids, val);
            }
        },
        showMatrixFilter(modalData) {
            let subCode = modalData.code % 1000;
            let code = parseInt(modalData.code / 1000);
            this.bidTypeScatId = true;
            this.modalData = modalData;
            this.getChartsData(code, subCode);
        },
        async getChartsData(code, subcode) {
            let res = await this.$api.insights.getChartsData(code, subcode, [
                this.useDate + " " + this.time[0],
                this.useDate + " " + this.time[1]
            ]);
            this.bidTypeScatData = {
                title: "竞价队列长度分布",
                xAxis: Object.keys(res),
                data: {
                    长度: lodash.values(res)
                }
            };
        },
        async getMatrixData() {
            this.loadState = "loading";

            // 加载整体数据
            this.isLoading = true; //数据正在加载，控制加载图
            let data = {
                product: "sfst",
                start_time: this.useDate + " 00:00:00",
                psids: this.psids,
                is_except_psid: this.isExceptPsid,
                bid_type: this.bidtype,
                promotion_objective : parseInt(this.promotionObj),
            };

            let res = await this.$api.insights.getMatrixData(data);
            this.loadState = "ok";
            let mxData = res;
            if (mxData.data) {
                let totalCount = mxData.data[0].cnt;
                this.funnelData = mxData.data
            }
            this.isLoading = false;
        },
        initialTime: function() {
            this.time = [
                moment()
                    .startOf("day")
                    .format("HH:mm:ss"),
                moment().format("HH:mm:ss")
            ];
            this.useDate = moment().format("YYYY-MM-DD");
        }
    }
};
</script>


