<template>
    <el-row>
        <el-form>
            <el-form-item label="选择数据类型">
                <el-radio-group v-model="dataType">
                    <template v-for="(item, key, i) in dataTypeConf">
                        <el-radio :label="key" :key="i">{{item}}</el-radio>
                    </template>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="选择日期范围">
                <el-date-picker
                    v-model="timeRange"
                    type="daterange"
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    :value-format="'yyyy-MM-dd'"
                ></el-date-picker>
            </el-form-item>
        </el-form>
        <el-row>
            <p>
                按天走势
                <i class="el-icon-loading" v-show="loading"></i>
            </p>
            <drawchart :chartData="chartData" chartId="postbackId" />
        </el-row>
        <el-form>
            <el-form-item label="选择日期">
                <el-date-picker v-model="day" type="date" :value-format="'yyyy-MM-dd'"></el-date-picker>
            </el-form-item>
        </el-form>
        <el-row>
            <p>
                按小时走势
                <i class="el-icon-loading" v-show="loading"></i>
            </p>
            <drawchart :chartData="chartData1" chartId="postbackId2" />
        </el-row>
    </el-row>
</template>

<script>
import moment from "moment";

export default {
    props: ["adId"],
    data() {
        return {
            dataType: "pv",
            dataTypeConf: {
                pv: "曝光量",
                bhv: "互动数",
                total_cpl: "线索收集数",
                ad_cpl: "广告线索收集数",
                mapping_cpl: "广告线索关联数",
                bhv_ratio: "互动率",
                cpl_ratio: "线索收集率",
                ad_cpl_ratio: "广告线索收集率",
                mapping_ratio: "关联成功率"
            },
            timeRange: [],
            list: [],
            hourList: {},
            day : "",
            loading: false
        };
    },
    mounted() {
        this.timeRange = [
            moment()
                .subtract(7, "days")
                .format("YYYY-MM-DD"),
            moment().format("YYYY-MM-DD")
        ];
        this.day = moment().format("YYYY-MM-DD");
    },
    watch: {
        timeRange: function() {
            this.getPostbackData();
        },
        day: function() {
            this.getPostbackHourData();
        }
    },
    computed: {
        chartData: function() {
            let tmpxAxis = [];
            let chart1Data = {};
            let yName = this.dataTypeConf[this.dataType];
            chart1Data[yName] = [];
            this.list.forEach((item, idx) => {
                tmpxAxis.push(item.t);
                chart1Data[yName].push(item[this.dataType]);
            });
            return {
                xAxis: tmpxAxis,
                data: chart1Data
            };
        },
        chartData1: function() {
            let tmpxAxis = [];
            for (var i = 0; i < 24; i++) {
                tmpxAxis.push(i + ":00");
            }

            let chart1Data = {
                今日: [],
                昨日: [],
                最近7日: []
            };

            ["今日", "昨日", "最近7日"].forEach(d => {
                if (this.hourList[d] == undefined || this.hourList[d] == null) {
                } else {
                    let dayData = this.hourList[d];
                    tmpxAxis.forEach(h => {
                        if (dayData[h] != undefined) {
                            chart1Data[d].push(dayData[h][this.dataType]);
                        } else {
                            chart1Data[d].push("");
                        }
                    });
                }
            });
            return {
                xAxis: tmpxAxis,
                data: chart1Data
            };
        }
    },
    methods: {
        async getPostbackData() {
            if(this.adId == "" || this.adId == undefined || this.adId == null) {
                return
            }
            this.loading = true;
            this.list = [];
            let res = await this.$api.insights.getCplPostback(
                this.adId,
                this.timeRange[0],
                this.timeRange[1]
            );
            this.loading = false;
            if(res.data != null) {
                this.list = res.data;
            }
            
        },
        async getPostbackHourData() {
            this.hourList = {};
            let res = await this.$api.insights.getCplPostbackHour(
                this.adId,
                this.day
            );
            this.hourList = res.data;
        }
    }
};
</script>

