<template>
    <el-row>
        <el-row>
            <el-col align="center">
                <el-radio-group v-model="chartTabType" @change="setChartLineData">
                    <el-radio-button label="budget">实时预算</el-radio-button>
                    <el-radio-button label="consume">实时消耗</el-radio-button>
                    <el-radio-button label="impression">实时曝光</el-radio-button>
                    <el-radio-button label="bhvs">实时互动</el-radio-button>
                    <el-radio-button label="adids">在线广告</el-radio-button>
                    <el-radio-button label="customers">在线广告主</el-radio-button>
                    <el-radio-button label="mids">在线博文</el-radio-button>
                    <el-radio-button label="adid_no_consume">无消耗广告计划</el-radio-button>
                </el-radio-group>
            </el-col>

            <el-col align="center" style="padding-top:10px;">
                <el-form :inline="true">
                    <el-form-item label="日期">
                        <el-date-picker
                            v-model="adTime"
                            value-format="yyyy-MM-dd"
                            placeholder="选择日期"
                            @change="setChartLineData"
                        ></el-date-picker>
                    </el-form-item>
                    <el-form-item label="类型">
                        <el-radio-group v-model="statType">
                            <el-radio-button label="division">分时</el-radio-button>
                            <el-radio-button label="total">累计</el-radio-button>
                        </el-radio-group>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        <el-row>
            <drawchart
                chartId="adStatChartId"
                :chartData="adlineChartData"
                lengendPosition="top"
                percentItem="1"
            />
        </el-row>
    </el-row>
</template>

<script>
import moment from "moment";

export default {
    data() {
        return {
            chartTabType: "consume",
            adTime : '',
            adlineData : {},
            statType: "division",
        };
    },
    watch: {
        statType() {
            this.setChartLineData();
        }
    },
    computed: {
        adlineChartData: function() {
            if (this.statType == "division") {
                if (this.adlineData["分时"] != undefined) {
                    return {
                        title: "分时数据",
                        data: this.adlineData["分时"],
                        xAxis: this.adlineData["时间列表"]
                    };
                } else {
                    return {
                        title: "分时数据",
                        data: {},
                        xAxis: []
                    };
                }
            }
            if (this.statType == "total") {
                if (this.adlineData["累计"] != undefined) {
                    return {
                        title: "累计数据",
                        data: this.adlineData["累计"],
                        xAxis: this.adlineData["时间列表"]
                    };
                } else {
                    return {
                        title: "累计数据",
                        data: {},
                        xAxis: []
                    };
                }
            }
        }
    },
    filters: {
        thousandNum: function(val) {
            return (val / 10000).toFixed(2);
        }
    },
    mounted() {
        this.adTime = moment().format("YYYY-MM-DD");
        this.setChartLineData();
    },
    methods: {
        async setChartLineData() {
            let res = await this.$api.insights.getChartLineData(
                "sfst",
                this.chartTabType,
                this.adTime
            );
            if (res.errcode === 0) {
                this.adlineData = res.data;
            }
        },
         swicthStatType: function(type) {
            if (this.statType == type) {
                return;
            }
            this.statType = type;
        },
    }
};
</script>
