<template>
    <el-col>
        <el-form
            :label-position="'left'"
            label-width="120px"
            :model="pandoraItem"
            :rules="rules"
            ref="ruleForm"
        >
            <el-form-item label="UID列表" prop="pandora_list">
                <el-input
                    type="textarea"
                    v-model="pandoraItem.pandora_list"
                    rows="3"
                    placeholder="多个UID应用逗号分隔，如 1234,123345"
                ></el-input>
            </el-form-item>
            <el-form-item label="扩展字段">
                <el-input type="textarea" v-model="pandoraItem.extend" rows="2"></el-input>
            </el-form-item>

            <el-form-item label="服务名">
                <el-input v-model="pandoraItem.server_type" placeholder="其他服务名" @change="changeServerType"></el-input>
                <el-radio v-model="pandoraItem.server_type" :label="'king'" @change="changeServerType">king</el-radio>
                <el-radio v-model="pandoraItem.server_type" :label="'fisher'" @change="changeServerType">fisher</el-radio>
            </el-form-item>
            <el-form-item label="模块">
                <el-radio v-model="pandoraItem.property_type" :label="'debug'" :disabled="pandoraItem.server_type == 'fisher'">debug</el-radio>
                <el-radio v-model="pandoraItem.property_type" :label="'unlike'" :disabled="pandoraItem.server_type == 'king'">unlike</el-radio>
            </el-form-item>

            <el-form-item label="黑名单">
                <el-radio v-model="pandoraItem.black_type" :label="1">是</el-radio>
                <el-radio v-model="pandoraItem.black_type" :label="0">否</el-radio>
            </el-form-item>
            <el-form-item label="过期时间（天）" prop="expire_time">
                <el-input-number
                    v-model="pandoraItem.expire_time"
                    placeholder="单位为天"
                    type="number"
                    :min="1"
                    :max="15"
                ></el-input-number>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitPandoraItem">立即添加</el-button>
            </el-form-item>
        </el-form>
    </el-col>
</template>

<script>
export default {
    data() {
        return {
            pandoraItem: {
                black_type: 1,
                property_type: "debug",
                expire_time: 0,
                server_type: "fisher",
                pandora_list: ""
            },
            rules: {
                pandora_list: [
                    {
                        required: true,
                        message: "请输入UID列表",
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    methods: {
        submitPandoraItem() {
            this.$refs["ruleForm"].validate(valid => {
                if (!valid) {
                    this.$message("校验不通过");
                    return;
                }

                this.addPandoraItem();
            });
        },
        async addPandoraItem() {
            this.pandoraItem.expire_time = parseInt(
                this.pandoraItem.expire_time
            );
            let res = await this.$api.insights.addPandoraItem(this.pandoraItem);
            if (res.errcode == 0) {
                this.pandoraItem.pandora_list = "";
                this.$message("添加成功");
                this.$refs["ruleForm"].resetFields();
                this.$emit("closeAddDebugPerson");
            } else {
                this.$message(res.errmsg);
            }
        },
        changeServerType() {
            if(this.pandoraItem.server_type == "king") {
                this.pandoraItem.property_type = "debug"
            }

            if(this.pandoraItem.server_type == "fisher") {
                this.pandoraItem.property_type = "unlike"
            }
        }
    }
};
</script>
