<template>
    <div class="matrix-layout">
        <div class="matrix-layout-title">
            <el-row>
                <el-col :span="8" :offset="8">
                    <div class="matrix-layout-title-description">
                        <div class="item">
                            <span style="backgroundColor: #0099CC;"></span>
                            <small style="color:grey;">通过</small>
                        </div>
                        <div class="item">
                            <span style="backgroundColor: #FF6666;"></span>
                            <small style="color:grey;">过滤</small>
                        </div>
                        <div class="item">
                            <span style="backgroundColor: rgb(100,103,96)"></span>
                            <small style="color:grey;">前过滤</small>
                        </div>
                    </div>
                </el-col>
                <el-col v-show="!wikiNotShow" :span="6" :offset="1">
                    <a href="javascript:;" @click="windowHref">
                        <el-button
                            type="primary"
                            icon="el-icon-view"
                            circle
                            style="margin-right:10px"
                        ></el-button>广告投放策略备案库
                    </a>
                </el-col>
            </el-row>
        </div>
        <div class="matrix-block-wrap" ref="funnelElem">
            <div class="matrix-layout-item" v-for="(item, index) in matrixData" :key="index">
                <el-tooltip :content="'错误码：' + item.code + '所属模块：' + item.desc" placement="top">
                    <span class="grain-label">{{ item.remark }}</span>
                </el-tooltip>
                <el-tooltip :content="item.comment" placement="top">
                    <div class="grain-box" @click="showMatrixFilterToParent(item)">
                        <span
                            v-for="ridx in (item.remainScales)"
                            :key="ridx+'remain'"
                            class="grain passed"
                        ></span>
                        <span
                            v-for="fidx in (item.filterScales)"
                            :key="fidx+'filter'"
                            class="grain no-pass_checked"
                        ></span>
                        <span
                            v-for="didx in (item.diffTolScales)"
                            :key="didx+'totalfilter'"
                            class="grain no-pass"
                        ></span>
                        <slot :funnelDetail="item"></slot>
                        <span
                            v-show="matrixType"
                            style="margin-left: 5px; font-size: 12px"
                        >{{ (item.cnt_ratio * 100).toFixed(2) + '%' }}</span>
                    </div>
                </el-tooltip>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "LeanMatrix",
    data() {
        return {
            hoverd: {},
            matrixType: true,
            funnelNum : 30,
        };
    },
    mounted() {
        if (this.componentName) {
            this.matrixType = false;
        }
    },
    computed : {
        matrixData : function() {
            let arr = [];
            if(this.theFunnelData == null || this.theFunnelData == undefined) {
                return [];
            }
            this.theFunnelData.forEach(filterItem => {
                filterItem.remainScales = Math.floor(
                    this.funnelNum * filterItem.cnt_ratio
                );
                filterItem.diffTolScales = Math.ceil(
                    this.funnelNum * filterItem.diff_ratio
                );
                filterItem.filterScales =
                    this.funnelNum -
                    filterItem.remainScales -
                    filterItem.diffTolScales;
                arr.push(filterItem);
            });
            return arr;
        },
    },
    props: ["theFunnelData", "componentName", "wikiNotShow"],
    methods: {
        showMatrixFilterToParent(item) {
            this.$emit("showMatrixFilter", item);
        },
        windowHref() {
            window.open(
                "http://wiki.intra.sina.com.cn/pages/viewpage.action?pageId=192553745"
            );
        }
    }
};
</script>

