<template>
    <div class="flow-wrapper">
        <div class="clearfix">
            <el-tabs type="border-card" @tab-click="changeTab">
                <template v-for="item in tabList">
                    <el-tab-pane :label="item.name" :name="item.code" :key="item.code"></el-tab-pane>
                </template>
                <component :is="currentComm"></component>
            </el-tabs>
        </div>
    </div>
</template>

<script>
import TenmaAllMsg from "./TenmaAllMsg";
import TenmaMsgStat from "./TenmaMsgStat";
const currentComm = ["TenmaAllMsg", "TenmaMsgStat"];
export default {
    components: {
        TenmaAllMsg,
        TenmaMsgStat
    },
    data() {
        return {
            tabList: [
                {
                    name: "广告消息透视",
                    code: "0"
                },
                {
                    name: "错误广告消息",
                    code: "1"
                }
            ],
            currentTab: "0",
            currentComm: currentComm["0"]
        };
    },
    methods: {
        changeTab(tab) {
            this.currentTab = tab.name;
            this.currentComm = currentComm[tab.name];
        }
    }
};
</script>
