<template>
    <div class="people">
        <el-form :inline="true" class="demo-form-inline">
            <el-form-item label="uid或昵称" style="margin-right:40px">
                <el-input v-model="uid" placeholder="请输入uid或昵称" @keyup.enter.native="searchNew"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="searchNew">查询</el-button>
            </el-form-item>
        </el-form>
        <!-- 查询结果展示 -->
        <el-card v-show="tableShow" style="margin-top:20px">
            <table class="mytable">
                <tbody>
                    <tr>
                        <td width="20%">实时商圈</td>
                        <td width="80%">{{ runTimeRbd }}</td>
                    </tr>
                    <tr>
                        <td>登录频次</td>
                        <td>{{responseUsers.login | getColValue('login')}}</td>
                    </tr>
                    <tr>
                        <td>人生状态</td>
                        <td>{{ responseUsers.life_state_2 | getColValue('life_state_2') }}</td>
                    </tr>
                    <tr>
                        <td>区县</td>
                        <td>{{responseUsers.district | getColValue('district')}}</td>
                    </tr>
                    <!-- <tr>
                        <td>常驻商圈</td>
                        <td>
                            {{ rbdMes }}
                        </td>
                    </tr>-->
                    <tr>
                        <td>性别</td>
                        <td>{{responseUsers.gender | getColValue('gender')}}</td>
                    </tr>
                    <tr>
                        <td>年龄</td>
                        <td>{{responseUsers.age | getColValue('age')}}</td>
                    </tr>
                    <tr>
                        <td>兴趣词</td>
                        <td>{{responseUsers.keyword}}</td>
                    </tr>
                    <tr>
                        <td>地域</td>
                        <td>{{responseUsers.loc | getColValue('loc')}}</td>
                    </tr>
                    <tr>
                        <td>兴趣</td>
                        <td
                            height="60"
                        >{{ responseUsers.interest_2 | getInterestRepresentation(responseUsers.interest_2) }}</td>
                    </tr>
                    <tr>
                        <td>人生状态(仅供wax使用)</td>
                        <td>{{responseUsers.life_state | getColValue('life_state')}}</td>
                    </tr>
                </tbody>
            </table>
        </el-card>
    </div>
</template>
<style scoped>
.front li {
    margin-top: 20px;
    list-style-type: none;
}
.mytable {
    table-layout: fixed;
    width: 100%;
    /* border:1px solid rgb(168, 163, 163); */
    margin: 0px;
}

.mytable tr td {
    padding: 10px;
    /* border-top:0.5px solid grey; */
    overflow: hidden;
    white-space: inherit;
    word-wrap: break-word;
    text-align: left;
    vertical-align: middle;
}
</style>

<script>
import perspectiveCode from "@/assets/js/page/perspectiveCode.js";
import interestCode from "@/config/interest_code.js";
export default {
    data() {
        return {
            uid: "1111681197",
            responseUsers: {},
            tableShow: false,
            rbdMes: "",
            runTimeRbd: "",
            isLoading: false,
            noData: false
        };
    },
    mounted() {},
    filters: {
        getColValue: function(value, key) {
            var methods = perspectiveCode;
            if (key === "login") {
                return methods.getLoginCode(value);
            } else if (key === "district") {
                return methods.getDistrictCode(value);
            } else if (key === "gender") {
                return methods.getGenderCode(value);
            } else if (key === "age") {
                return methods.getAgeCode(value);
            } else if (key === "loc") {
                return methods.getLocCode(value);
            } else if (key === "life_state" || key === "life_state_2") {
                if (value == undefined) {
                    return;
                }
                var tempArr = [];
                var finalValue = [];
                tempArr = value.toString().split(",");
                for (var i = 0; i < tempArr.length; i++) {
                    finalValue.push(methods.getLifeStateCode(tempArr[i]));
                }
                return finalValue.join(",");
            }
            return value;
        },
        //获取兴趣映射
        getInterestRepresentation(value, key) {
            if (!value) {
                return value;
            }
            var oneLevel = value.split(",");
            var interArr = [];
            var interestPresen = [];
            for (let item = 0; item < oneLevel.length; item++) {
                interArr.push(oneLevel[item].split(":")[0]);
            }
            // console.log(interArr.length);
            var interest_code_filter = interestCode;

            for (var onePre = 0; onePre < interArr.length; onePre++) {
                if (interest_code_filter.hasOwnProperty(interArr[onePre])) {
                    interestPresen.push(interest_code_filter[interArr[onePre]]);
                }
            }
            return interestPresen.join(",");
        }
    },
    methods: {
        //常驻和实时商圈映射
        getRbdRepresentation(value) {
            if (!value) return;
            var rbdArr = value.split(",");
            var handleRbd = [];
            for (let rbdIt = 0; rbdIt < rbdArr.length; rbdIt++) {
                handleRbd.push(rbdArr[rbdIt].split(":")[0]);
            }
            //this.getRbdData(handleRbd.join(","));
        },
        async getRbdData(rbd) {
            let res = await this.$api.insights.getRbdData(rbd);
            var data = res.data;
            var rbdData = [];
            for (const rbKey in data) {
                if (data.hasOwnProperty(rbKey)) {
                    rbdData.push(data[rbKey]);
                }
            }
            if (rbdData.length > 1) {
                this.rbdMes = rbdData.join(",");
            } else {
                this.runTimeRbd = rbdData.join(",");
            }
        },
        async searchNew() {
            if (this.uid == "") {
                this.$alert("请输入uid");
                return false;
            }
            this.isLoading = true;

            let res = await this.$api.insights.getUserPic(this.uid);
            this.isLoading = false;
            if (res.errcode != 0) {
                thia.$alert("对不起，没有查询结果");
                this.noData = true;
                this.tableShow = false;
                return;
            }
            this.responseUsers = res.data;
            this.tableShow = true;
            this.getRbdRepresentation(this.responseUsers.rbd);
            this.getRbdRepresentation(this.responseUsers.bd);
        }
    }
};
</script>

<style lang="less">
.people .el-form-item {
    margin-bottom: 0;
}
</style>
