<template>
    <el-form :inline="true">
        <el-form-item label="uid或昵称" style="margin-right:40px">
            <el-input v-model="uid" placeholder="请输入uid或昵称" @keyup.enter.native="searchNew"></el-input>
        </el-form-item>
        <el-form-item label="日期范围" style="margin-right:40px">
            <el-date-picker
                value-format="yyyy-MM-dd"
                style="width:400px"
                v-model="dateRange"
                type="daterange"
                align="right"
                unlink-panels
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :picker-options="pickerOptions"
            ></el-date-picker>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="searchNew">查询</el-button>
        </el-form-item>
    </el-form>
</template>

<script>
import moment from "moment"
export default {
    data() {
        return {
            uid: "lee东升",
            dateRange: [],
            pickerOptions: {
                shortcuts: [
                    {
                        text: "最近一周",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(
                                start.getTime() - 3600 * 1000 * 24 * 7
                            );
                            picker.$emit("pick", [start, end]);
                        }
                    },
                    {
                        text: "最近一个月",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(
                                start.getTime() - 3600 * 1000 * 24 * 30
                            );
                            picker.$emit("pick", [start, end]);
                        }
                    },
                    {
                        text: "最近三个月",
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(
                                start.getTime() - 3600 * 1000 * 24 * 90
                            );
                            picker.$emit("pick", [start, end]);
                        }
                    }
                ]
            }
        };
    },
    props: [],
    mounted() {
        this.initDatePlugin();
    },
    methods: {
        initDatePlugin: function() {
            this.dateRange = [
                moment()
                    .subtract(7, "day")
                    .format("YYYY-MM-DD"),
                moment().format("YYYY-MM-DD")
            ];
            this.searchNew();
        },
        searchNew() {
            this.$emit("searchNew", this.uid, this.dateRange);
        }
    }
};
</script>

<style>
.people .el-form-item {
    margin-bottom: 0;
}
</style>

