<template>
<div>
    <div :id="chartId" style="height:400px;width: 100%;">

    </div>
</div>
</template>

<script>

import lodash from 'lodash';
import echarts from 'echarts';
require('echarts/theme/vintage')
require('echarts/theme/dark')
require('echarts/theme/infographic')
require('echarts/theme/macarons')
require('echarts/theme/shine')
import ChartConfig from '@/config/ChartConfig';

export default {
    props : ['chartId', 'chartData', 'dataType', 'showToolBox'],
    watch : {
        chartData : {
            handler : function(val) {
                this.drawPie(val);
            },
            deep : true
        },
    },
    mounted(){
        this.drawPie(this.chartData);
    },
    methods:{
        drawPie : function(chartData) {

            var myChart = echarts.init(document.getElementById(this.chartId), 'shine');
            let initOption = ChartConfig.pie_option;
            initOption.title.show = false;
            if(chartData && chartData.data) {
                initOption.legend.data = chartData.data.map(v => v.name)
                initOption.series.data = chartData.data
            }
            // console.log(ChartConfig)

            myChart.clear();
            myChart.setOption(initOption);
            setTimeout(() => {
                myChart.resize();
            }, 500)
            // myChart.on("click", this.clickPiePart);
        },
        // clickPiePart : function(param) {
        //     this.$emit('clickPiePart', param);
        // }
    }
}
</script>
