 <template>
    <el-row>
        <el-row>
            <el-col>
                <el-form label-width="100px">
                    <el-form-item label="数据类型">
                        <el-radio-group v-model="dataType" @change="changeDataType">
                            <el-radio-button label="ad_count">广告数量</el-radio-button>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="是否多天对比">
                        <el-radio v-model="isMultiDate" :label="true" @change="changeIsMutiDate">是</el-radio>
                        <el-radio v-model="isMultiDate" :label="false" @change="changeIsMutiDate">否</el-radio>
                    </el-form-item>
                    <el-form-item label="数据源" v-if="!isMultiDate">
                        <el-checkbox-group v-model="dataSrcList">
                            <template v-for="(val, key) in dataTypeList">
                                <el-checkbox :label="key" :key="key">{{val}}</el-checkbox>
                            </template>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-form-item label="数据源" v-if="isMultiDate">
                        <el-radio-group v-model="dataSrc">
                            <template v-for="(val, key) in dataTypeList">
                                <el-radio :label="key" :key="key">{{val}}</el-radio>
                            </template>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="日期">
                        <el-date-picker
                            type="date"
                            v-model="date"
                            placeholder="选择日期"
                            value-format="yyyy-MM-dd"
                            @change="reqGetDayAccmulativeData"
                            :disabled="loadData"
                        ></el-date-picker>
                        <i class="el-icon-loading" v-if="loadData"></i>
                    </el-form-item>
                    <el-form-item label="已选日期" v-if="isMultiDate">
                        <template v-for="(tag,date) in compareData">
                            <el-tag :key="date" closable @close="deleteCompareDate(date)" v-if="tag != null">{{date}}</el-tag>
                        </template>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>

        <el-row>
            <drawchart
                chartId="ocpxCustChartId"
                :chartData="chartData"
                lengendPosition="top"
                :yform="yform"
            />
        </el-row>
    </el-row>
</template>

<script>
const dataTypeConfig = {
    consume: {
        consume: "OCPX累计消耗",
        ocpx1_consume: "OCPX1累计消耗",
        ocpx2_consume: "OCPX2累计消耗",
        ocpx1_cpl_consume: "OCPX1-CPL累计消耗",
        ocpx1_app_consume: "OCPX1-APP累计消耗",
        ocpx2_cpl_consume: "OCPX2-CPL累计消耗",
        ocpx2_app_consume: "OCPX2-APP累计消耗"
    },
    ad_count: {
        ad_count: "OCPX累计在线计划个数",
        ocpx1_ad_count: "OCPX1累计在线计划个数",
        ocpx2_ad_count: "OCPX2累计在线计划个数",
        ocpx1_cpl_ad_count: "OCPX1-CPL累计在线计划个数",
        ocpx1_app_ad_count: "OCPX1-APP累计在线计划个数",
        ocpx2_cpl_ad_count: "OCPX2-CPL累计在线计划个数",
        ocpx2_app_ad_count: "OCPX2-APP累计在线计划个数"
    }
};
import moment from "moment";
export default {
    data() {
        return {
            dataType: "ad_count",
            date: "",
            dayData: [],
            dataSrcList: ["ad_count", "ocpx1_ad_count", "ocpx2_ad_count"],
            dataSrc: "consume",
            compareData: {},
            isMultiDate: false,
            loadData: false
        };
    },
    computed: {
        chartData: function() {
            if (this.isMultiDate) {
                return this.genMutiDayChartData();
            } else {
                return this.getOneDayData(this.dayData);
            }
        },
        dataTypeList() {
            let dataTypeMap = dataTypeConfig[this.dataType];
            return dataTypeMap;
        },
        yform() {
            if (this.dataType == "consume") {
                return "万";
            }
            return "";
        }
    },
    mounted() {
        this.date = moment().format("YYYY-MM-DD");
        this.reqGetDayAccmulativeData();
    },
    methods: {
        changeDataType() {
            if (this.dataType == "consume") {
                this.dataSrcList = [
                    "consume",
                    "ocpx1_consume",
                    "ocpx2_consume"
                ];
                this.dataSrc = "consume";
            } else {
                this.dataSrcList = [
                    "ad_count",
                    "ocpx1_ad_count",
                    "ocpx2_ad_count"
                ];
                this.dataSrc = "ad_count";
            }
            // this.dayData = [];
            // this.compareData ={};
            // this.reqGetDayAccmulativeData();
        },
        getOneDayData(tmpData) {
            let xAxis = Object.keys(tmpData);
            let retData = [];
            xAxis.forEach(x => {
                this.dataSrcList.forEach(key => {
                    let xName = this.dataTypeList[key];
                    if (retData[xName] == undefined) {
                        retData[xName] = [];
                    }
                    if (this.dataType == "consume") {
                        retData[xName].push(
                            (tmpData[x][key] / 10000).toFixed(2)
                        );
                    } else {
                        retData[xName].push(tmpData[x][key]);
                    }
                });
            });
            return {
                xAxis: xAxis,
                data: retData,
                title: ""
            };
        },
        async reqGetDayAccmulativeData() {
            this.loadData = true;
            let res = await this.$api.insights.getDayOcpxAccumulative(
                this.date
            );
            this.loadData = false;
            if (res.errcode === 0) {
                this.$set(this, "dayData", res.data);
            } else {
                this.$set(this, "dayData", []);
            }
            if (this.isMultiDate) {
                this.addCompareData();
            }
        },
        addCompareData() {
            this.$set(
                this.compareData,
                this.date,
                this.$lodash.cloneDeep(this.dayData)
            );
        },
        changeIsMutiDate() {
            if (this.isMultiDate) {
                this.addCompareData();
            }
        },
        deleteCompareDate(date) {
            this.$set(
                this.compareData,
                this.date,
                null
            );
        },
        genMutiDayChartData() {
            let dataSource = this.dataSrc;
            let xAxisSet = [];
            Object.keys(this.compareData).forEach(d => {
                if (this.compareData[d] != null) {
                    xAxisSet = this.$lodash.union(
                        Object.keys(this.compareData[d]),
                        xAxisSet
                    );
                }
            });

            xAxisSet = xAxisSet.sort();

            let yData = {};
            Object.keys(this.compareData).forEach(d => {
                let tmpData = this.compareData[d];
                if (tmpData != null) {
                    yData[d] = [];
                    xAxisSet.forEach(x => {
                        if (tmpData[x] == undefined) {
                            yData[d].push("");
                        } else {
                            if (this.dataType == "consume") {
                                yData[d].push(
                                    (tmpData[x][dataSource] / 10000).toFixed(2)
                                );
                            } else {
                                yData[d].push(tmpData[x][dataSource]);
                            }
                        }
                    });
                }
            });

            return {
                xAxis: xAxisSet,
                data: yData,
                title: ""
            };
        }
    }
};
</script>