<template>
    <el-row>
        <el-row>
            <el-form :inline="true">
                <el-col :span="8" style="margin-left:30px">
                    <el-form-item label="状态：">
                        <el-select v-model="pieType">
                            <el-option value="optimization" label="分优化目标数量"></el-option>
                            <el-option value="sale_type" label="分售卖类型数量"></el-option>
                            <el-option value="bid_type" label="分竞价类型数量"></el-option>
                            <el-option value="bid_consume" label="分竞价类型累计消耗"></el-option>
                            <el-option value="realtime_bid_consume" label="分竞价类型实时消耗"></el-option>
                            <el-option value="realtime_new_promotion_consume" label="新营销目标实时消耗"></el-option>
                            <el-option value="new_promotion_consume" label="新营销目标累计消耗"></el-option>
                            <el-option value="ad_filter_reason" label="广告过滤原因"></el-option>
                            <el-option value="creative_style" label="分创意类型消耗"></el-option>
                            <el-option value="offline_reason" label="广告下线原因"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="日期：">
                        <el-date-picker
                            v-model="currentDate"
                            value-format="yyyy-MM-dd"
                            placeholder="选择日期"
                            @change="setOnlineAdData"
                        ></el-date-picker>
                    </el-form-item>
                </el-col>
            </el-form>
        </el-row>
        <el-row>
            <drawchart
                style="width:85%;padding-left:50px"
                chartId="marketTargetId"
                :chartData="pieData"
                data-type="line"
                yform="个"
                showToolBox="false"
            />
        </el-row>
    </el-row>
</template>

<script>
import moment from "moment";

export default {
    data() {
        return {
            currentDate: "",
            pieData: {
                title: "",
                data: {},
                xAxis: []
            },
            pieType: "optimization"
        };
    },
     watch: {
        pieType() {
            this.setOnlineAdData();
        }
    },
    mounted() {
       this.currentDate = moment().format("YYYY-MM-DD");
        this.setOnlineAdData();
    },
    methods: {
       async setOnlineAdData() {
            let keys = [];
            let values = [];
            let product = "sfst";
            let theme = this.pieType;
            let date = this.currentDate;

            let res = await this.$api.insights.getOnlineAdData(
                product,
                theme,
                date
            );
            if (res.errcode === 0) {
                let tmpData = {};
                let allKeys = Object.keys(res.data);
                for (var i in allKeys) {
                    if (allKeys[i] != "时间") {
                        tmpData[allKeys[i]] = res.data[allKeys[i]];
                    }
                }

                this.pieData = {
                    xAxis: res.data["时间"],
                    title: "在线广告",
                    data: tmpData
                };
            }
        },
    }
};
</script>
