<script>
export default {
    props: ["adPlan", "customer", "activeData"],
    data() {
        return {
            campaignAdList: false,
            adPlanList: []
        };
    },
    computed: {
        socialTargetText: function() {
            let isAllFans = 0;
            if (
                this.adPlan.targeting_data != undefined &&
                this.adPlan.targeting_data.is_all_fans != undefined
            ) {
                isAllFans = this.adPlan.targeting_data.is_all_fans;
            }

            if (
                this.adPlan.targeting_data.social_targeting.self_fans_target ==
                0
            ) {
                return "排除自己粉丝（非粉丝人群走维度定向）";
            }

            if (
                this.adPlan.targeting_data.social_targeting.self_fans_target ==
                2
            ) {
                return "只投自己粉丝（粉丝人群走维度定向)";
            }

            if (isAllFans == 1) {
                return "不限粉丝关系（粉丝人群忽略维度定向，非粉丝人群走定向）";
            }

            return "不限粉丝关系（粉丝人群和非粉丝人群都走维度定向）";
        },
        socoalReation: function() {
            if (
                this.adPlan.targeting_data.social_targeting.self_fans_target ==
                0
            ) {
                return "不投放粉丝";
            }

            if (
                this.adPlan.targeting_data.social_targeting.self_fans_target ==
                2
            ) {
                return "只投自己粉丝";
            }

            return "不限";
        },
        isAllFans: function() {
            let isAllFans = 0;
            if (this.adPlan.targeting_data.is_all_fans == undefined) {
                isAllFans = this.adPlan.targeting_data.is_all_fans;
            }
            return isAllFans == 1 ? "全部粉丝" : "非全部粉";
        }
    },
    methods: {
        async getAdPlanList() {
            this.adPlanList = [];
            let res = await this.$api.insights.getAdplanList(
                this.adPlan.campaignid
            );
            if (res.errcode != 0) {
                return;
            }
            this.adPlanList = res.data;
            this.campaignAdList = true;
        },
        showFullText(txt) {
            this.$message({
                showClose: true,
                message: txt,
                duration: 2000
            });
        }
    }
};
</script>
