<template>
    <div>
        <el-row>
            <el-form :inline="true" class="demo-form-inline">
                <el-col :span="15" :offset="1">
                    <el-form-item label="计划预算范围：">
                        <el-slider v-model="range" range :max="910" style="width:600px"></el-slider>
                    </el-form-item>
                </el-col>
                <el-col :span="2">
                    <el-form-item>{{range[0]}} ~ {{range[1]}}</el-form-item>
                </el-col>
                <el-col :span="3" :offset="1">
                    <el-form-item>
                        <el-button @click="search" type="primary">查询</el-button>
                    </el-form-item>
                </el-col>
            </el-form>
        </el-row>

        <loading :state="loadState" />
        <el-row v-show="!loadState">
            <el-col
                :span="16"
                :offset="2"
                style="margin-top:20px;margin-bottom:20px"
                v-if="list.length!=0"
            >
                <el-col :span="10">
                    <el-input placeholder="输入计划ID 进行检索" v-model="searchID">
                        <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
            </el-col>

            <el-col :span="16" :offset="2">
                <el-table :data="showDatas" stripe style="width: 100%" height="700">
                    <el-table-column label="计划ID" width="180">
                        <template slot-scope="scope">
                            <adlink :adId="scope.row.ad_id" />
                        </template>
                    </el-table-column>
                    <el-table-column prop="ad_quota" label="预算" width="180"></el-table-column>
                    <el-table-column prop="last1day_consume" label="昨天消耗"></el-table-column>
                    <el-table-column prop="bid_type" label="竞价类型"></el-table-column>
                    <el-table-column prop="bid_price" label="竞价价格"></el-table-column>
                </el-table>

                
            </el-col>
        </el-row>
    </div>
</template>

<script>
import VueSlider from "vue-slider-component";
import "vue-slider-component/theme/default.css";

export default {
    data() {
        return {
            range: [500, 700],
            list: [],
            loadState: false,
            searchID: ""
        };
    },
    computed: {
        showDatas: function() {
            let datas = this.list;
            let search = this.searchID;
            if (!search) {
                return this.list;
            } else {
                datas = datas.filter(function(item) {
                    if (item.ad_id.toString().indexOf(search) !== -1) {
                        return item;
                    }
                });
            }
            return datas;
        }
    },
    components: {
        VueSlider
    },
    methods: {
        async search() {
            this.searchID = "";
            this.list = [];
            this.loadState = "loading";
            let res = await this.$api.insights.getAdBudgetList(
                this.range[0],
                this.range[1]
            );

            if (res.data == null || res.data.length < 1) {
                this.loadState = "暂无计划";
            } else {
                this.loadState = "";
                this.list = res.data;
            }
        }
    }
};
</script>
