import Vue from 'vue'
import App from './Index.vue'
import router from './router.js'
import lodash from 'lodash'
import moment from 'moment'
import api from '@/api';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import common from '@/utils/common.js';

Vue.use(ElementUI);

import Loading from '@/components/Widget/Loading';
import ShowJson from '@/components/Widget/ShowJson';
import DrawChart from '@/components/Widget/DrawChart';
import DrawPie from '@/components/Widget/DrawPie';
import DrawPileBar from '@/components/Widget/DrawPileBar';
import AdLink from '@/components/Widget/AdLink';

import VueClipboard from 'vue-clipboard2'
VueClipboard.config.autoSetContainer = true
Vue.use(VueClipboard)

Vue.component('loading', Loading);
Vue.component('ShowJson', ShowJson);
Vue.component('drawchart', DrawChart);
Vue.component('drawpie', DrawPie);
Vue.component('drawpilebar', DrawPileBar);
Vue.component('adlink', AdLink);

Vue.config.productionTip = false;

Vue.prototype.$lodash = lodash;
Vue.prototype.$moment = moment;
Vue.prototype.$api = api;
Vue.prototype.$common = common;


new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
});
