<template>
    <div>
        <section class="content">
            <el-row>
               
            </el-row>

            <el-row class="marginLeft">
                <el-card>
                    <div slot="header" class="clearfix">
                        <h3>CTR、CVR查询</h3>
                    </div>

                    <div>
                        <el-form :inline="true" class="demo-form-inline">
                            <el-col :span="6" :offset="1">
                                <el-form-item label="计划ID">
                                    <el-input v-model="ctrAdid" placeholder="请输入计划ID"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="8">
                                <el-form-item label="查询时间">
                                    <el-date-picker
                                        v-model="currentDate"
                                        type="date"
                                        placeholder="选择日期"
                                        value-format="yyyy-MM-dd"
                                    ></el-date-picker>
                                </el-form-item>
                            </el-col>
                            <el-col :span="2" :offset="4">
                                <el-form-item>
                                    <el-button type="primary" @click="getCtrData">查询</el-button>
                                </el-form-item>
                            </el-col>
                            <el-col :span="2">
                                <el-form-item>
                                    <el-button type="success" @click="goodLuckCTRCVR">手气不错</el-button>
                                </el-form-item>
                            </el-col>
                        </el-form>
                        <div class="content-item-trdcontent">
                            <div class="left">
                                <div
                                    id="CtrCharts"
                                    style="width: 100%; height: 400px; overflow:hidden;"
                                ></div>
                            </div>
                            <div class="right" style="position:relative">
                                <el-button
                                    size="mini"
                                    style="display:block;position:absolute;right:54px;top:20px"
                                    v-bind="{type:ctrMode == 'ctr'?'warning':'default'}"
                                    @click="changeCtrMode('ctr')"
                                >CTR</el-button>
                                <el-button
                                    size="mini"
                                    v-bind="{type:ctrMode == 'cvr'?'warning':'default'}"
                                    style="display:block;position:absolute;right:54px;top:50px"
                                    @click="changeCtrMode('cvr')"
                                >CVR</el-button>
                            </div>
                        </div>
                    </div>
                </el-card>
            </el-row>

            <div class="marginLeft">
                <el-card>
                    <div slot="header" class="clearfix">
                        <h3>广告成本走势</h3>
                    </div>
                    <div>
                        <el-form
                            :inline="true"
                            class="demo-form-inline"
                            style="width:90%;text-align:center;display:flex;justify-content:space-between;margin:0 auto"
                        >
                            <!-- <el-col :span="6" :offset="1"> -->
                            <el-form-item label="计划ID">
                                <el-input v-model="adId" placeholder="请输入计划ID"></el-input>
                            </el-form-item>
                            <!-- </el-col>
                            <el-col :span="10">-->
                            <el-form-item label="查询时间">
                                <el-date-picker
                                    v-model="timeArea"
                                    type="daterange"
                                    range-separator="~"
                                    start-placeholder="开始日期"
                                    end-placeholder="结束日期"
                                    placeholder="选择日期"
                                    value-format="yyyy-MM-dd"
                                ></el-date-picker>
                            </el-form-item>
                            <!-- </el-col>
                            <el-col :span="2" :offset="2">-->
                            <el-form-item>
                                <el-button type="primary" @click="query('runCost')">查询</el-button>

                                <!-- </el-col>
                                <el-col :span="2">-->
                                <el-button type="success" @click="goodLuck">手气不错</el-button>
                            </el-form-item>
                            <!-- </el-col> -->
                        </el-form>
                        <div id="adCost" style="width: 100%; height: 400px;"></div>
                    </div>
                </el-card>
            </div>

            <el-row class="marginLeft">
                <el-card>
                    <div slot="header" class="clearfix">
                        <h3>广告详情列表</h3>
                    </div>
                    <div style="padding:0 0 60px 0">
                        <el-form :inline="true" class="demo-form-inline">
                            <el-col :span="6" :offset="1">
                                <el-form-item label="计划ID">
                                    <el-input v-model="detailAdId" placeholder="请输入计划ID"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="6">
                                <el-form-item label="营销目标">
                                    <el-select v-model="curTheme" placeholder="活动区域">
                                        <el-option
                                            v-for="(te, idx) in theme"
                                            :value="te.value"
                                            :key="idx"
                                            :label="te.name"
                                        ></el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :span="6">
                                <el-form-item label="查询类别">
                                    <el-radio-group v-model="ocType">
                                        <el-radio label="ocpx_top">OCPX-2</el-radio>
                                        <el-radio label="ocpm_top">OCPX-1</el-radio>
                                    </el-radio-group>
                                </el-form-item>
                            </el-col>
                            <el-col :span="2" :offset="3">
                                <el-form-item>
                                    <el-button type="success" @click="query('detailList')">查询</el-button>
                                </el-form-item>
                            </el-col>
                        </el-form>

                        <el-table :data="tableData" border style="width: 100%" v-show="!isLoading">
                            <el-table-column
                                type="index"
                                :index="indexMethod"
                                label="N.O."
                                width="180"
                            ></el-table-column>
                            <el-table-column label="预算" width="180">
                                <template slot-scope="scope">{{scope.row.Budget.toFixed(2)}}</template>
                            </el-table-column>
                            <el-table-column label="消耗">
                                <template slot-scope="scope">{{scope.row.Consume.toFixed(2)}}</template>
                            </el-table-column>
                            <el-table-column label="广告主ID">
                                <template slot-scope="scope">
                                    <router-link
                                        :to="{ path: '/insights/customer', query: { customer_id: scope.row.CustId }}"
                                    >{{ scope.row.CustId }}</router-link>
                                </template>
                            </el-table-column>
                            <el-table-column label="计划ID">
                                <template slot-scope="scope">
                                    <adlink :adId="scope.row.AdId" />
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-pagination
                            v-show="!isLoading"
                            style="text-align:center;margin-top:20px"
                            @current-change="pageChange"
                            :current-page.sync="pageIndex"
                            :page-size="pageSize"
                            layout="total, prev, pager, next"
                            :total="totalTableData.length"
                        ></el-pagination>
                        <loading :state="isLoading ? 'loading' : ''" />
                    </div>
                </el-card>
            </el-row>
        </section>
    </div>
</template>

<script>
// 引入 ECharts 主模块
var echarts = require("echarts/lib/echarts");
// 引入折线图
require("echarts/lib/chart/line");
// 引入提示框和标题组件
require("echarts/lib/component/tooltip");
require("echarts/lib/component/legend");
require("echarts/lib/component/title");

import * as CONST from "@/constant.js";
import moment from "moment";
// 引入样式
import "vue-easytable/libs/themes-base/index.css";
// 导入 table 和 分页组件
import { VTable, VPagination } from "vue-easytable";
import { Radio } from "vue-checkbox-radio";

export default {
    data() {
        return {
            chartTabType: "ocpx_budget",
            currentDate: "",
            startDate: "",
            endDate: "",
            adId: "",
            detailAdId: "",
            pageSize: 10,
            pageIndex: 1,
            indexNow: 1,
            hasCostData: false,
            totalTableData: [],
            ocType: "ocpx_top",
            tableData: [],
            showPagenation: true,
            detailAdPlanId: "",
            
            curTheme: "",
            isLoading: true,
            
            
            statType: "累计",
            bcData: {},
            bcChartData: {
                times: [],
                data: []
            },
            curTrendTheme: "",
            ctrChartsData: null,
            ctrAdid: 4334814,
            ctrDate: moment().format("YYYY-MM-DD"),
            ctrMode: "ctr",
            timeArea: [],
            loadState: "暂无数据"
        };
    },
    components: {
        VTable,
        VPagination,
        Radio
    },
    mounted() {
        
        // this.currenDate2 = moment().format("YYYY-MM-DD-HH:mm:ss");
        this.timeArea = [
            moment()
                .subtract(2, "days")
                .format("YYYY-MM-DD"),
            moment().format("YYYY-MM-DD")
        ];
        this.getDetailList("ocpx_top");
        this.getOneAdId();
        this.goodLuckCTRCVR();
        this.getBudgetAndCost();
        this.getCtrData();
    },
    methods: {
        indexMethod(index) {
            return this.pageSize * (this.pageIndex - 1) + index + 1;
        },
        
        goodLuck() {
            this.getOneAdId();
        },
        // 随机获取一个AdId
        async getOneAdId() {
            let res = await this.$api.insights.randomGetOcpxAdId();
            this.adId = res;
            this.query("runCost");
            this.getCtrData();
        },
        async goodLuckCTRCVR() {
            let res = await this.$api.insights.randomGetOcpxAdId();
            this.ctrAdid = res;
            this.query("runCost");
            this.getCtrData();
        },
        // 获取预算和消耗数据
        
        // 获取ocpx转化成本和填写价格数据
        async getCostAndFillData() {
            let param = {
                product: "sfst",
                adid: Number(this.adId),
                start_time: this.timeArea[0] + " 00:00:00",
                end_time: this.timeArea[1] + " 23:59:59"
            };

            let res = await this.$api.insights.getCostAndFillData(param);
            let data = JSON.parse(res);
            data = data.data;
            let chartData = {
                times: [],
                data: []
            };
            let activationCost = {
                name: "转化成本",
                type: "line",
                symbol: "emptyCircle",
                showSymbol: true,
                showAllSymbol: false,
                smooth: true,
                seriesLayoutBy: "row",
                data: []
            };
            let fillCost = {
                name: "填写成本",
                type: "line",
                symbol: "emptyCircle",
                showSymbol: true,
                showAllSymbol: false,
                smooth: true,
                seriesLayoutBy: "row",
                data: []
            };
            for (const item in data) {
                chartData.times.push(item);
                if (data[item].active == 0) {
                    activationCost.data.push(0);
                } else {
                    let cost = data[item].consume / data[item].active;
                    activationCost.data.push(cost);
                }
                fillCost.data.push(data[item].bidprice);
            }
            chartData.data.push(fillCost, activationCost);
            this.hasCostData = true;
            this.drawAdCostLine(chartData);
        },
        // 获取ocpm,ocpx数据
        async getDetailList(type) {
            this.isLoading = true;
            let res = await this.$api.insights.getOcpmOcpxDetailList(
                type,
                "sfsf",
                this.curTheme
            );
            let tableData = JSON.parse(res);
            this.totalTableData = tableData.slice();
            if (this.detailAdId) {
                this.tableData = [];
                for (const item of tableData) {
                    if (item.AdId == this.detailAdId) {
                        this.tableData.push(item);
                        this.showPagenation = false;
                        this.isLoading = false;
                        break;
                    }
                }
            } else {
                this.getTableData(tableData);
                this.showPagenation = true;
                this.isLoading = false;
            }
        },
        // 分类查询
        query(type) {
            switch (type) {
                case "runTime":
                    this.getBudgetAndCost();
                    break;

                case "runCost":
                    this.getCostAndFillData();
                    break;

                case "detailList":
                    this.getDetailList(this.ocType);
                    break;

                case "active_sheet":
                    this.getActiveSheetList();
                    break;

                default:
                    break;
            }
        },
        
        // 广告成本走势图
        drawAdCostLine(data) {
            let option = {
                tooltip: {
                    trigger: "axis"
                },
                legend: {
                    left: "42%",
                    top: "5%",
                    data: data.data.map(item => item.name)
                },
                grid: {
                    bottom: "23%",
                    containLabel: false
                },
                toolbox: {
                    feature: {
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: "category",
                    boundaryGap: false,
                    data: data.times,
                    axisLine: {
                        onZero: false,
                        lineStyle: {
                            color: "#337ab7"
                        }
                    }
                },
                yAxis: {
                    splitLine: { show: false }, //去除网格线
                    type: "value",
                    axisLine: {
                        onZero: false,
                        lineStyle: {
                            color: "#337ab7"
                        }
                    }
                },
                series: data.data
            };
            // 基于准备好的dom，初始化echarts实例
            this.$nextTick(() => {
                let myChart = echarts.init(document.getElementById("adCost"));
                // 绘制图表
                myChart.setOption(option);
                myChart.resize();
            });
        },
        pageChange(pageIndex) {
            this.pageIndex = pageIndex;
            this.getTableData(this.totalTableData);
        },
        getTableData(tableData) {
            this.tableData = tableData.slice(
                (this.pageIndex - 1) * this.pageSize,
                this.pageIndex * this.pageSize
            );
        },
        pageSizeChange(pageSize) {
            this.pageSize = pageSize;
            this.getTableData(this.totalTableData);
        },
        // ctr图表
        drawCtrLine(data) {
            let option = {
                tooltip: {
                    trigger: "axis",
                    formatter: params => {
                        let str = `<div style="margin: 10px;">
                                <p style="margin-bottom: 5px">${
                                    params[0] ? params[0].name : ""
                                }</p>
                                ${params
                                    .map(
                                        (item, index) => `
                                        <p>${item.marker}<span style="margin-left: 5px;">${item.value}%</span></p>
                                    `
                                    )
                                    .join("")}
                                </div>`;
                        return str;
                    }
                },
                legend: {
                    left: "center",
                    top: "5%",
                    data: data.data.map(item => item.name)
                },
                grid: {
                    bottom: "26%",
                    right: "3%",
                    containLabel: false
                },
                toolbox: {
                    feature: {
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: "category",
                    boundaryGap: false,
                    data: data.times,
                    axisLine: {
                        onZero: false,
                        lineStyle: {
                            color: "#337ab7"
                        }
                    }
                },
                yAxis: {
                    splitLine: { show: false }, //去除网格线
                    axisLabel: {
                        formatter: "{value}%"
                    },
                    axisLine: {
                        onZero: false,
                        lineStyle: {
                            color: "#337ab7"
                        }
                    }
                },
                series: data.data
            };
            // 基于准备好的dom，初始化echarts实例
            let myChart = echarts.init(document.getElementById("CtrCharts"));
            // 绘制图表
            myChart.setOption(option);
            myChart.resize();
        },

        changeCtrMode(mode) {
            this.ctrMode = mode;
            let ctrSeriesData = [];
            for (let o in this.ctrChartsData) {
                if (o.indexOf(mode) > -1) {
                    let obj = {
                        name: o.toUpperCase(),
                        type: "line",
                        symbol: "emptyCircle",
                        showSymbol: true,
                        showAllSymbol: false,
                        smooth: true,
                        seriesLayoutBy: "row",
                        data: this.ctrChartsData[o].map(item => item * 100)
                    };
                    ctrSeriesData.push(obj);
                }
            }

            this.drawCtrLine({
                data: ctrSeriesData,
                times: this.ctrChartsData.time
            });
        },

        async getCtrData() {
            let res = await this.$api.insights.getCtrData(
                "sfst",
                this.currentDate,
                Number(this.ctrAdid)
            );
            let data = JSON.parse(res);
            if (data.errcode === 0 || !data.errcode) {
                this.ctrChartsData = data.data;
                this.changeCtrMode("ctr");
            }
        }
    }
};
</script>

