<template>
    <div>
        <el-card class="box-card" style="height:630px">
            <div slot="header" class="clearfix">
                <strong>{{statData.title || 'Unknow'}}</strong>                
            </div>
            <div class="panel-body">
                <el-col :span="8" class="center-block metric-panel">
                    <p>接收消息总数</p>
                    <p class="metric-value h2" style="color: rgb(51,122,183)">
                        <strong>{{statData.recv_count}}</strong>
                    </p>
                </el-col>
                <el-col :span="8" class="center-block metric-panel">
                    <p>失败数</p>
                    <p class="metric-value h2" style="color: rgb(51,122,183)">
                        <strong>{{statData.fail_count}}</strong>
                    </p>
                </el-col>
                <el-col :span="8" class="center-block metric-panel">
                    <p>失败率</p>
                    <p class="metric-value h2" style="color: rgb(51,122,183)">
                        <strong>{{statData.fail_rate}}</strong>
                    </p>
                </el-col>
                <el-col :span="24">
                    <drawchart :chartId="'tenmaMsg' + actionCode" :chartData="chartData" yform="%" />
                </el-col>
            </div>
        </el-card>
    </div>
</template>

<script>
export default {
    props: ["actionCode"],
    data() {
        return {
            statData: {},
            chartData: {
                title: "",
                data: {},
                xAxis: []
            }
        };
    },
    mounted() {
        if (this.actionCode > 0) {
            this.getActionMsgStat();
            this.getActionMsgChartData();
        }
    },
    watch: {
        actionCode: function(val, oldVal) {
            this.getActionMsgStat();
            this.getActionMsgChartData();
        }
    },
    methods: {
        async getActionMsgStat() {
            this.list = [];
            let res = await this.$api.insights.getActionMsgStat(
                this.actionCode
            );
            if (res.errcode != 0) {
                //this.$bootbox.alert("暂时获取不到该消息的错误统计信息");
                return;
            }
            this.statData = res.data;
        },
        async getActionMsgChartData() {
            let res = await this.$api.insights.getActionMsgChartData(
                this.actionCode
            );
            if (res.errcode != 0) {
                //this.$bootbox.alert("暂时获取不到该消息的错误率曲线信息");
                return;
            }
            this.chartData = res.data;
        }
    }
};
</script>
