 <template>
    <el-row>
        <el-row>
            <el-col>
                <el-form label-width="100px">
                    <el-form-item label="是否多天对比">
                        <el-radio v-model="isMultiDate" :label="true" @change="changeIsMutiDate">是</el-radio>
                        <el-radio v-model="isMultiDate" :label="false" @change="changeIsMutiDate">否</el-radio>
                    </el-form-item>
                    <el-form-item label="数据源" v-if="!isMultiDate">
                        <el-checkbox-group v-model="dataSrcList">
                            <template v-for="(val, key) in dataTypeList">
                                <el-checkbox :label="key" :key="key">{{val}}</el-checkbox>
                            </template>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-form-item label="数据源" v-if="isMultiDate">
                        <el-radio-group v-model="dataSrc">
                            <template v-for="(val, key) in dataTypeList">
                                <el-radio :label="key" :key="key">{{val}}</el-radio>
                            </template>
                        </el-radio-group>
                    </el-form-item>

                    <el-form-item label="日期">
                        <el-date-picker
                            type="date"
                            v-model="date"
                            placeholder="选择日期"
                            value-format="yyyy-MM-dd"
                            @change="reqGetDayOcpxBudget"
                            :disabled="loadData"
                        ></el-date-picker>
                        <i class="el-icon-loading" v-if="loadData"></i>
                        <!-- <el-button
                            size="mini"
                            v-if="isMultiDate"
                            @click="addDayData"
                            type="primary"
                        >多天对比</el-button>-->
                    </el-form-item>

                    <el-form-item label="已选日期" v-if="isMultiDate">
                        <template v-for="(tag,date) in compareData">
                            <el-tag
                                :key="date"
                                closable
                                @close="deleteCompareDate(date)"
                                v-if="tag != null"
                            >{{date}}</el-tag>
                        </template>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>

        <el-row>
            <drawchart
                chartId="ocpxBudgetChartId"
                :chartData="chartData"
                lengendPosition="top"
                yform="万"
            />
        </el-row>
    </el-row>
</template>

<script>
const ocpxBudgetMap = {
    account_budget: "意图总预算",
    account_ocpx1_budget: "OCPX1意图预算",
    account_ocpx2_budget: "OCPX2意图预算",
    account_ocpx1_cpl_budget: "OCPX1-CPL意图预算",
    account_ocpx1_app_budget: "OCPX1-APP意图预算",
    account_ocpx2_cpl_budget: "OCPX2-CPL意图预算",
    account_ocpx2_app_budget: "OCPX2-APP意图预算"
};
import moment from "moment";
export default {
    data() {
        return {
            // chartData: {
            //     title: "",
            //     data: {},
            //     xAxis: []
            // },
            dataTypeList: ocpxBudgetMap,
            date: [],
            dayData: [],
            dataSrcList: [
                "account_budget",
                "account_ocpx1_budget",
                "account_ocpx2_budget"
            ],
            dataSrc: "account_budget",
            compareData: {},
            isMultiDate: false,
            loadData: false
        };
    },
    computed: {
        chartData: function() {
            if (this.isMultiDate) {
                return this.genMutiDayChartData();
            } else {
                return this.getOneDayData(this.dayData);
            }
        }
    },
    mounted() {
        this.date = moment().format("YYYY-MM-DD");
        this.reqGetDayOcpxBudget();
    },
    methods: {
        getOneDayData(tmpData) {
            let xAxis = Object.keys(tmpData);
            let retData = [];
            xAxis.forEach(x => {
                this.dataSrcList.forEach(key => {
                    let xName = this.dataTypeList[key];
                    if (retData[xName] == undefined) {
                        retData[xName] = [];
                    }
                    retData[xName].push((tmpData[x][key] / 10000).toFixed(2));
                });
            });
            return {
                xAxis: xAxis,
                data: retData,
                title: ""
            };
        },
        async reqGetDayOcpxBudget() {
            this.loadData = true;
            let res = await this.$api.insights.getDayOcpxRealBudget(this.date);
            this.loadData = false;
            if (res.errcode === 0) {
                this.$set(this, "dayData", res.data);
            } else {
                this.$set(this, "dayData", []);
            }
            if (this.isMultiDate) {
                this.addCompareData();
            }
        },
        addCompareData() {
            this.$set(
                this.compareData,
                this.date,
                this.$lodash.cloneDeep(this.dayData)
            );
        },
        changeIsMutiDate() {
            if (this.isMultiDate) {
                this.addCompareData();
            }
        },
        deleteCompareDate(date) {
            this.$set(this.compareData, this.date, null);
        },
        genMutiDayChartData() {
            let dataSource = this.dataSrc;
            let xAxisSet = [];
            Object.keys(this.compareData).forEach(d => {
                if (this.compareData[d] != null) {
                    xAxisSet = this.$lodash.union(
                        Object.keys(this.compareData[d]),
                        xAxisSet
                    );
                }
            });

            xAxisSet = xAxisSet.sort();

            let yData = {};
            Object.keys(this.compareData).forEach(d => {
                let tmpData = this.compareData[d];
                if (tmpData != null) {
                    yData[d] = [];
                    xAxisSet.forEach(x => {
                        if (tmpData[x] == undefined) {
                            yData[d].push("");
                        } else {
                            yData[d].push(
                                (tmpData[x][dataSource] / 10000).toFixed(2)
                            );
                        }
                    });
                }
            });

            return {
                xAxis: xAxisSet,
                data: yData,
                title: ""
            };
        }
    }
};
</script>