<template>
    <div>
        <peopleTop @searchNew="searchNew"></peopleTop>
        <!-- 查询结果展示 -->
        <loading :state="loadState" />

        <div v-show="loadState != 'loading'" style="overflow:scroll" id="userFeedbackScroll">
            <el-table
                :data="showDatas"
                height="700"
                stripe
                style="max-width:2000px;min-width:2000px"
                @header-click="changeSortColumn"
                :header-cell-style="{
                                'cursor':'pointer',
                                'color': '#606266',
                            }"
            >
                <el-table-column
                    prop="shield_time"
                    label="时间"
                    sortItem="pv_time"
                    :render-header="renderHeader"
                ></el-table-column>
                <el-table-column prop="feedid" label="博文/创意id" :render-header="renderHeader">
                    <template slot-scope="scope">
                        <a :href="scope.row.msg_url" target="_blank">
                            <strong>{{scope.row.mid}}</strong>
                        </a>
                    </template>
                </el-table-column>
                <el-table-column prop="ad_id" label="计划id" :render-header="renderHeader">
                    <template slot-scope="scope">
                        <adlink :adId="scope.row.ad_id" />
                    </template>
                </el-table-column>
                <el-table-column prop="feedbackcode" label="行为码" :render-header="renderHeader">
                    <template slot-scope="scope">{{scope.row.feedback_text}}</template>
                </el-table-column>
                <el-table-column prop="ad_type" label="产品线" :render-header="renderHeader">
                    <template slot-scope="scope">{{scope.row.ad_type_name}}</template>
                </el-table-column>
                <el-table-column prop="cust_id" label="广告主id" :render-header="renderHeader">
                    <template slot-scope="scope">
                        <router-link
                            target="_blank"
                            :to="{ path: '/insights/customer', query: { customer_id: scope.row.cust_id }}"
                        >
                            <strong>{{ scope.row.cust_id }}</strong>
                        </router-link>
                    </template>
                </el-table-column>
                <el-table-column prop="custid" label="广告主" :render-header="renderHeader">
                    <template slot-scope="scope">
                        <a
                            target="_blank"
                            :href="'https://weibo.com/u/' + scope.row.custid"
                        >{{scope.row.cust_name}}</a>
                    </template>
                </el-table-column>
                <el-table-column prop="tag_text" label="创意分类" :render-header="renderHeader">
                    <template slot-scope="scope">{{scope.row.tag_text || '-'}}</template>
                </el-table-column>
                <el-table-column prop="shield_time" label="失效时间" :render-header="renderHeader"></el-table-column>
            </el-table>
        </div>
        <!-- 查询结果展示结束 -->
    </div>
</template>

<script>
import perspectiveCode from "@/assets/js/page/perspectiveCode.js";
import moment from "moment";
import ScrollBtn from "@/components/Widget/ScrollBtn";

import PeopleTop from "@/Insight/People/PeopleTop";
export default {
    data() {
        return {
            uid: "1111681197",
            startDate: "",
            endDate: "",
            listData: [],
            tableShow: false,
            sortKey: "time_stamp",
            sortOrder: true,
            templateId: "userFeedbackScroll",
            loadState: false
        };
    },
    components: { ScrollBtn, PeopleTop },
    computed: {
        showDatas: function() {
            return this.sortShowData(this.listData);
        }
    },
    methods: {
        async searchNew(uid, dateRange) {
            this.loadState = "loading";
            let begin = dateRange[0];
            let end = dateRange[1];
            if (uid == "") {
                this.$alert("请输入uid");
                this.loadState = "no-data";
                return false;
            }

            let res = await this.$api.insights.getFeedBackByUid(
                uid,
                begin,
                end
            );
            if (res.errcode != 0) {
                this.$alert("对不起，没有查询结果");
                this.loadState = "no-data";
                return;
            }
            this.loadState = "";
            var list = res.data;
            list.sort(function(a, b) {
                return (
                    new Date(b.pv_time).getTime() -
                    new Date(a.pv_time).getTime()
                );
            });
            this.listData = list;
            this.tableShow = true;
        },
        async openAdPage(mid, custId) {
            let res = await this.$api.insights.convertMid(mid);
            if (res.errcode != 0) {
                this.$alert(res.errmsg || "打开链接失败");
                return;
            }
            window.open(res.data.url, "_blank");
        },
        changeSortColumn: function(columnName) {
            if (columnName == this.sortKey) {
                this.$set(this, "sortOrder", !this.sortOrder);
            } else {
                this.$set(this, "sortKey", columnName);
                this.$set(this, "sortOrder", true);
            }
        },
        sortByKey: function(key, desc) {
            return function(a, b) {
                if (desc) {
                    return a[key] <= b[key] ? 1 : -1;
                }
                return a[key] >= b[key] ? 1 : -1;
            };
        },
        sortShowData: function(data) {
            return data.sort(this.sortByKey(this.sortKey, this.sortOrder));
        },
        renderHeader(h, { column }) {
            // h即为cerateElement的简写，具体可看vue官方文档
            return h("div", [
                h("span", column.label),
                h("i", {
                    class: "el-icon-d-caret",
                    style: "color:#409eff;margin-left:5px;"
                })
            ]);
        }
    }
};
</script>
