<template>
    <el-row>
        <el-row>
            <el-form :inline="true">
                <el-col :span="6" :offset="1">
                    <el-form-item label="计划ID">
                        <el-input v-model="adId" placeholder="请输入计划ID"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="10">
                    <el-form-item label="时间范围">
                        <el-date-picker
                            v-model="timeRange"
                            type="datetimerange"
                            range-separator="至"
                            start-placeholder="开始"
                            end-placeholder="结束"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            :picker-options="pickerOptions"
                        ></el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="2">
                    <el-form-item>
                        <el-button type="primary" @click="queryAd" :disabled="loading">查询</el-button>
                    </el-form-item>
                </el-col>
                <el-col :span="2">
                    <el-form-item>
                        <el-button type="success" @click="randomOcpxId" :disabled="loading">手气不错</el-button>
                    </el-form-item>
                </el-col>
            </el-form>
        </el-row>
        <el-row>
            <h3 style="margin-top:20px">落地页PID成本控制数据监控</h3>
            <el-divider></el-divider>
            <AdPidControl
                :adId="adId"
                :timeRange="timeRange"
                ref="AdPidControl"
                @setLoading="setLoading"
            />

            <h3 style="margin-top:20px">PID成本控制Redis - 日志数据比对</h3>
            <el-divider></el-divider>
            <AdLearnStat
                :adId="adId"
                :timeRange="timeRange"
                ref="AdLearnStat"
                @setLoading="setLoading"
            />

            <!-- <h3 style="margin-top:20px">回传量走势</h3>
            <el-divider></el-divider>
            <CplPostback  :adId="adId" ref="CplPostback"
            @setLoading="setLoading" />-->
        </el-row>
    </el-row>
</template>

<script>
import moment from "moment";
import AdPidControl from "./AdPidControl";
import AdLearnStat from "./AdLearnStat";
import CplPostback from "./CplPostback";

export default {
    data() {
        let allShortcuts = [];
        allShortcuts.push({
            text: "最近5分钟",
            onClick(picker) {
                picker.$emit("pick", [
                    moment()
                        .subtract(5, "minutes")
                        .format("YYYY-MM-DD HH:mm:ss"),
                    moment().format("YYYY-MM-DD HH:mm:ss")
                ]);
            }
        });
        allShortcuts.push({
            text: "最近15分钟",
            onClick(picker) {
                picker.$emit("pick", [
                    moment()
                        .subtract(15, "minutes")
                        .format("YYYY-MM-DD HH:mm:ss"),
                    moment().format("YYYY-MM-DD HH:mm:ss")
                ]);
            }
        });
        allShortcuts.push({
            text: "最近30分钟",
            onClick(picker) {
                picker.$emit("pick", [
                    moment()
                        .subtract(30, "minutes")
                        .format("YYYY-MM-DD HH:mm:ss"),
                    moment().format("YYYY-MM-DD HH:mm:ss")
                ]);
            }
        });
        allShortcuts.push({
            text: "最近1小时",
            onClick(picker) {
                picker.$emit("pick", [
                    moment()
                        .subtract(1, "hours")
                        .format("YYYY-MM-DD HH:mm:ss"),
                    moment().format("YYYY-MM-DD HH:mm:ss")
                ]);
            }
        });
        allShortcuts.push({
            text: "最近2小时",
            onClick(picker) {
                picker.$emit("pick", [
                    moment()
                        .subtract(2, "hours")
                        .format("YYYY-MM-DD HH:mm:ss"),
                    moment().format("YYYY-MM-DD HH:mm:ss")
                ]);
            }
        });
        allShortcuts.push({
            text: "最近3小时",
            onClick(picker) {
                picker.$emit("pick", [
                    moment()
                        .subtract(3, "hours")
                        .format("YYYY-MM-DD HH:mm:ss"),
                    moment().format("YYYY-MM-DD HH:mm:ss")
                ]);
            }
        });
        allShortcuts.push({
            text: "最近5小时",
            onClick(picker) {
                picker.$emit("pick", [
                    moment()
                        .subtract(5, "hours")
                        .format("YYYY-MM-DD HH:mm:ss"),
                    moment().format("YYYY-MM-DD HH:mm:ss")
                ]);
            }
        });
        allShortcuts.push({
            text: "最近6小时",
            onClick(picker) {
                picker.$emit("pick", [
                    moment()
                        .subtract(6, "hours")
                        .format("YYYY-MM-DD HH:mm:ss"),
                    moment().format("YYYY-MM-DD HH:mm:ss")
                ]);
            }
        });
        return {
            adId: "",
            timeRange: [],
            pickerOptions: {
                shortcuts: allShortcuts
            },
            loading: false
        };
    },
    mounted() {
        this.timeRange = [
            this.$common.getTodayDate() + " 00:00:00",
            moment().format("YYYY-MM-DD HH:mm:ss")
        ];
    },
    components: {
        AdPidControl,
        AdLearnStat,
        CplPostback
    },
    methods: {
        async randomOcpxId() {
            this.loading = true;
            let res = await this.$api.insights.randomOcpxObject("cpl");
            this.loading = false;
            if (res.errcode == 0) {
                this.adId = res.data.ad_id;
                setTimeout(() => {
                    this.queryAd();
                }, 500);
            } else {
                this.$message(res.errmsg)
            }
        },
        queryAd() {
            if (this.adId == "" || parseInt(this.adId) < 1) {
                this.$message("请输入广告Id");
                return;
            }
            this.loading = true;
            this.$refs["AdPidControl"].getFillCostData();
            this.$refs["AdLearnStat"].getRedisReqData();
        },
        setLoading(f) {
            this.loading = f;
        }
    }
};
</script>

