<template>
    <el-row>
        <el-row>
            <el-form :inline="true">
                <el-form-item label="日期">
                    <el-date-picker v-model="curDate" value-format="yyyy-MM-dd" placeholder="选择日期" @change="getCustConsumeAnaly"></el-date-picker>
                </el-form-item>
            </el-form>
        </el-row>
        <el-row :gutter="20">
            <el-col :span="6">
                <el-card class="box-card">
                    <p style="color:grey; display: flex;">
                        正向流转
                        <a
                            @click="showDetail(stateInfo.last1day_new_details)"
                            href="javascript:;"
                            style="font-size:10px; text-align:right; flex: 1"
                        >详细</a>
                    </p>
                    <p class="detail-budget">
                        <strong>{{stateInfo.last1day_new_total_consume | thousandNum }}</strong><b>万元</b>
                    </p>
                    <p style="font-size:10px;text-align:center">
                        查询日新增的广告主的消耗
                    </p>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card class="box-card">
                    <p style="color:grey; display: flex">
                        平向流转
                        <a
                            @click="showDetail(stateInfo.lastdays_remain_details)"
                            href="javascript:;"
                            style="font-size:10px; text-align:right; flex: 1"
                        >详细</a>
                    </p>
                    <p class="detail-budget">
                        <strong
                            :class="{'red-font' : stateInfo.lastdays_remain_total_consume < 0}"
                        >{{ stateInfo.lastdays_remain_total_consume | thousandNum }}</strong>
                        <b>万元</b>
                    </p>
                    <p style="font-size:10px;text-align:center">
                        两天留存的广告主的消耗差值
                    </p>
                    
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card class="box-card">
                    <p style="color:grey; display: flex">
                        负向流转
                        <a
                            @click="showDetail(stateInfo.last2day_old_details, 'asc')"
                            href="javascript:;"
                            style="font-size:10px; text-align:right; flex: 1"
                        >详细</a>
                    </p>
                    <p class="detail-budget">
                        <strong
                            :class="{'red-font' : stateInfo.last2day_old_total_consume < 0}"
                        >{{ stateInfo.last2day_old_total_consume | thousandNum}}</strong><b>万元</b>
                    </p>
                     <p style="font-size:10px;text-align:center">
                        查询日前1天流失的广告主的消耗
                    </p>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card class="box-card">
                    <p style="color:grey; display: flex">总体消耗差额</p>
                    <p class="detail-budget">
                        <strong
                            :class="{'red-font' : stateInfo.lastdays_total_diff < 0}"
                        >{{ stateInfo.lastdays_total_diff | thousandNum }}</strong><b>万元</b>
                    </p>
                     <p style="font-size:10px;text-align:center">
                        &nbsp;
                    </p>
                </el-card>
            </el-col>
        </el-row>
        <el-dialog :visible.sync="showConsumeDetail" width="50%">
            <el-table :data="showList" height="450px">
                <el-table-column label="广告主Id" width="200">
                    <template slot-scope="scope">
                        <a
                            :href="'http://weibo.com/u/' + scope.row.uid "
                            target="_blank"
                        >{{scope.row.uid}}</a>
                    </template>
                </el-table-column>
                <el-table-column label="昵称" width="200">
                    <template slot-scope="scope">
                        <a
                            :href="'http://weibo.com/u/' + scope.row.uid "
                            target="_blank"
                        >{{custInfoList[scope.row.uid] != undefined ? custInfoList[scope.row.uid].name : "暂无"}}</a>
                    </template>
                </el-table-column>
                <el-table-column label="消耗" prop="consume" width="200"></el-table-column>
            </el-table>

            <el-pagination
                @current-change="changePage"
                :current-page.sync="page"
                layout="total, prev, pager, next"
                :page-size="10"
                :total="detail.length"
                align="center"
            ></el-pagination>
        </el-dialog>
    </el-row>
</template>

<script>
import moment from "moment";
export default {
    data() {
        return {
            curDate: "",
            stateInfo: {},
            showConsumeDetail: false,
            detail: [],
            page: 1,
            custInfoList: {}
        };
    },
    filters: {
        thousandNum: function(val) {
            return (val / 10000 ).toFixed(2)
            // if (val) {
            //     return (val + "").replace(/\B(?=(?:\d{3})+\b)/g, ",");
            // }
        }
    },
    computed: {
        showList: function() {
            let start = (this.page - 1) * 10;
            let end = start + 10;
            return this.detail.slice(start, end);
        }
    },
    mounted() {
        this.curDate = moment()
            .subtract(1, "d")
            .format("YYYY-MM-DD");
        this.getCustConsumeAnaly();
    },
    methods: {
        async getCustConsumeAnaly() {
            this.list = [];

            let res = await this.$api.insights.getCustConsumeAnaly(
                this.curDate.replace(/\-/g, '')
            );

            this.stateInfo = res.data;
        },
        showDetail(d, desc) {
            this.showConsumeDetail = true;
            this.detail = this.$lodash.transform(
                d,
                function(result, value, key) {
                    result.push({
                        uid: key,
                        consume: value
                    });
                },
                []
            );

            let order = desc || "desc"
            this.detail = this.$lodash.orderBy(
                this.detail,
                ["consume"],
                [order]
            );
            this.changePage(1);
        },
        changePage(num) {
            this.page = num;
            let uids = [];
            for (var i in this.showList) {
                uids.push(this.showList[i].uid);
            }
            this.batchGetUserInfo(uids);
        },
        async batchGetUserInfo(uids) {
            let res = await this.$api.insights.batchGetCustomer(uids.join(","));
            this.loadCustInfo = false;
            if (res.errcode != 0) {
                return;
            }

            this.custInfoList = res.data;
        }
    }
};
</script>
