<template>
    <el-row>
        <el-row>
            <p>
                填写成本与实时成本走势
                <i class="el-icon-loading" v-show="loading"></i>
            </p>
            <drawchart :chartData="chartData1" chartId="adFillCostId" />
        </el-row>
        <el-row style="margin-top:30px;">
            <p>
                曝光量与转化数走势
                <i class="el-icon-loading" v-show="loading"></i>
            </p>
            <drawchart :chartData="chartData2" chartId="adPvId" />
        </el-row>
        <el-row style="margin-top:30px;">
            <p>
                PID出价与结算出价
                <i class="el-icon-loading" v-show="loading"></i>
            </p>
            <drawchart :chartData="chartData3" chartId="adPidId" />
        </el-row>
    </el-row>
</template>

<script>
import moment from "moment";

export default {
    props: ["adId", "timeRange"],
    data() {
        return {
            chartData1: {
                xAixs: [],
                title: "填写成本与实时成本走势",
                data: {}
            },
            chartData2: {
                xAixs: [],
                title: "曝光量与转化数走势",
                data: {}
            },
            chartData3: {
                xAixs: [],
                title: "曝光量与转化数走势",
                data: {}
            },
            loading: false
        };
    },
    methods: {
        async getFillCostData() {
            if (
                moment(this.timeRange[0])
                    .add(2, "days")
                    .isBefore(moment(this.timeRange[1]))
            ) {
                this.$message("选择的时间太长了，暂时只支持最长两天的时间范围");
                this.$emit("setLoading", false);
                return;
            }
            this.loading = true;
            this.chartData1 = this.chartData2 = this.chartData3 = {
                xAxis: [],
                data: {}
            };
            let res = await this.$api.insights.getOcpxAdCostData(
                this.adId,
                this.timeRange[0],
                this.timeRange[1]
            );
            this.loading = false;
            let tmpxAxis = [];
            let chart1Data = {
                填写成本: [],
                实时成本: [],
                成本偏差: [],
                成本上限: [],
                成本下限: []
            };
            let chart2Data = {
                曝光量: [],
                转化量: []
            };
            let chart3Data = {
                PID出价: [],
                结算出价: []
            };
            res.data.forEach((item, idx) => {
                tmpxAxis.push(item.xaxis);
                chart1Data["填写成本"].push(item.fill_cost);
                chart1Data["实时成本"].push(item.real_cost);
                if (item.real_cost == "NaN") {
                    chart1Data["实时成本"].push("");
                } else {
                    chart1Data["实时成本"].push(item.real_cost);
                }
                if (item.cost_diff == "NaN") {
                    chart1Data["成本偏差"].push("");
                } else {
                    chart1Data["成本偏差"].push(item.cost_diff);
                }
                chart1Data["成本下限"].push(item.cost_floor);
                chart1Data["成本上限"].push(item.cost_ceil);
                chart2Data["曝光量"].push(item.cpl_imp);
                chart2Data["转化量"].push(item.cpl_convert);
                chart3Data["PID出价"].push(item.cpl_adjust_cost);
                chart3Data["结算出价"].push(item.bid_price);
            });
            this.chartData1 = {
                xAxis: tmpxAxis,
                data: chart1Data
            };
            this.chartData2 = {
                xAxis: tmpxAxis,
                data: chart2Data
            };
            this.chartData3 = {
                xAxis: tmpxAxis,
                data: chart3Data
            };
            this.$emit("setLoading", false);
        }
    }
};
</script>

