<template>
    <div>
        <h3>
            今日广告计划消耗排行榜
            <span style="color: grey; font-size: 12px">(占整体消耗比例：{{percent}})</span>
        </h3>
        <el-divider></el-divider>

        <el-table
            :data="top10ConsumeAdPlan"
            stripe
            style="width: 100%;margin-top:30px"
            v-show="loadState === 'ok'"
        >
            <el-table-column label="N.O." width="50">
                <template slot-scope="scope">{{(currentPage - 1) * 10 + scope.$index + 1}}</template>
            </el-table-column>
            <el-table-column label="消耗（元）">
                <template slot-scope="scope">
                    <strong style="color: red">{{scope.row.consume}}</strong>
                </template>
            </el-table-column>
            <el-table-column label="计划ID">
                <template slot-scope="scope">
                    <adlink :adId="scope.row.adid" />
                </template>
            </el-table-column>
            <el-table-column label="广告主" width="160px">
                <template slot-scope="scope">
                    <a
                        :href="'//weibo.com/u/' + scope.row.custid"
                        target="_blank"
                    >{{scope.row.name || '无'}}</a>
                </template>
            </el-table-column>
            <el-table-column label="广告主ID">
                <template slot-scope="scope">
                    <router-link
                        :to="{ path: '/insights/customer', query: { customer_id: scope.row.custid }}"
                        target="_blank"
                    >{{ scope.row.custid }}</router-link>
                </template>
            </el-table-column>
            <el-table-column label="营销目标" prop="Promotion_objective_remark" width="200px"></el-table-column>
            <el-table-column label="计费模式" prop="bid_type_remark"></el-table-column>
            <el-table-column label="广告出价(元)" prop="bid_price"></el-table-column>
            <el-table-column label="优化目标" prop="Optimization_objective_remark"></el-table-column>
            <el-table-column label="投放速度" prop="delivery_speed_remark"></el-table-column>
        </el-table>
        <el-pagination
            @current-change="changePage"
            :current-page.sync="currentPage"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :page-count="totalPages"
            align="center"
            style="margin:20px 0"
            v-show="loadState === 'ok'"
        ></el-pagination>
        <loading :state="loadState" />
    </div>
</template>

<script>
let totalData = [];
export default {
    data() {
        return {
            top10ConsumeAdPlan: [],
            percent: "",

            pageSize: 10,
            currentPage: 1,
            // 总页数
            totalPages: 0,
            loadState: "暂无数据"
        };
    },
    mounted() {
        this.getTop10ConsumeAdPlan();
    },
    methods: {
        async getTop10ConsumeAdPlan() {
            this.loadState = "loading";

            let res = await this.$api.insights.getTop10ConsumeAdPlan({
                product: "sfst",
                date: this.$moment().format("YYYYMMDD")
            });
            if (res.errcode != 0) {
                this.loadState = "暂无数据";
                return;
            }
            this.loadState = "ok";
            this.percent = res.percentage;
            let data = res.data;

            this.totalPages = Math.ceil(data.length / this.pageSize);
            for (let i = 0; i < this.totalPages; i++) {
                totalData.push(
                    data.slice(i * this.pageSize, (1 + i) * this.pageSize)
                );
            }
            this.top10ConsumeAdPlan = totalData[0];
        },
        changePage(page) {
            this.currentPage = page;
            this.top10ConsumeAdPlan = totalData[this.currentPage - 1];
        }
    }
};
</script>
