<template>
    <div class="lean-matrix-wrapper">
        <el-form
            :inline="true"
            class="demo-form-inline"
            label-width="80px"
            style="padding-top:20px"
        >
            <el-row>
                <el-col :span="8">
                    <el-form-item label="DSP">
                        <el-select v-model="dsp_id">
                            <el-option :key="0" :value="-1" :label="'全部'"></el-option>
                            <template v-for="item in dspList">
                                <el-option :label="item.name" :value="item.id" :key="item.id"></el-option>
                            </template>
                        </el-select>
                    </el-form-item>
                </el-col>

                <el-col :span="8">
                    <el-form-item :label="'场景流'">
                        <el-select v-model="psid" placeholder="选择场景" style="width:260px">
                            <el-option :key="0" :value="-1" :label="'全部'"></el-option>
                            <template v-for="(n, id) in sceneFlow">
                                <el-option :value="id" :key="id" :label="n"></el-option>
                            </template>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item :label="'创意'">
                        <el-select v-model="creative_id" placeholder="选择创意" style="width:260px">
                            <el-option :key="0" :value="-1" :label="'全部'"></el-option>
                            <template v-for="item in cryList">
                                <el-option
                                    :key="item.id"
                                    :value="item.creative_id"
                                    :label="item.title + '(id:' + item.creative_id + ')'"
                                ></el-option>
                            </template>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="日期">
                        <el-date-picker
                            v-model="useDate"
                            value-format="yyyy-MM-dd"
                            type="date"
                            placeholder="选择日期"
                            style="width:200px"
                        ></el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="2" :offset="2">
                    <el-button type="primary" @click="getMatrixData">查询</el-button>
                </el-col>
            </el-row>
        </el-form>

        <div class="page-header">
            <h4>精益矩阵</h4>
        </div>

        <loading :state="loadState" />
        <p style="text-align:center;">
            <a href="http://wiki.intra.sina.com.cn/pages/viewpage.action?pageId=233346673">广告投放策略备案库</a>
        </p>
        <LeanMatrix
            :theFunnelData="funnelData"
            @showMatrixFilter="showMatrixFilter"
            v-if="loadState != 'loading'"
            :wikiNotShow="true"
        ></LeanMatrix>

        <el-dialog title="详情" :visible.sync="showDetail" width="75%">
            <div style="padding:0 30px;display:flex;justly-content:center;" align="center">
                <el-card style="width:30%;margin:0 20px" align="center">
                    <p class="remain">剩余数:</p>
                    <span style="font-size:20px;color:rgb(0, 152, 217)">{{modalData.cnt}}</span>
                </el-card>
                <el-card style="width:30%;margin:0 20px" align="center">
                    <p class="filter">过滤数:</p>
                    <span style="font-size:20px;color:rgb(0, 152, 217)">{{modalData.filter_cnt}}</span>
                </el-card>
                <el-card style="width:30%;margin:0 20px" align="center">
                    <p class="filter-rate">过滤占比:</p>
                    <span
                        style="font-size:20px;color:rgb(0, 152, 217)"
                    >{{Number(modalData.filter_ratio * 100).toFixed(2)}}%</span>
                </el-card>
            </div>
        </el-dialog>
    </div>
</template>

<script>
// 引入 ECharts 主模块

import sceneFlow from "@/config/SceneFlow.js";
import moment from "moment";
import lodash from "lodash";
import LeanMatrix from "@/Insight/View/LeanMatrix";

export default {
    components: {
        LeanMatrix
    },
    data() {
        return {
            funnelData: [],
            sceneFlow: {
                1000000002:	"主信息流",
                1000000004: "热门微博流",
                1000000008: "开机报头",
                1000000009: "正文页",
                1000000010: "评论流",
                1000000011: "图集流",
                1000000013: "视频贴片",
                1000000014: "视频后推荐",
                1000000015: "微博极速版",
                1000000016: "竖版视频",
                1000000017: "快应用",
                1000000018: "图集瀑布流",
                3000000001: "站外信息流"
            },

            useDate: "",
            psid: "",
            dsp_id: "",
            creative_id: "",
            dspList: [],
            cryList: [],
            loadState: "",
            showDetail: false,
            modalData: {}
        };
    },
    watch: {
        dsp_id: function() {
            this.creative_id = -1;
            this.getDspCrstyList();
        }
    },
    mounted() {
        this.useDate = moment().format("YYYY-MM-DD");
        this.getWaxDspList();
    },
    methods: {
        async getWaxDspList() {
            let res = await this.$api.insights.getWaxDspList();
            this.dspList = res;
        },
        async getDspCrstyList() {
            let res = await this.$api.insights.getDspCrstyList(this.dsp_id);
            this.cryList = res;
        },
        async getMatrixData() {
            if (this.useDate == "" || this.useDate == null) {
                this.$message("请选择日期");
                return;
            }

            let data = {
                date: this.useDate
            };
            if (this.psid > 0) {
                data.psid = this.psid;
            }
            if (this.dsp_id > 0) {
                data.dsp_id = this.dsp_id;
            }
            if (this.creative_id > 0) {
                data.crid = this.creative_id;
            }

            this.loadState = "loading";
            let mxData = await this.$api.insights.getWaxMatrixData(data);
            this.loadState = "ok";
            if (mxData.errcode == 0) {
                this.funnelData = mxData.data;
            } else {
                this.funnelData = [];
            }
        },
        showMatrixFilter(d) {
            this.modalData = d;
            this.showDetail = true;
        }
    }
};
</script>