<template>
    <el-row>
        
        <el-divider><p class="text" style="font-size:18px;">
            共{{allIps.length}}个
            <el-button type="primary" size="small" @click="copyIp(allIps.join('\n'))">复制所有ip</el-button>
        </p></el-divider>
        <el-row :gutter="10">
            <template v-for="item in allGroupIp">
                <el-col :span="4">
                    <el-card class="box-card">
                        <div slot="header" class="clearfix">
                            <span>{{item.name}}（{{item.ip.length}}）</span>
                            <el-button
                                style="float: right; padding: 3px 0"
                                type="text"
                                @click="copyIp(item.ip.join('\n'))"
                            >复制ip</el-button>
                        </div>
                        <div v-for="(ip,i) in item.ip" :key="i" class="text item">{{i+1}}. {{ip}}</div>
                    </el-card>
                </el-col>
            </template>
        </el-row>
    </el-row>
</template>

<script>
import moment from "moment";

const KingIpConf = [
    { name: "阿里云", ip_prefix: "10.85.", ip: [] },
    { name: "华为云", ip_prefix: "10.131.", ip: [] },
    { name: "北显1", ip_prefix: "10.77.", ip: [] },
    { name: "北显2", ip_prefix: "10.13.", ip: [] },
    { name: "大白楼", ip_prefix: "10.41.", ip: [] },
    { name: "瀛海", ip_prefix: "10.23.", ip: [] }
];
export default {
    data() {
        return {
            kingIpNodes: [],
            allIps: [],
            allGroupIp: []
        };
    },
    mounted() {
        this.getKingIps();
        this.allGroupIp = this.$lodash.cloneDeep(KingIpConf);
    },
    methods: {
        async getKingIps() {
            let res = await this.$api.insights.getKingIps();
            this.kingIpNodes = res.data;
            this.formatAllIps();
        },
        formatAllIps() {
            let groupIp = {};
            this.kingIpNodes.forEach(node => {
                this.allIps.push(node.Address);

                let ns = node.Address.split(".");

                let ipPrefix = ns[0] + "." + ns[1] + ".";

                if (groupIp[ipPrefix] == undefined) {
                    groupIp[ipPrefix] = [];
                }

                groupIp[ipPrefix].push(node.Address);
            });
            let kingIpPrefixs = [];
            this.allGroupIp.forEach((node, i) => {
                if (groupIp[node.ip_prefix] != undefined) {
                    this.allGroupIp[i].ip = groupIp[node.ip_prefix];
                }
                kingIpPrefixs.push(node.ip_prefix);
            });
            let g = this.$lodash.difference(
                Object.keys(groupIp),
                kingIpPrefixs
            );

            console.log(g);

            g.forEach(ipP => {
                this.allGroupIp.push({
                    name: "unknow",
                    ip_prefix: ipP,
                    ip: groupIp[ipP]
                });
            });
        },
        copyIp(data) {
            this.$copyText(data).then(
                e => {
                    this.$message("复制成功");
                },
                () => {
                    alert("复制失败");
                }
            );
        }
    }
};
</script>

