<template>
    <div>
        <el-form :inline="true" class="demo-form-inline" style="margin: 30px 0 0 20px">
            <el-row :gutter="20">
                <el-col :span="6">
                    <el-form-item label="产品线">
                        <el-select v-model="product" @change="search">
                            <el-option label="超级粉丝通" value="sfst"></el-option>
                            <el-option label="Wax" value="wax"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="广告主id">
                        <el-input
                            v-model="custId"
                            placeholder="请输入广告主id"
                            @keyup.enter.native="search"
                        ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item>
                        <el-button type="primary" @click="search">查询</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div style="font-size: 32px;color: rgb(85,160,85)">
            <loading :state="loadState" />
        </div>
        <el-card v-show="lastStatTime != ''">
            <div class="text-center">
                <h6>最新校验时间：{{lastStatTime}}</h6>
            </div>
        </el-card>

        <el-card class="box-card" v-if="list.length >0 ">
            <div slot="header" class="clearfix">
                <span>
                    统计时间：
                    <strong>{{list[0].insert_time}}</strong>
                </span>
            </div>

            <div>
                <el-table :data="list" stripe style="width: 100%">
                    <el-table-column label="广告主ID" width="120">
                        <template slot-scope="scope">
                            <router-link
                                :to="{ path: '/insights/customer', query: { customer_id: scope.row.cust_id }}"
                            >{{ scope.row.cust_id }}</router-link>
                        </template>
                    </el-table-column>
                    <el-table-column label="广告主" width="120">
                        <template slot-scope="scope">
                            <a
                                :href="'//weibo.com/u/' + scope.row.cust_id"
                                target="_blank"
                            >{{scope.row.nick_name|| '无'}}</a>
                        </template>
                    </el-table-column>
                    <el-table-column prop="jiesuan_balance" label="结算余额（元）"></el-table-column>
                    <el-table-column prop="jucai_balance" label="聚财余额（元）"></el-table-column>
                    <el-table-column label="聚财偏差(聚财 - 结算)（元">
                        <template slot-scope="scope">
                            <span
                                style="text-align: right"
                                :class="{'red-font' : scope.row.jucai_offset > 0 ||scope.row.jucai_offset < 0  }"
                            >{{scope.row.jucai_offset}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="bp_balance" label="BP余额（元）"></el-table-column>
                    <el-table-column prop="address" label="BP偏差(BP - 结算)（元）">
                        <template slot-scope="scope">
                            <span
                                style="text-align: right"
                                :class="{'red-font' : scope.row.bp_offset > 0 || scope.row.bp_offset < 0}"
                            >{{scope.row.bp_offset}}</span>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <!-- <div class="text-center" v-html="paginator" @click="getData($event)"></div> -->

        </el-card>

    </div>
</template>

<script>
export default {
    data() {
        return {
            custId: "",
            paginator: "",
            list: [],
            loadState: "",
            page: 1,
            lastStatTime: "",
            product: "sfst"
        };
    },
    mounted() {
        if (this.$route.query.cust_id > 0) {
            this.ad_id = this.$route.query.cust_id;
            this.search();
        } else {
            this.getCalibrationList();
        }
    },
    methods: {
        search: function() {
            var reg = /^[0-9]+.?[0-9]*$/;

            if (this.custId == "") {
                this.list = [];

                this.lastStatTime = "";

                this.getCalibrationList();

                return false;
            }

            if (!reg.test(this.custId)) {
                this.$bootbox.alert("广告主id不能含有非数字字母");

                return false;
            }

            this.lastStatTime = "";
            this.getCustomerCalibration();
        },
        async getCustomerCalibration() {
            this.list = [];
            this.loadState = "loading";

            let res = await this.$api.insights.getCustomerCalibration(
                this.custId,
                this.product
            );
            this.loadState = "";
            if (res.errcode != 0) {
                this.$bootbox.alert("暂时获取不到该广告主的财务误差信息");
                return;
            }
            this.loadState = "ok";
            this.paginator = res.data.footer;
            this.list = res.data.list;
            this.$forceUpdate();
            if (this.list.length == 0) {
                this.loadState = "该广告主聚财和结算没有误差";
            }
        },
        async getCalibrationList() {
            this.list = [];
            this.loadState = "loading";

            let res = await this.$api.insights.getCalibrationList(
                this.page,
                this.product
            );
            this.loadState = "";

            if (res.errcode != 0) {
                this.$bootbox.alert("暂时获取不到广告消耗信息");
                return;
            }

            this.loadState = "ok";

            this.paginator = res.data.footer;

            console.log(res.data.list.length, res.data.list[0].cust_id);
            if (res.data.list.length == 1 && res.data.list[0].cust_id == 0) {
                this.loadState = "结算账户、聚财账户、BP账户一致性验证通过！";

                this.list = [];

                this.lastStatTime = res.data.list[0].insert_time;
            } else {
                this.list = res.data.list;
            }
        },
        getData: function(e) {
            this.page = $(e.target).data("page");

            this.getCalibrationList();
        }
    }
};
</script>
