<template>
    <div>
        <el-table
            stripe
            :data="showDatas"
            height="700"
            style="max-width:2000px;min-width:2000px"
            @header-click="changeSortColumn"
            :header-cell-style="{
                'cursor':'pointer',
                'color': '#606266',
            }"
        >
            <el-table-column
                prop="pv_time"
                label="曝光时间"
                :render-header="renderHeader"
            ></el-table-column>
            <el-table-column
                prop="博文id"
                label="博文/创意id"
                :render-header="renderHeader"
            >
                <template slot-scope="scope">
                    <a :href="scope.row.msg_url" target="_blank">
                        <strong>{{scope.row.mid}}</strong>
                    </a>
                </template>
            </el-table-column>
            <el-table-column prop="creative_style_name" label="创意样式" :render-header="renderHeader"></el-table-column>
            <el-table-column prop="ad_id" label="计划id" :render-header="renderHeader">
                <template slot-scope="scope">
                    <adlink :adId="scope.row.ad_id" />
                </template>
            </el-table-column>
            <el-table-column prop="customer_nickname" label="广告主" :render-header="renderHeader">
                <template slot-scope="scope">
                    <template
                        v-if="!loadCustInfo && custInfoList[scope.row.customer_id] != undefined"
                    >
                        <a
                            target="_blank"
                            :href="'https://weibo.com/u/' + scope.row.customer_id"
                        >{{custInfoList[scope.row.customer_id].name}}</a>
                    </template>
                    <template
                        v-else-if="!loadCustInfo && custInfoList[scope.row.customer_id] == undefined"
                    >暂无</template>
                    <template v-else>获取中...</template>
                </template>
            </el-table-column>
            <el-table-column prop="customer_id" label="广告主id" :render-header="renderHeader">
                <template slot-scope="scope">
                    <router-link
                        target="_blank"
                        :to="{ path: '/insights/customer', query: { customer_id: scope.row.customer_id }}"
                    >
                        <strong>{{ scope.row.customer_id }}</strong>
                    </router-link>
                </template>
            </el-table-column>
            <el-table-column prop="customer_category" label="广告行业" :render-header="renderHeader">
                <template slot-scope="scope">{{scope.row.industry}}</template>
            </el-table-column>
            <el-table-column prop="ad_type" label="产品线" :render-header="renderHeader">
                <template slot-scope="scope">{{scope.row.ad_type_name}}</template>
            </el-table-column>
            <el-table-column prop="psid" label="场景" :render-header="renderHeader">
                <template slot-scope="scope">{{scope.row.psid_name}}</template>
            </el-table-column>
            <el-table-column prop="promotion_objective" label="营销目标" :render-header="renderHeader">
                <template slot-scope="scope">{{scope.row.promotion_objective_name}}</template>
            </el-table-column>
            <el-table-column prop="device" label="设备终端" :render-header="renderHeader">
                <template slot-scope="scope">{{scope.row.device_name}}</template>
            </el-table-column>
             <el-table-column prop="frequency_type" label="频次类型" :render-header="renderHeader">
                <template slot-scope="scope">{{scope.row.frequency_type}}</template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {

    props:['showDatas','custInfoList','loadCustInfo'],
    methods: {
        renderHeader(h, { column }) {
            // h即为cerateElement的简写，具体可看vue官方文档
            return h("div", [
                h("span", column.label),
                h("i", {
                    class: "el-icon-d-caret",
                    style: "color:#409eff;margin-left:5px;"
                })
            ]);
        },
        changeSortColumn(column) {
            this.$emit('changeSortColumn',column);
        }
    }
};
</script>

