<template>
    <div id="app">
        <el-container>
            <el-header :height="headerHeight">
                <myheader
                    :userName="userName"
                    :userType="userType"
                    contentType="insights"
                    @setHeaderHeight="setHeaderHeight"
                ></myheader>
            </el-header>
            <el-container>
                <el-aside :width="showSlideMenu?open:close">
                    <slidemenu
                        :currentMenuData="insightMenuList"
                        :showSlideMenu="showSlideMenu"
                        @expand="expand"
                        style="overflow-y:auto;ovreflow-x:hidden"
                    ></slidemenu>
                </el-aside>
                <el-main>
                    <el-row>
                        <el-col :span="21">
                            <el-breadcrumb separator-class="el-icon-arrow-right">
                                <el-breadcrumb-item :to="{ path: '/insights/index' }">首页</el-breadcrumb-item>
                                <el-breadcrumb-item
                                    v-for="(item, idx) in $route.meta.title"
                                    :key="idx"
                                >{{item}}</el-breadcrumb-item>
                            </el-breadcrumb>
                        </el-col>
                        <el-col :span="2">
                            <a href="/static/insight-update-0106.pdf" target="_blank">菜单栏调整详情</a>
                        </el-col>
                    </el-row>
                    <el-divider></el-divider>
                    <router-view @hook:mounted="handleChildCreated" />
                </el-main>
            </el-container>
        </el-container>
        <el-dialog title="侧边菜单栏调整通知" :visible.sync="updateDailog" width="60%">
            <iframe src="/static/insight-update-0106.pdf" width="100%" height="500px"/>
            <el-row style="margin-top:10px;">
                <el-col align="center">
                <el-button type="primary" @click="closeUpdateDailog">我知道了，不再展示</el-button>
                </el-col>
            </el-row>
        </el-dialog>
    </div>
</template>
<script>
//components
import myheader from "@/components/EleHeader";
import slideMenu from "@/components/SlideMenu";

//css style
import "@/assets/css/bootstrap-3.3.7.css";

import "@/assets/css/common.less";

import InsightsMenu from "@/config/InsightMenu";
import moment from 'moment';

export default {
    name: "Insights",
    data() {
        return {
            showSlideMenu: true,
            currentMenuData: [],
            userType: 0,
            userName: "",
            contentType: "insights",
            insightMenuList: [],
            allPages: [],
            isInitAllPage: false,
            close: "64px",
            open: "210px",
            headerHeight: "60px",
            updateDailog : false,
        };
    },
    components: {
        myheader: myheader,
        slidemenu: slideMenu
    },
    methods: {
        expand(flag) {
            this.$set(this, "showSlideMenu", flag);
        },
        setHeaderHeight(h) {
            this.headerHeight = h;
        },
        async getUserInfo() {
            let res = await this.$api.insights.getLoginUserAccess();
            if (res.errcode == 0) {
                this.userType = res.data.user_type;
                this.userName = res.data.user_name;
            }

            this.getInsightMenu(res.data);
            this.getAllPage();
            this.handleChildCreated();
        },
        getInsightMenu: function(insightUser) {
            if (this.userType == 0) {
                this.insightMenuList = [];
                return;
            }
            if (this.userType == 10) {
                this.insightMenuList = InsightsMenu;
                return;
            }

            for (var i in InsightsMenu) {
                if (
                    insightUser.page_list.indexOf(
                        InsightsMenu[i].mainMenuName
                    ) >= 0
                ) {
                    this.insightMenuList.push(InsightsMenu[i]);
                    continue;
                }

                let secondMenuList = [];

                for (var j in InsightsMenu[i].secondLevelMenu) {
                    if (
                        insightUser.page_list.indexOf(
                            InsightsMenu[i].secondLevelMenu[j].url
                        ) >= 0
                    ) {
                        secondMenuList.push(InsightsMenu[i].secondLevelMenu[j]);
                    }
                }

                if (secondMenuList.length > 0) {
                    this.insightMenuList.push({
                        iconfont: InsightsMenu[i].iconfont,
                        mainMenuName: InsightsMenu[i].mainMenuName,
                        url: InsightsMenu[i].url,
                        badge: InsightsMenu[i].badge,
                        secondLevelMenu: secondMenuList
                    });
                }
            }
        },
        handleChildCreated() {
            if (this.isInitAllPage) {
                this.isInsightsAccess();
                this.sendPageView();
                this.checkUpdatePage();
            }
        },
        checkUpdatePage: function() {
            let initPageAt = this.PageInitAt;

            if (moment(initPageAt).isBefore(moment().subtract(3600, "s"))) {
                window.location.reload();
            }
        },
        async sendPageView() {
            let fullUrl = this.$route.path;
            let queryKeys = Object.keys(this.$route.query);
            let queryPairs = [];
            queryKeys.forEach(item => {
                queryPairs.push(item + "=" + this.$route.query[item]);
            });
            let res = await this.$api.user.sendPvStat(
                fullUrl + "?" + queryPairs.join("-")
            );
        },
        getAllPage: function() {
            for (var i in this.insightMenuList) {
                for (var j in this.insightMenuList[i].secondLevelMenu) {
                    this.allPages.push(
                        this.insightMenuList[i].secondLevelMenu[j].url
                    );
                }
            }
            this.isInitAllPage = true;
        },
        isInsightsAccess: function() {
            if(this.userType > 1) {
                return;
            }
            if (
                this.$route.path == "/" ||
                "/insights/forbidden" == this.$route.path
            ) {
                return;
            }
            if (this.allPages.indexOf(this.$route.path) > -1) {
                return;
            }

            this.$router.push({ path: "/insights/forbidden" });
        },
        closeUpdateDailog() {
            this.updateDailog = false;
            localStorage.setItem("insight-update-0106", moment().seconds)
        }
    },
    mounted() {
        this.getUserInfo(false);
        if (localStorage.getItem("insight-update-0106") == null) {
            this.updateDailog = true;
        }
        
    }
};
</script>
