<template>
<div>
    <div :id="chartId" style="height:500px;width: 100%;">

    </div>
</div>
</template>

<script>

import lodash from 'lodash';
import echarts from 'echarts';
require('echarts/theme/vintage')
require('echarts/theme/dark')
require('echarts/theme/infographic')
require('echarts/theme/macarons')
require('echarts/theme/shine')
import ChartConfig from '@/config/ChartConfig';
const lineColorConfig = [

];
export default {
    props : ['chartId', 'chartData', 'maxShowVal', 'xform', 'horizon'],
    watch : {
        chartData : {
            handler : function(val) {
                this.drawChart(val);
            },
            deep : true
        },
    },
    mounted(){
        this.drawChart(this.chartData);
    },
    methods:{
        drawChart : function(chartData) {
            var myChart = echarts.init(document.getElementById(this.chartId), 'shine');

            let option = {
                tooltip : {
                    trigger: 'axis',
                    axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                        type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                    },
                },
                legend: {
                    data:[]
                },
                toolbox: {
                    show : false,
                    feature : {
                        mark : {show: true},
                        dataView : {show: true, readOnly: false},
                        magicType : {show: true, type: ['line', 'bar', 'stack', 'tiled']},
                        restore : {show: true},
                        saveAsImage : {show: true}
                    }
                },
                calculable : true,
                // xAxis : [
                //     {
                //         type : 'category',
                //         data : []
                //     }
                // ],
                // yAxis : [
                    
                //     {
                //         type : 'value'
                //     }
                // ],
                series : []
            };

            if(this.horizon == 'x') {
                option.xAxis = [
                    {
                        type : 'category',
                        data : [],
                        axisLabel: {
                            interval: 0,
                        }
                    }
                ];
                option.yAxis = [
                   {
                        type : 'value'
                    }
                ];
                option.xAxis[0].data = chartData.yAxis;
            } else {
                option.xAxis = [
                    {
                        type : 'value'
                    }
                ];
                option.yAxis = [
                    {
                        type : 'category',
                        data : []
                    }
                ];
                option.yAxis[0].data = chartData.yAxis;
            }
            
            option.legend.data = Object.keys(chartData.data);
            let legendList = Object.keys(chartData.data);
            let legendListData = [];
            let that = this;
            legendList.forEach( function(element, index) {
                legendListData.push({
                    data : chartData.data[element],
                    name : element,
                    type : 'bar',
                    stack: '总量',
                    itemStyle : { normal: {label : {show: true, position: 'insideRight', formatter : function(p) {
                        if(p.value <= that.maxShowVal) {
                            return '';
                        }
                        return p.value + that.xform;
                    }}}},
                })
            });

            option.series = legendListData;
            // option.plotOptions.column.formatter = function(){
            //     if(this.y == 0){
            //         return '';
            //     } else {
            //         return (this.y);
            //     }
            // }
            myChart.clear();
            myChart.setOption(option);   
        },
    }
}
</script>
