<template>
    <el-row>
        <el-row>
            <el-form :inline="true">
                <el-col :span="6" :offset="1">
                    <el-form-item label="数据类型:">
                        <el-select v-model="curTrenType">
                            <el-option
                                :label="te.name"
                                :value="te.value"
                                v-for="(te, idx) in trendType"
                                :key="idx"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="营销目标:">
                        <el-select v-model="curTrendTheme">
                            <el-option
                                v-for="(te, idx) in theme"
                                :label="te.name"
                                :value="te.value"
                                :key="idx"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="优化目标:">
                        <el-select v-model="uType">
                            <el-option
                                v-for="(te, idx) in uTypeList"
                                :label="te.name"
                                :value="te.value"
                                :key="idx"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-form>
            <el-form :inline="true">
                <el-col :span="8" :offset="1">
                    <el-form-item label="查询时间:">
                        <el-date-picker
                            v-model="currentDate"
                            type="date"
                            placeholder="选择日期"
                            value-format="yyyy-MM-dd"
                        ></el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="2" style="text-align: right;">
                    <el-button type="primary" @click="getBudgetAndCost">查 询</el-button>
                </el-col>
            </el-form>
        </el-row>
        <el-row>
            <el-col :span="20">
                <div id="movements" style="width: 100%; height: 400px; overflow:hidden;"></div>
            </el-col>
            <el-col :span="4">
                <el-button
                    style="display:block;position:absolute;right:54px;top:20px"
                    @click="swicthStatType('累计')"
                    v-bind="{type: statType=='累计' ? 'success':'defalut'}"
                    size="mini"
                >累计</el-button>
                <el-button
                    style="display:block;position:absolute;right:54px;top:50px"
                    @click="swicthStatType('分时')"
                    v-bind="{type: statType=='分时' ? 'success':'defalut'}"
                    size="mini"
                >分时</el-button>
            </el-col>
        </el-row>
    </el-row>
</template>

<script>
import moment from "moment";
import echarts from "echarts";

export default {
    props: ["adId"],
    data() {
        return {
            currentDate: "",
            curTrenType: "ocpm_budget",
            curTrendTheme: "",
            theme: [
                { name: "整体", value: "" },
                { name: "收集销售线索", value: "88030003" },
                { name: "推广App", value: "88030004" },
                { name: "推广内容", value: "88030002" }
            ],
            trendType: [
                {
                    name: "ocpm预算",
                    value: "ocpm_budget"
                },
                {
                    name: "ocpm消耗",
                    value: "ocpm_consume"
                },
                {
                    name: "ocpx预算",
                    value: "ocpx_budget"
                },
                {
                    name: "ocpx消耗",
                    value: "ocpx_consume"
                }
            ],
            uTypeList: [
                { name: "整体", value: "" },
                { name: "加关注", value: "86002003" },
                { name: "线索(表单提交)", value: "86005001" },
                { name: "使用率", value: "86004002" },
                { name: "安装率", value: "86004001" }
            ],
            uType: "",
            statType: "累计",
            bcData: {},
            bcChartData: {
                times: [],
                data: []
            }
        };
    },
    mounted() {
        this.currentDate = moment().format("YYYY-MM-DD");
        this.getBudgetAndCost();
    },
    methods: {
        async getBudgetAndCost() {
            let theme = this.curTrendTheme;
            let date = moment(this.currentDate).format("YYYY-MM-DD");
            this.drawBCLine({ data: [] });
            let l = this.$loading({
                lock: false,
                text: "加载中",
                spinner: "el-icon-loading",
                background: "rgba(0, 0, 0, 0.7)",
                target: "#movements"
            });
            let res = await this.$api.insights.getBudgetAndCost(
                theme,
                this.uType,
                date,
                this.curTrenType
            );
            l.close();
            this.bcData = res.data;
            // 统一获取时间列表
            this.bcChartData.times =
                this.bcData["时间列表"] != null
                    ? this.bcData["时间列表"].map(item => item.split(" ")[1])
                    : [];
            this.swicthStatType(this.statType);
        },
        swicthStatType(state) {
            this.bcChartData.data = [];
            this.statType = state;
            let len = this.bcChartData.times.length;
            for (const key in this.bcData[state]) {
                let lineData = {
                    name: "",
                    type: "line",
                    symbol: "emptyCircle",
                    showSymbol: true,
                    showAllSymbol: false,
                    smooth: true,
                    seriesLayoutBy: "row",
                    data: []
                };
                lineData.name = key;
                lineData.data = this.bcData[state][key]
                    ? this.bcData[state][key]
                    : new Array(len).fill(0, 0, len);
                this.bcChartData.data.push(lineData);
            }
            this.drawBCLine(this.bcChartData);
        },
        // 预算和消耗走势图
        drawBCLine(data) {
            let option = {
                tooltip: {
                    trigger: "axis"
                },
                legend: {
                    left: "center",
                    top: "5%",
                    data: data.data.map(item => item.name)
                },
                grid: {
                    bottom: "26%",
                    right: "3%",
                    containLabel: false
                },
                toolbox: {
                    feature: {
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: "category",
                    boundaryGap: false,
                    data: data.times,
                    axisLine: {
                        onZero: false,
                        lineStyle: {
                            color: "#337ab7"
                        }
                    }
                },
                yAxis: {
                    splitLine: { show: false }, //去除网格线
                    type: "value",
                    axisLine: {
                        onZero: false,
                        lineStyle: {
                            color: "#337ab7"
                        }
                    }
                },
                series: data.data
            };
            // 基于准备好的dom，初始化echarts实例
            let myChart = echarts.init(document.getElementById("movements"));
            // 绘制图表
            myChart.setOption(option);
            myChart.resize();
        }
    }
};
</script>

