            <div v-show="loadState != 'loading'">
                <AdPlanListTable
                    :list="adPlanList"
                    @clickBidType="clickBidType"
                    :bidType="typeToChild"
                />
                <div style="text-align: center;padding:20px 0">
                    <el-pagination
                        @current-change="pageChange"
                        :current-page.sync="page"
                        :page-size="pageSize"
                        layout="total, prev, pager, next"
                        :total="total"
                    ></el-pagination>
