<script>
import * as CONST from "@/constant.js";
import AdPlanListTable from "./AdPlanListTable";
import { VPagination } from "vue-easytable";
import ScrollBtn from "@/components/Widget/ScrollBtn";

export default {
    components: { VPagination, AdPlanListTable, ScrollBtn },
    data() {
        return {
            loadState: "",
            adId: "",
            custId: "",
            adStatus: "1",
            bidType: "0",
            marketTarget: "",
            adPlanList: [],
            pageSize: 30,
            page: 1,
            total: 0,
            templateId: "adPlanScroll",
            typeToChild: 0
        };
    },
    mounted() {
        this.getAdPlanList();
    },
    methods: {
        async getAdPlanList() {
            this.list = [];
            this.pager = "";
            this.loadState = "loading";

            var sendParam = {
                page: this.page,
                status: this.adStatus,
                adid: this.adId,
                customer_id: this.custId,
                bidtype: parseInt(this.bidType),
                promotion: this.marketTarget
            };
            let res = await this.$api.insights.getAdPlanList(sendParam);
            if (res.errcode === 0) {
                this.loadState = "ok";
                this.adPlanList = res.data.list;
                this.pageSize = res.page_capacity;
                this.total = res.page_number * res.page_capacity;
                this.typeToChild = this.bidType;
            } else {
                this.$bootbox.alert("服务暂时不可用，请稍后再试");
            }
        },

        /* 切换页码 */
        pageChange(num) {
            this.page = num;
            this.getAdPlanList();
        },

        getData: function(e) {
            this.page = $(e.target).data("page");
            this.getAdPlanList();
        },
        clickBidType: function(item) {
            console.log(item);
        },
        search: function() {
            this.page = 1;
            this.getAdPlanList();
        }
    }
};
</script>

