<template>
    <el-row>
        <el-row v-show="objType != 'total'">
            <p>今日最近50条</p>
            <el-table :data="objList" height="200px" border id="app-object-list">
                <el-table-column :property="objType" :label="objectName" width="130">
                    <template slot-scope="scope">
                        <el-link @click="inputObjVal(scope.row)">{{scope.row[objType]}}</el-link>
                    </template>
                </el-table-column>
                <template v-for="(n, k, i) in quotaConf">
                    <el-table-column :property="k" :label="n" width="120" :key="i"></el-table-column>
                </template>
            </el-table>
        </el-row>
        <el-row style="margin-top:30px;">
            <el-col :span="10" :offset="1">
                <el-input
                    placeholder="请输入id"
                    v-model="objVal"
                    class="input-with-select"
                    :disabled="objType == 'total'"
                >
                    <template slot="prepend">
                        <el-select v-model="objType" @change="selectObjType">
                            <el-option label="广告计划" value="ad_id"></el-option>
                            <el-option label="广告主" value="customer_id"></el-option>
                            <el-option label="整体" value="total"></el-option>
                        </el-select>
                    </template>
                </el-input>
            </el-col>
            <el-col :span="5" :offset="1">
                <el-button type="primary" @click="queryAll" :loading="loading">查询</el-button>
                <el-button
                    type="warning"
                    @click="gookLuck"
                    :disabled="loading"
                    v-if="objType != 'total'"
                >手气不错</el-button>
            </el-col>
        </el-row>

        <el-row style="margin-top:40px;">
            <p class="h3">发送量、回传量趋势</p>
            <el-divider></el-divider>
            <el-form :inline="true" label-width="100px">
                <el-form-item label="日期">
                    <el-date-picker
                        v-model="day"
                        type="date"
                        placeholder="选择日期"
                        @change="trendDayChange"
                        :value-format="'yyyy-MM-dd'"
                        :disabled="loading"
                    ></el-date-picker>
                </el-form-item>
                <span style="display:inline-block;float:right;">
                    <el-link type="primary" @click="trendDialog = true">查看原数据</el-link>
                </span>
            </el-form>
        </el-row>
        <el-row style="text-align:center">
            <el-radio-group v-model="dataType">
                <template v-for="(n, k, idx) in quotaConf">
                    <el-radio :label="k" :key="idx">{{ n }}</el-radio>
                </template>
            </el-radio-group>
        </el-row>
        <el-row id="trend-chart">
            <drawchart :chartData="trendChartData" chartId="app-trend-chart-id" />
        </el-row>
        <el-row style="text-align:center;margin-top:20px;">
            <el-checkbox-group v-model="dayDataType">
                <template v-for="(n, k, idx) in quotaConf">
                    <el-checkbox :label="k" :key="idx">{{ n }}</el-checkbox>
                </template>
            </el-checkbox-group>
        </el-row>
        <el-row id="day-trend-chart">
            <drawchart :chartData="dayTrendChartData" chartId="app-day-trend-chart-id" />
        </el-row>

        <el-row style="margin-top:40px;">
            <p class="h3">发送量、回传量分布</p>
            <el-divider></el-divider>
            <el-row style="text-align:center;margin-top:30px;">
                <el-form :inline="true" label-width="100px">
                    <el-form-item label="日期范围">
                        <el-date-picker
                            v-model="dayRange"
                            type="daterange"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            value-format="yyyy-MM-dd"
                            @change="getDistribute"
                        ></el-date-picker>
                    </el-form-item>
                </el-form>
            </el-row>
            <el-divider></el-divider>
        </el-row>

        <el-row id="send-distribute-chart">
            <el-col style="text-align:center" :span="12">
                <el-row style="margin-bottom:30px;">
                    <el-radio-group v-model="dimType" @change="getDistribute">
                        <el-radio :label="'sorted_type'">互动类型</el-radio>
                        <el-radio :label="'host'">IP</el-radio>
                        <el-radio :label="'bhv_code'">行为码</el-radio>
                        <el-radio :label="'os_type'">系统类型</el-radio>
                    </el-radio-group>
                    <span style="display:inline-block;float:right;">
                        <el-link type="primary" @click="disDialog = true">查看原数据</el-link>
                    </span>
                </el-row>
                <el-row>
                    <drawpie
                        :chartData="pieData"
                        chartId="send-distribute-chart-id"
                    />
                    <!-- <drawchart
                        :chartData="distributeChartData"
                        chartId="send-distribute-chart-id"
                        data-type="bar"
                    /> -->
                </el-row>
            </el-col>
        </el-row>

        <el-dialog title="趋势数据" :visible.sync="trendDialog" width="70%">
            <p class="h4">今日</p>

            <el-table :data="$lodash.values(trendData['今日'])" height="200px" border>
                <el-table-column property="t" label="时间" width="200"></el-table-column>
                <template v-for="(n, k, i) in quotaConf">
                    <el-table-column :property="k" :label="n" width="150" :key="i"></el-table-column>
                </template>
            </el-table>
            <p class="h4">昨日</p>

            <el-table :data="$lodash.values(trendData['昨日'])" height="200px" border>
                <el-table-column property="t" label="时间" width="200"></el-table-column>
                <template v-for="(n, k, i) in quotaConf">
                    <el-table-column :property="k" :label="n" width="150" :key="i"></el-table-column>
                </template>
            </el-table>

            <p class="h4">最近7日</p>

            <el-table :data="$lodash.values(trendData['最近7日'])" height="200px" border>
                <el-table-column property="t" label="时间" width="200"></el-table-column>
                <template v-for="(n, k, i) in quotaConf">
                    <el-table-column :property="k" :label="n" width="150" :key="i"></el-table-column>
                </template>
            </el-table>

            <p class="h4">天数据</p>

            <el-table :data="$lodash.values(dayTrendData)" height="200px" border>
                <el-table-column property="t" label="时间" width="200"></el-table-column>
                <template v-for="(n, k, i) in quotaConf">
                    <el-table-column :property="k" :label="n" width="150" :key="i"></el-table-column>
                </template>
            </el-table>
        </el-dialog>

        <el-dialog title="发送量分布数据" :visible.sync="disDialog" width="40%">
            <p class="h4">按{{dimType}}分布</p>
            <el-table :data="distributeData" height="400px" border>
                <el-table-column property="name" label="维度名" width="200"></el-table-column>
                <el-table-column property="send_total" label="发送量" width="200"></el-table-column>
            </el-table>
        </el-dialog>
    </el-row>
</template>

<script>
import moment from "moment";

export default {
    data() {
        return {
            objType: "ad_id",
            objVal: "",
            objList: [],
            loading: false,
            dataType: "send_total",
            day: "",
            trendData: {},
            dayDataType: ["send_total"],
            dayTrendData: {},
            quotaConf: {
                send_total: "发送量",
                send_success: "发送成功量",
                send_failure: "发送失败量",
                pv: "曝光量",
                bhv: "互动量",
                back_num: "回传量",
                bhv_ratio: "互动率",
                failure_ratio: "失败率",
                activate_ratio: "激活率"
            },
            trendDialog: false,
            disDialog: false,

            dayRange: [],
            dimType: "sorted_type",
            distributeData: []
        };
    },
    computed: {
        objectName: function() {
            if (this.objType == "customer_id") {
                return "广告主";
            }
            return "广告";
        },
        dayTrendChartData: function() {
            let tmpxAxis = Object.keys(this.dayTrendData);
            let yData = {};
            this.dayDataType.forEach(t => {
                yData[this.quotaConf[t]] = [];
            });
            tmpxAxis.forEach(d => {
                this.dayDataType.forEach(t => {
                    if (this.dayTrendData[d] != undefined) {
                        yData[this.quotaConf[t]].push(this.dayTrendData[d][t]);
                    } else {
                        yData[this.quotaConf[t]].push("");
                    }
                });
            });
            return {
                xAxis: tmpxAxis,
                data: yData
            };
        },
        trendChartData: function() {
            let tmpxAxis = [];
            for (var i = 0; i < 24; i++) {
                if (i < 10) {
                    tmpxAxis.push("0" + i + ":00:00");
                } else {
                    tmpxAxis.push(i + ":00:00");
                }
            }

            let chart1Data = {
                今日: [],
                昨日: [],
                最近7日: []
            };

            ["今日", "昨日", "最近7日"].forEach(d => {
                if (
                    this.trendData[d] == undefined ||
                    this.trendData[d] == null
                ) {
                } else {
                    let dayData = this.trendData[d];
                    tmpxAxis.forEach(h => {
                        if (dayData[h] != undefined) {
                            chart1Data[d].push(dayData[h][this.dataType]);
                        } else {
                            chart1Data[d].push("");
                        }
                    });
                }
            });

            return {
                xAxis: tmpxAxis,
                data: chart1Data
            };
        },
        pieData: function() {
            let chartData = [];
            this.distributeData.forEach(item => {
                chartData.push({
                    name: item.name,
                    value: item.send_total
                });
            });
            return {
                data: chartData
            };
        },
        distributeChartData: function() {
            let tmpxAxis = [];
            let yData = {
                发送量分布: []
            };
            this.distributeData.forEach(item => {
                tmpxAxis.push(item.name);
                yData["发送量分布"].push(item.send_total);
            });
            return {
                xAxis: tmpxAxis,
                data: yData
            };
        }
    },
    mounted() {
        this.day = this.$common.getTodayDate();
        this.dayRange = [
            moment()
                .subtract(7, "days")
                .format("YYYY-MM-DD"),
            this.$common.getTodayDate()
        ];
        this.selectObjType();
    },
    methods: {
        selectObjType: function() {
            if (this.objType == "total") {
                this.objVal = "";
            } else {
                this.getObjectList();
                this.gookLuck();
            }
        },
        inputObjVal(row) {
            this.objVal = row[this.objType];
            this.queryAll();
        },
        queryAll() {
            if (this.objVal.trim() == "" && this.objType != "total") {
                this.$message("请输入" + this.objectName + "id");
                return;
            }
            this.getTrend();
            this.getDayTrend();
            this.getDistribute();
        },
        async gookLuck() {
            let res = await this.$api.insights.randomOcpxObject("app");
            if (res.errcode != 0) {
                return;
            }
            if (this.objType == "ad_id") {
                this.objVal = res.data.ad_id + "";
            } else {
                this.objVal = res.data.customer_id + "";
            }
            this.queryAll();
        },
        async getObjectList() {
            if (this.objType == "total") {
                return;
            }
            this.objList = [];
            let l = this.$loading({
                lock: false,
                text: "加载中",
                spinner: "el-icon-loading",
                background: "rgba(0, 0, 0, 0.7)",
                target: "#app-object-list"
            });
            let res = await this.$api.insights.getOcpxAppList(this.objType);
            l.close();
            if (res.errcode == 0) {
                this.objList = res.data;
            }
        },
        async getTrend() {
            if (this.objVal.trim() == "" && this.objType != "total") {
                this.$message("请输入" + this.objectName + "id");
                return;
            }
            this.loading = true;

            let p = {
                object_type: this.objType,
                object_value: this.objVal,
                date: this.day
            };
            let l = this.$loading({
                lock: false,
                text: "加载中",
                spinner: "el-icon-loading",
                background: "rgba(0, 0, 0, 0.7)",
                target: "#trend-chart"
            });
            let res = await this.$api.insights.getOcpxAppTrend(p);
            l.close();
            if (res.errcode == 0 && res.data != null) {
                this.trendData = res.data;
            } else {
                this.trendData = {};
            }
        },
        trendDayChange() {
            this.getDayTrend();
            this.getTrend()
        },
        async getDayTrend() {
            if (this.objVal.trim() == "" && this.objType != "total") {
                this.$message("请输入" + this.objectName + "id");
                return;
            }

            let fromDay = moment(this.day)
                .subtract(7, "days")
                .format("YYYY-MM-DD");
            let p = {
                object_type: this.objType,
                object_value: this.objVal,
                from_date: fromDay,
                to_date: this.day
            };
            let l = this.$loading({
                lock: false,
                text: "加载中",
                spinner: "el-icon-loading",
                background: "rgba(0, 0, 0, 0.7)",
                target: "#day-trend-chart"
            });
            let res = await this.$api.insights.getOcpxAppDayTrend(p);
            l.close();
            this.loading = false;
            if (res.errcode == 0 && res.data != null) {
                this.dayTrendData = res.data;
            } else {
                this.dayTrendData = {};
            }
        },
        async getDistribute() {
            if (this.objVal.trim() == "" && this.objType != "total") {
                this.$message("请输入" + this.objectName + "id");
                return;
            }
            if (this.dayRange.length < 1) {
                return;
            }

            let p = {
                object_type: this.objType,
                object_value: this.objVal,
                from_date: this.dayRange[0],
                to_date: this.dayRange[1],
                dim_type: this.dimType
            };
            let l = this.$loading({
                lock: false,
                text: "加载中",
                spinner: "el-icon-loading",
                background: "rgba(0, 0, 0, 0.7)",
                target: "#send-distribute-chart"
            });
            let res = await this.$api.insights.getOcpxApSendDistri(p);
            l.close();
            if (res.errcode == 0 && res.data != null) {
                this.distributeData = res.data;
            } else {
                this.distributeData = [];
            }
        }
    }
};
</script>

<style>
.input-with-select .el-input-group__prepend {
    background-color: #fff;
    width: 130px;
}
</style>