<template>
    <div>
        <el-row :gutter="20">
            <el-col :span="12">
                <el-card>
                    <drawchart
                        chartId="tp999ChartId"
                        :chartData="tpChartData"
                        yform="ms"
                        :showToolBox="false"
                    />
                </el-card>
            </el-col>
            <el-col :span="12">
                <el-card>
                    <drawchart
                        chartId="allMsgCount"
                        :chartData="chartData"
                        @clickPiePart="clickPiePart"
                        data-type="bar"
                    />
                </el-card>
            </el-col>
        </el-row>

        <el-card style="margin-top:20px">
            <el-row :gutter="20">
                <el-form :inline="true" class="demo-form-inline" label-width="80px">
                    <el-col :span="8">
                        <el-form-item label="广告ID">
                            <el-input
                                v-model="adId"
                                placeholder="请输入广告ID"
                                @keyup.enter.native="startSearch"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="广告主ID">
                            <el-input
                                v-model="custId"
                                placeholder="请输入广告主ID"
                                @keyup.enter.native="startSearch"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="消息类型">
                            <el-select v-model="actionType" placeholder="活动区域">
                                <el-option value="0" label="全部"></el-option>
                                <template v-for="(item, type) in actionTypeConfig">
                                    <el-option :value="type" :label="item" :key="type"></el-option>
                                </template>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-form>
            </el-row>

            <el-row :gutter="20">
                <el-form :inline="true" class="demo-form-inline" label-width="80px">
                    <el-col :span="8">
                        <el-form-item label="IP">
                            <el-input
                                v-model="ip"
                                placeholder="请输入IP"
                                @keyup.enter.native="startSearch"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="消息ID">
                            <el-input
                                v-model="msgId"
                                placeholder="请输入消息ID"
                                @keyup.enter.native="startSearch"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="2">
                        <el-form-item>
                            <el-button type="primary" @click="startSearch">查询</el-button>
                        </el-form-item>
                    </el-col>
                    <el-col :span="2">
                        <el-form-item>
                            <el-button type="warning" @click="randomGetMsgList">手气不错</el-button>
                        </el-form-item>
                    </el-col>
                </el-form>
            </el-row>

            <el-row>
                <el-col :span="24">
                    <TenmaMsgList :list="list" />
                </el-col>
            </el-row>
            <loading :state="loadState" />
        </el-card>
    </div>
</template>

<script>
import TenmaMsgList from "./TenmaMsgList";
import moment from "moment";
import lodash from "lodash";

export default {
    data() {
        return {
            adId: "",
            day: "",
            list: [],
            loadState: "",
            chartData: {
                data: {},
                title: "",
                xAxis: []
            },
            tpChartData: {
                data: {},
                title: "",
                xAxis: []
            },
            totalMsgCount: 0,
            actionTypeName: "",
            actionType: "0",
            msgId: "",
            custId: "",
            ip: "",
            actionTypeConfig: {},
            pieData: {}
        };
    },
    components: {
        TenmaMsgList
    },
    mounted() {
        this.getTenmaAllMsgCount();
        this.getTenmaMsgTypeConfig();
        this.getTenmaMsgTpStat();
    },
    methods: {
        async getMsgList() {
            let sendParam = {
                day: this.day,
                ad_id: this.adId,
                action_type: this.actionType,
                msg_id: this.msgId,
                ip: this.ip,
                customer_id: this.custId
            };

            let res = await this.$api.insights.getMsgList(sendParam);
            let data = res;

            this.loadState = "";
            // if (data.data === false) {
            //     this.loadState = "";
            //     if (this.list.length == 0) {
            //         this.loadState = "暂时没有数据";
            //     }
            //     return;
            // }

            this.list = this.list.concat(data.data);

            this.reduceDay();

            //this.getMsgList();
        },
        reduceDay: function() {
            this.day = moment(this.day, "YYYYMMDD")
                .subtract("days", 1)
                .startOf("day")
                .format("YYYYMMDD");
        },
        startSearch: function() {
            if (
                lodash.trim(this.adId) == "" &&
                lodash.trim(this.custId) == "" &&
                lodash.trim(this.msgId) == ""
            ) {
                this.$alert("请输入广告id，或者广告主id，或者消息id");
                return;
            }

            this.list = [];

            this.loadState = "loading";

            this.day = moment().format("YYYYMMDD");

            this.getMsgList();
        },
        async getTenmaAllMsgCount() {
            let res = await this.$api.insights.getTenmaAllMsgCount();
            this.chartData = res.data;

            this.totalMsgCount = lodash.sum(lodash.values(this.pieData.data));
        },
        async getTenmaMsgTpStat() {
            let res = await this.$api.insights.getTenmaMsgTpStat();
            this.tpChartData = res.data;
        },
        async getTenmaMsgTypeConfig() {
            let res = await this.$api.insights.getTenmaMsgTypeConfig();
            this.actionTypeConfig = res.data;
        },
        clickPiePart: function(param) {
            this.actionTypeName = param.name;
        },
        async randomGetMsgList() {
            this.list = [];

            this.loadState = "loading";

            let res = await this.$api.insights.getRandomMsgList();
            this.list = res.data;
            this.loadState = "";
        }
    }
};
</script>
