<template>
    <el-row>
        <el-row>
            <drawchart :chartData="chartData" chartId="sentiAnalyChartId" />
        </el-row>
        <el-row>
            <el-table :data="list" height="600px">
                <el-table-column label="用户Id">
                    <template slot-scope="scope">
                        <a :href="'http://weibo.com/u/' + scope.row.sentiment.uid " target="_blank" >{{scope.row.sentiment.uid}}</a>
                    </template>
                </el-table-column>
                <el-table-column label="博文ID">
                    <template slot-scope="scope">
                        <a :href="scope.row.mid_url " target="_blank" >{{scope.row.sentiment.mid}}</a>
                    </template>
                </el-table-column>
                <el-table-column label="内容" prop="sentiment.content" width="600"></el-table-column>
                <el-table-column label="日期" prop="sentiment.date"></el-table-column>
            </el-table>
            <el-pagination
                @current-change="pageChange"
                :current-page.sync="page"
                layout="total, prev, pager, next"
                :page-count="total"
                align="center"
            ></el-pagination>
        </el-row>
    </el-row>
</template>

<script>
export default {
    data() {
        return {
            custId: "",
            total: 0,
            list: [],
            page: 1,
            chartData: {
                title: "",
                data: {},
                xAixs: []
            }
        };
    },
    mounted() {
        this.getSentimentAnly();
        this.getSentimentStat();
    },
    methods: {
        async getSentimentAnly() {
            this.list = [];

            let res = await this.$api.insights.getSentimentAnaly(this.page);

            if (res.errcode == 0) {
                this.list = res.data.list;
                this.total = res.data.total_page;
            } else {
                this.$message("暂无数据");
            }
        },
        pageChange(num) {
            this.page = num;
            this.getSentimentAnly();
        },
        async getSentimentStat() {
            let res = await this.$api.insights.getSentimentStat();
            if (res.errcode != 0) {
                this.chartData = {
                    title: "",
                    data: {},
                    xAixs: []
                };
                return;
            }

            this.chartData = res.data;
        }
    }
};
</script>
