<template>
    <div>
        <div class="row" style="margin-top:20px">
            <div class="col-md-12" style="overflow: scroll;">
                <drawchart chartId="quotaNotice" :chartData="lineData" yform="个" />
            </div>
        </div>

        <el-card>
            <el-form :inline="true" class="demo-form-inline">
                <el-form-item label="维度">
                    <el-select v-model="causeType" @change="switchCauseType">
                        <el-option label="广告" value="1"></el-option>
                        <el-option label="广告系列" value="2"></el-option>
                        <el-option label="账户" value="3"></el-option>
                        <el-option label="余额" value="4"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item :label="causeName+'id'">
                    <el-input v-model="causeId" :placeholder="'请输入'+causeName+'id'"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="toGetListData">查询</el-button>
                </el-form-item>
            </el-form>

            <el-row class="row">
                <el-col :span="24" style="overflow: scroll;">
                    <QuotaNoticeTable
                        :list="list"
                        :page="currentPage1"
                        v-show="loadState != 'loading'"
                        :causeName="causeName"
                        :causeType="causeType"
                    />
                </el-col>
                <loading :state="loadState" />
            </el-row>

            <el-pagination
                align="center"
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage1"
                layout="total, prev, pager, next"
                :page-count="totalPage"
            ></el-pagination>
            <!-- <div
                class="text-center"
                v-html="pager"
                @click="getData($event)"
                v-show="loadState != 'loading'"
            ></div>-->
        </el-card>
    </div>
</template>

<script>
import QuotaNoticeTable from "./QuotaNoticeTable";

export default {
    data() {
        return {
            list: [],
            page: 1,
            causeType: "1",
            causeName: "广告",
            causeId: "",
            pager: "",
            loadState: "",
            causeId: "",
            lineData: {
                title: "",
                data: {},
                xAxis: []
            },
            currentPage1: 1,
            totalPage:0
        };
    },
    mounted() {
        this.getQuotaNoticeList();
        this.getQuotaNoticeLineData();
    },
    components: {
        QuotaNoticeTable
    },
    methods: {
        async getQuotaNoticeList() {
            this.list = [];
            this.pager = "";
            this.loadState = "loading";

            let sendParam = {
                page: this.page,
                cause_type: this.causeType
            };

            let res = await this.$api.insights.getQuotaNoticeList(
                this.currentPage1,
                this.causeType
            );
            if (res.errcode != 0) {
                return;
            }
            this.loadState = "ok";
            this.totalPage=res.data.total;

            this.$set(this, "list", res.data.list);
            this.$set(this, "pager", res.data.footer);
        },
        handleCurrentChange() {
            // this.page = $(e.target).data("page");
            this.getQuotaNoticeList();
        },
        getData: function() {
            this.getQuotaNoticeList();
        },
        switchCauseType: function() {
            if (this.causeType == 1) {
                this.causeName = "广告";
            } else if (this.causeType == 2) {
                this.causeName = "广告系列";
            } else if (this.causeType == 3) {
                this.causeName = "账户";
            } else if (this.causeType == 4) {
                this.causeName = "余额";
            }
            this.page = 1;
            this.getQuotaNoticeLineData();
            this.toGetListData();
        },
        toGetListData: function() {
            if (this.causeId > 0) {
                this.searchQuotaNoticeList();
            } else {
                this.getQuotaNoticeList();
            }
        },
        async searchQuotaNoticeList() {
            this.list = [];
            this.pager = "";
            this.loadState = "loading";

            let res = await this.$api.insights.getQuotaNoticeListById(
                this.currentPage1,
                this.causeType,
                this.causeId
            );
            if (res.errcode != 0) {
                return;
            }
            this.loadState = "ok";
            this.totalPage= res.data.total;
            this.$set(this, "list", res.data);
        },
        async getQuotaNoticeLineData() {
            let res = await this.$api.insights.getQuotaNoticeLineData(
                this.causeType
            );
            if (res.errcode != 0) {
                return;
            }
            console.log(res);
            this.$set(this, "lineData", res.data);
        }
    }
};
</script>
