import Vue from 'vue'
import Router from 'vue-router'

//概览
import Index from '@/Insight/View/Index'
import Ocpx from '@/Insight/Ocpx/Ocpx'

import LeanMatrix from '@/Insight/View/LeanMetrixFrame'

//广告
import AdPlanInsight from '@/Insight/Ad/AdPlanInsight'
import Customer from '@/Insight/Ad/Customer'
