<template>
    <div>
        <el-table :data="list" border style="width: 100%" stripe height="500">
            <el-table-column type="index" label="编号" width="100" :index="indexMethod"></el-table-column>
            <template v-if="causeType == 4">
                <el-table-column prop="quota" label="今日余额" width="140"></el-table-column>
            </template>
            <template v-else>
                <el-table-column prop="quota" label="今日限额" width="140"></el-table-column>
            </template>
            <el-table-column prop="consume" label="今日已消耗"></el-table-column>
            <el-table-column prop="address" label="广告主ID"  width="180">
                <template slot-scope="scope">
                    <router-link
                        :to="{ path: '/insights/customer', query: { ad_id: scope.row.customer_id }}"
                    >{{ scope.row.customer_id }}</router-link>
                </template>
            </el-table-column>
            <el-table-column prop="cause_id" :label="causeName + 'ID'"></el-table-column>
            <el-table-column prop="time" label="统计时间"  width="240"></el-table-column>
        </el-table>

        
    </div>
</template>

<script>
export default {
    props: ["list", "causeName", "causeType", "page"],
    methods: {
        cellClick: function(emitFunc, item, index) {
            this.$emit(emitFunc, item, index);
        },
        indexMethod(idx) {
            return (this.page - 1)  * 20 + idx + 1
        },

    }
};
</script>
