<template>
    <div class="kepler-attention">
        <el-row>
            <el-col :span="24">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <strong>获取用户uid的关注列表</strong>
                    </div>
                    <div>
                        <el-form
                            :inline="true"
                            class="demo-form-inline"
                            style="margin:10px 0 0 20px"
                        >
                            <el-form-item label="广告主ID:">
                                <el-input
                                    v-model="custId"
                                    placeholder="请输入广告主ID"
                                    @keyup.enter.native="search"
                                ></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="getSingleData">查询</el-button>
                            </el-form-item>
                        </el-form>
                        <loading :state="singleLoadState" />
                        <json-viewer
                            v-show="!singleLoadState"
                            :value="singleData"
                            :expand-depth="3"
                            boxed
                            sort
                        ></json-viewer>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="24" style="margin-top:20px">
                <el-card class="box-card">
                    <div slot="header" class="clearfix">
                        <strong>获取多个uid关注关系</strong>
                    </div>
                    <div>
                        <el-form
                            :inline="true"
                            class="demo-form-inline"
                            style="margin:10px 0 0 20px"
                        >
                            <el-form-item label="广告主ID:">
                                <el-input
                                    v-model="uid1"
                                    placeholder="请输入广告主ID"
                                    @keyup.enter.native="search"
                                ></el-input>
                            </el-form-item>
                            <el-form-item label="广告主ID:">
                                <el-input
                                    v-model="uid2"
                                    placeholder="请输入广告主ID"
                                    @keyup.enter.native="search"
                                ></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="getMultipleData">查询</el-button>
                            </el-form-item>
                        </el-form>
                        <json-viewer
                            v-show="!multipleLoadState"
                            :value="multiple"
                            :expand-depth="3"
                            boxed
                            sort
                        ></json-viewer>
                        <loading :state="multipleLoadState" />
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import JsonViewer from "vue-json-viewer";
import * as CONST from "@/constant";

export default {
    components: {
        JsonViewer
    },
    data() {
        return {
            singleData: {},
            multiple: {},
            uid2: "",
            uid1: "",
            custId: "",
            singleLoadState: "暂无数据",
            multipleLoadState: "暂无数据"
        };
    },
    methods: {
        async getSingleData() {
            let res = await this.$api.insights.getSingleData(
                parseInt(this.custId)
            );
            this.singleData = JSON.parse(res);
            this.singleLoadState = "";
        },

        async getMultipleData() {
            let res = await this.$api.insights.getMultipleData(
                parseInt(this.uid1),
                parseInt(this.uid2)
            );
            this.multiple = JSON.parse(res);
            this.multipleLoadState = "";
        }
    }
};
</script>

<style lang="less">
.kepler-attention {
    .search {
        height: 60px;
        line-height: 60px;
    }
    .jv-container .jv-code {
        min-height: 500px;
        overflow: auto;
    }
}
</style>

