<template>
    <el-row>
        <el-row :gutter="30">
            <el-col :span="6">
                <el-card class="box-card">
                    <p style="color:grey; display: flex;">
                        实时预算
                        <span style="font-size:10px; text-align:right; flex: 1">与昨日比：</span>
                        <span
                            :class="stateInfo.budget_incr<0 ? 'detail-decr' : 'detail-incr'"
                        >{{Number(stateInfo.budget_incr * 100).toFixed(2)}}%</span>
                    </p>
                    <p class="detail-budget">
                        <strong class>{{ stateInfo.budget | thousandNum }}</strong>
                    </p>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card class="box-card">
                    <p style="color:grey; display: flex;">
                        实时消耗
                        <span style="font-size:10px; text-align:right; flex: 1">与昨日比：</span>
                        <span
                            :class="stateInfo.consume_incr<0 ? 'detail-decr' : 'detail-incr'"
                        >{{Number(stateInfo.consume_incr * 100).toFixed(2)}}%</span>
                    </p>
                    <p class="detail-budget">
                        <strong class>{{ stateInfo.consume | thousandNum }}</strong>
                    </p>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card class="box-card">
                    <p style="color:grey; display: flex;">
                        广告计划
                        <span style="font-size:10px; text-align:right; flex: 1">有消耗计划占比：</span>
                        <span
                            class="detail-incr"
                        >{{Number(stateInfo.adcnt_v2 / stateInfo.adcnt * 100).toFixed(2)}}%</span>
                    </p>
                    <p class="detail-budget">
                        <el-tooltip class="item" effect="dark" content="在线计划数" placement="top-start">
                            <strong>{{ stateInfo.adcnt | thousandNum}}</strong>
                        </el-tooltip>
                        <span>|</span>
                        <el-tooltip class="item" effect="dark" content="有消耗计划数" placement="top-start">
                            <strong >{{ stateInfo.adcnt_v2 | thousandNum}}</strong>
                        </el-tooltip>
                    </p>
                </el-card>
            </el-col>
            <el-col :span="6">
                <el-card class="box-card">
                    <p style="color:grey; display: flex;">
                        广告主
                        <span style="font-size:10px; text-align:right; flex: 1">有消耗广告主占比：</span>
                        <span
                            class="detail-incr"
                        >{{Number(stateInfo.custcnt_v2 / stateInfo.custcnt * 100).toFixed(2)}}%</span>
                    </p>
                    <p class="detail-budget">
                        <strong>{{ stateInfo.custcnt | thousandNum}}</strong>
                        <span>|</span>
                        <strong>{{ stateInfo.custcnt_v2 | thousandNum}}</strong>
                    </p>
                </el-card>
            </el-col>
        </el-row>
    </el-row>
</template>

<script>
export default {
    data() {
        return {
            stateInfo: {}
        };
    },
    filters: {
        thousandNum: function(val) {
            if (val) {
                return (val + "").replace(/\B(?=(?:\d{3})+\b)/g, ",");
            }
        }
    },
    mounted() {
        this.getGlobalStatInfo();
    },
    methods: {
        async getGlobalStatInfo() {
            let product = "sfsf";
            let res = await this.$api.insights.getGlobalStatInfo(product);
            this.stateInfo = res;
        }
    }
};
</script>
