<template>
    <div >
        <PeopleTop @searchNew="searchNew"></PeopleTop>

        <el-form :inline="true">
            <el-form-item>
                <el-input
                    placeholder="支持输入mid、广告计划id、广告主id自动检索"
                    style="width:400px"
                    v-model="search"
                >
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </el-form-item>
            <el-form-item label="产品线">
                <el-select v-model="filter.ad_type" style="width: 120px;">
                    <el-option value="0" label="所有"></el-option>
                    <template v-for="item in adList">
                        <el-option :label="item.ad_type_name" :value="item.ad_type"></el-option>
                    </template>
                </el-select>
            </el-form-item>
            <el-form-item label="场景">
                <el-select v-model="filter.psid" style="width: 120px;">
                    <el-option value="0" label="所有"></el-option>
                    <template v-for="item in psidList">
                        <el-option :value="item.psid" :label="item.psid_name"></el-option>
                    </template>
                </el-select>
            </el-form-item>
            <el-form-item label="设备">
                <el-select v-model="filter.device" style="width: 120px;">
                    <el-option value="-1" label="所有"></el-option>
                    <template v-for="item in deviceList">
                        <el-option :value="item.device" :label="item.device_name"></el-option>
                    </template>
                </el-select>
            </el-form-item>
        </el-form>
        <loading :state="loadState" />
        <div v-show="resData.length > 0" id="realExposureScroll" style="overflow:scroll">
            <PeopleList
                :showDatas="showDatas"
                :custInfoList="custInfoList"
                :loadCustInfo="loadCustInfo"
                @changeSortColumn="changeSortColumn"
            ></PeopleList>
        </div>
    </div>
</template>
<script>
import moment from "moment";
import lodash from "lodash";
import ScrollBtn from "@/components/Widget/ScrollBtn";

import PeopleList from "@/Insight/People/PeopleList";
import PeopleTop from "@/Insight/People/PeopleTop";

export default {
    data() {
        return {
            uid: "lee东升",
            ad_type: 0,
            startDate: "",
            endDate: "",
            resData: [],
            sortKey: "pv_time",
            sortOrder: true,
            loadState: false,
            search: "",
            filter: {
                psid: "0",
                device: "-1",
                ad_type: "0"
            },
            psidList: [],
            deviceList: [],
            adList: [],
            custInfoList: {},
            loadCustInfo: false,
            // dateRange: [],
            templateId: "realExposureScroll"
        };
    },
    components: { ScrollBtn, PeopleList, PeopleTop },
    computed: {
        showDatas: function() {
            var result = this.resData.filter(function(data) {
                var reg = eval("/(?=" + this.search + ")/");

                let retData =
                    reg.test(data.mid) ||
                    reg.test(data.ad_id) ||
                    reg.test(data.customer_id);

                if (this.filter.ad_type != 0) {
                    retData = retData && data.ad_type == this.filter.ad_type;
                }

                if (this.filter.device != -1) {
                    retData = retData && data.device == this.filter.device;
                }

                if (this.filter.psid != 0) {
                    retData = retData && data.psid == this.filter.psid;
                }

                return retData;
            }, this);

            return this.sortByColumn(result);
        }
    },
    methods: {
        changeSortColumn(column, event) {
            console.log(columnName);
            let columnName = column.property;
            if (columnName == this.sortKey) {
                this.$set(this, "sortOrder", !this.sortOrder);
            } else {
                this.$set(this, "sortKey", columnName);
                this.$set(this, "sortOrder", true);
            }
        },
        keysrt: function(key, desc) {
            return function(a, b) {
                return desc
                    ? a[key] <= b[key]
                        ? 1
                        : -1
                    : a[key] >= b[key]
                    ? 1
                    : -1;
            };
        },
        sortByColumn: function(arr) {
            return arr.sort(this.keysrt(this.sortKey, this.sortOrder));
        },

        async searchNew(uid, dateRange) {
            let begin = dateRange[0] 
            let end = dateRange[1]
            if (uid == "") {
                this.$alert("请输入uid");
                return false;
            }
            this.loadState = "loading";
            this.resData = [];

            let params = {
                uid : uid,
                begin : begin,
                end : end,
                source : 'real'
            }

            let res = await this.$api.insights.getUserFrequency(
               params
            );
            this.loadState = "";
            if (res.errcode != 0) {
                this.loadState = "no-data";
                this.resData = [];
                return;
            }
            this.resData = res.data;
            this.adList = this.$lodash.uniqBy(this.resData, "ad_type");
            this.psidList = this.$lodash.uniqBy(this.resData, "psid");
            this.deviceList = this.$lodash.uniqBy(this.resData, "device");
            this.loadCustInfo = true;
            this.custInfoList = {};
            this.batchGetUserInfo(this.collectProp("customer_id"));
        },
        collectProp: function(prop) {
            let res = [];

            for (var i in this.resData) {
                res.push(this.resData[i][prop]);
            }

            return res;
        },
        initDatePlugin: function() {
            this.dateRange = [
                moment()
                    .subtract(1, "day")
                    .format("YYYY-MM-DD"),
                moment().format("YYYY-MM-DD")
            ];
        },
        openMsg: function(item) {
            if (item.psid == "70106011") {
                this.$alert("微博正文页暂无链接");
                return;
            }

            if (item.msg_url != "") {
                window.open(item.msg_url, "_blank");
            } else {
                this.convertMid(item.mid);
            }
        },
        async convertMid(mid) {
            let res = await this.$api.insights.convertMid(mid);
            if (res.errcode != 0) {
                this.$alert(res.errmsg || "打开链接失败");
                return;
            }
            window.open(res.data.url, "_blank");
        },
        async batchGetUserInfo(uids) {
            let res = await this.$api.insights.batchGetCustomer(uids.join(','));
            this.loadCustInfo = false;
            if (res.errcode != 0) {
                return;
            }

            this.custInfoList = res.data;
        }
    }
};
</script>
