export default function dateFormat(timestamp) {
  if (!timestamp) {
    return '';
  }
  let date = new Date(timestamp);
  let fillTime = t => t < 10 ? `0${t}` : t;
  let Y = date.getFullYear();
  let M = date.getMonth() + 1;
  let D = date.getDate();
  let h = date.getHours();
  let m = date.getMinutes();
  return `${Y}-${fillTime(M)}-${fillTime(D)}`;
}