    mounted() {
        tabInit("JsonIpt");

        this.startTime = `${dateFormat(new Date().getTime())} 00:00:00`;
        this.endTime = `${dateFormat(new Date().getTime())} 23:59:59`;
        this.time = [
            `${dateFormat(new Date().getTime())} 00:00:00`,
            `${dateFormat(new Date().getTime())} 23:59:59`
        ];
    },
    methods: {
        async send() {
            let data = {
                cust_id: parseInt(this.custId),
                start_time: this.time[0],
                end_time: this.time[1],
                targeting: this.value
            };
            let res = await this.$api.insights.getRes(data);
            this.originalData = JSON.parse(res);
        }
    }
};
</script>

