<template>
    <el-row>
        <el-row>
            <el-form :inline="true" class="demo-form-inline">
                <el-col :span="8" style="margin-left:30px">
                    <el-form-item label="类型：">
                        <el-select v-model="valTypeGraph">
                            <el-option
                                v-for="(te, idx) in abcType"
                                :value="idx"
                                :key="idx"
                                :label="te"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="日期：">
                        <el-date-picker
                            v-model="timeGraph"
                            value-format="yyyy-MM-dd"
                            placeholder="选择日期"
                        ></el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="typeBudget">查 询</el-button>
                </el-col>
            </el-form>
        </el-row>
        <div style="text-align:center;">
            <span>数据展现形式 ：</span>
            <el-radio v-model="graphType" label="percent">率值</el-radio>
            <el-radio v-model="graphType" label="int">数值</el-radio>
        </div>
        <!-- <div class="content-item-trdcontent" style="margin:20px"> -->
        <div style="width:100%">
            <drawchart
                style="width:85%;margin:0 auto"
                chartId="budgetConsumeGrapg"
                :chartData="intData"
                :showToolBox="false"
                v-if="graphType=='percent'"
                yform="%"
                lengendPosition="top"
                percentItem="1"
            />
            <drawchart
                style="width:85%;margin:0 auto"
                chartId="budgetConsumeGrapg"
                :chartData="lineData"
                :showToolBox="false"
                v-else-if="graphType=='int'"
                lengendPosition="top"
            />
        </div>
    </el-row>
</template>

<script>
import moment from "moment";

export default {
    data() {
        return {
            valTypeGraph: "ocpx",
            abcType: {
                total: "全部",
                price_low: "出价低于7块",
                ocpx: "OCPX-2",
                target: "人群包或粉丝定向",
                intent: "意图预算",
                growfans: "涨粉",
                rest: "其他"
            },
            timeGraph: "",
            graphType: "int",
            intData: {
                data: {
                    消耗占预算比例: []
                },
                title: ["消耗占预算比例"],
                xAxis: []
            },
            lineData: {
                data: {
                    预算: [],
                    消耗: [],
                    未消耗: []
                },
                title: ["预算", "消耗", "未消耗"],
                xAxis: []
            }
        };
    },
    mounted() {
        this.timeGraph = moment().format("YYYY-MM-DD");
        this.typeBudget();
    },
    methods: {
        async typeBudget() {
            // var myChart = echarts.init(document.getElementById(this.chartId), 'shine');
            this.lineData.xAxis = [];
            this.intData.xAxis = [];
            this.lineData.data.预算 = [];
            this.lineData.data.消耗 = [];
            this.lineData.data.未消耗 = [];
            this.intData.data.消耗占预算比例 = [];
            let res = await this.$api.insights.getCustomersBudgetConsume({
                type: this.valTypeGraph,
                date: this.timeGraph
            });
            if (res.errcode == 0 && res.data) {
                let data = res.data;
                let len = data.length;
                for (let i = 0; i < data.length - 1; i++) {
                    // '发送总数','发送成功总数','用户信息匹配失败数','发送失败总数','回传总数'
                    this.lineData.xAxis.push(data[i].CreateTime);
                    this.intData.xAxis.push(data[i].CreateTime);
                    this.lineData.data.预算.push(data[i].budget);
                    this.lineData.data.消耗.push(data[i].consume);
                    this.lineData.data.未消耗.push(
                        data[i].unconsume.toFixed(2)
                    );
                    this.intData.data.消耗占预算比例.push(
                        data[i].ratio.toFixed(4)
                    );
                }
            }
        }
    }
};
</script>
