<template>
    <div>
        <div class="text-center">
            <b>选择时间段：</b>
            <el-select v-model="days" @change="getAdConsumeList" size="mini">
                <el-option value="7">最近7天</el-option>
                <el-option value="15">最近15天</el-option>
                <el-option value="30">最近30天</el-option>
            </el-select>
        </div>
        <div align="center" >
        <drawchart chartId="consumeChartId" :chartData="consumeChartData" style="width:90%" /></div>
    </div>
</template>

<script>
import lodash from "lodash";
export default {
    props: {
        adId: ""
    },
    data() {
        return {
            consumeChartData: {
                title: "",
                data: {},
                xAxis: []
            },
            days: 7
        };
    },
    methods: {
        async getAdConsumeList() {
            this.consumeChartData.data = {};

            let res = await this.$api.insights.getAdConsumeList(
                this.adId,
                this.days
            );
            var data = res;
            if (data.errcode != 0) {
                this.consumeChartData.title = "暂时获取不到广告消耗信息";
                this.consumeChartData.xAxis = [];
                this.consumeChartData.data = {};
                return;
            }
            this.consumeChartData.title = "最近" + this.days + "消耗";
            this.consumeChartData.xAxis = Object.keys(data.data);
            this.consumeChartData.data["消耗"] = lodash.values(data.data);
        }
    }
};
</script>
