<template>
    <div>
        <el-table :data="list" stripe style="width: 100%">
            <el-table-column prop="ad_update_log.req_time" label="变更时间"></el-table-column>
            <el-table-column prop="action_text" label="变更类型"></el-table-column>
            <el-table-column prop="content.from" label="来源"></el-table-column>
            <el-table-column label="详情">
                <template slot-scope="scope">
                    <a href="javascript:;" @click="showDetail(scope.row)">详情</a>
                </template>
            </el-table-column>
        </el-table>
        <el-row style="margin-top:10px">
        <el-pagination
            @current-change="getAdPlanUpdateList"
            :current-page.sync="page"
            layout="total, prev, pager, next"
            :page-count="totalPage"
            :hide-on-single-page="true"
            align="center"
        ></el-pagination>
        </el-row>

        <el-dialog title="变更详情" :visible.sync="adUpdateDetail" width="80%">
            <ShowJson :showStr="detail"></ShowJson>
        </el-dialog>
    </div>
</template>

<script>
export default {
    props: {
        adId: ""
    },
    data() {
        return {
            paginator: "",
            logId: 0,
            list: [],
            detail: "",
            page: 1,
            totalPage: 0,
            adUpdateDetail: false
        };
    },

    methods: {
        initAdPlanUpdateList() {
            this.page = 1;
            this.getAdPlanUpdateList()
        },
        async getAdPlanUpdateList() {
            let res = await this.$api.insights.getAdPlanUpdateList(
                this.adId,
                this.page
            );
            this.totalPage = res.data.total_page;
            this.list = res.data.list;
        },
        showDetail: function(logInfo) {
            this.detail = JSON.stringify(logInfo.content);
            this.adUpdateDetail = true;
        }
    }
};
</script>
