<template>
    <div>
        <el-card style="margin-top:20px">
            <el-row :gutter="20">
                <el-form :inline="true" class="demo-form-inline" label-width="80px">
                    <el-col :span="8">
                        <el-form-item label="广告ID">
                            <el-input
                                v-model="adId"
                                placeholder="请输入广告ID"
                                @keyup.enter.native="getTenmaErrorMsgList"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="广告主ID">
                            <el-input
                                v-model="custId"
                                placeholder="请输入广告主ID"
                                @keyup.enter.native="getTenmaErrorMsgList"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="错误类型">
                            <el-radio-group v-model="errType">
                                <el-radio label="system">系统错误</el-radio>
                                <el-radio label="service">业务错误</el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-col>
                </el-form>
            </el-row>

            <el-row :gutter="20">
                <el-form :inline="true" class="demo-form-inline" label-width="80px">
                    <el-col :span="8">
                        <el-form-item label="消息类型">
                            <el-select v-model="actionType" placeholder="活动区域">
                                <el-option value="0" label="全部"></el-option>
                                <template v-for="(item, type) in actionTypeConfig">
                                    <el-option :value="type" :label="item" :key="type"></el-option>
                                </template>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="IP">
                            <el-input
                                v-model="ip"
                                placeholder="请输入IP"
                                @keyup.enter.native="getTenmaErrorMsgList"
                            ></el-input>
                        </el-form-item>
                    </el-col>

                    <el-col :span="2">
                        <el-form-item>
                            <el-button type="primary" @click="getTenmaErrorMsgList">查询</el-button>
                        </el-form-item>
                    </el-col>
                </el-form>
            </el-row>

            <loading :state="loadState" />

            <el-row>
                 <div class="panel panel-default hide-border" v-show="list.length >0 && loadState == ''">
            <div class="panel-body">
                <div>
                    <table
                        class="table table-bordered table-hover table-responsive"
                        id="fansListId"
                    >
                        <thead>
                            <tr class="info">
                                <td>请求时间</td>
                                <td>消息类型</td>
                                <td>广告ID</td>
                                <td>广告主ID</td>
                                <td>来自</td>
                                <td>去向</td>
                                <td>IP</td>
                                <td>请求内容</td>
                                <td>总耗时(ms)</td>
                                <td>模块耗时(ms)</td>
                                <td>错误码</td>
                                <td>错误信息</td>
                                <td>系统内部错误</td>
                            </tr>
                        </thead>
                        <tbody>
                            <template v-for="log in list">
                                <tr>
                                    <td>{{log.request_time}}</td>
                                    <td>{{log.action_text}}</td>
                                    <td>
                                        <adlink :adId="log.ad_id" />
                                    </td>
                                    <td>
                                        <template v-if="log.customer_id > 0">
                                            <router-link
                                                :to="{ path: '/insights/customer', query: { customer_id: log.customer_id }}"
                                            >{{ log.customer_id }}</router-link>
                                        </template>
                                        <template v-else>无</template>
                                    </td>
                                    <td>{{log.from}}</td>
                                    <td>{{log.destination}}</td>
                                    <td>{{log.ip}}</td>
                                    <td>
                                        <a
                                            href="javascript:;"
                                            @click="showReqContent(log.request_content)"
                                        >详情</a>
                                    </td>
                                    <td>{{log.cost}}</td>
                                    <td>{{log.inner_cost}}</td>
                                    <td>{{log.errcode}}</td>
                                    <td class="red-font">{{log.errmsg}}</td>
                                    <td>
                                        <template v-if="log.inner_errmsg">
                                            <a
                                                href="javascript:;"
                                                @mouseover="showErrMsg(log, $event)"
                                            >{{log.inner_errcode}}</a>
                                        </template>
                                        <template v-else>{{log.inner_errcode}}</template>
                                    </td>
                                </tr>
                            </template>
                        </tbody>
                    </table>
                </div>
            </div>
            <div>
                <div class="text-center" v-html="paginator" @click="getData($event)"></div>
            </div>
        </div>

        <div
            class="modal fade"
            tabindex="-1"
            role="dialog"
            aria-labelledby="myLargeModalLabel"
            id="reqContentId"
        >
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">请求内容</h4>
                    </div>
                    <div class="modal-body" style="max-height: 600px;overflow: scroll;">
                        <ShowJson :showStr="requestContent"></ShowJson>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
                    </div>
                </div>
            </div>
        </div>
            </el-row>
        </el-card>
    </div>
</template>

<script>
export default {
    props: ["outActionType"],
    data() {
        return {
            paginator: "",
            list: [],
            loadState: "",
            page: 1,
            adId: "",
            custId: "",
            requestContent: "{}",
            actionTypeConfig: {},
            actionType: "0",
            ip: "",
            errType: "system"
        };
    },
    watch: {
        outActionType: function(val, oldVal) {
            this.actionType = val;
            this.changeActionType();
        }
    },
    mounted() {
        this.getTenmaErrorMsgList();
        this.getTenmaActionTypeConfig();
    },
    methods: {
        async getTenmaErrorMsgList() {
            this.list = [];

            this.loadState = "loading";

            let send = {
                page: this.page,
                ad_id: this.adId,
                action_type: this.actionType,
                ip: this.ip,
                customer_id: this.custId,
                err_type: this.errType
            };

            let res = await this.$api.insights.getTenmaErrorMsgList(send);
            var data = res;

            this.loadState = "";

            if (data.errcode != 0) {
                //this.$bootbox.alert("暂时获取不到错误列表信息");
                return;
            }

            this.paginator = data.data.footer;

            this.list = data.data.list;

            if (this.list.length == 0) {
                this.loadState = "no-data";
            }
        },
        getData: function(e) {
            this.page = $(e.target).data("page");

            this.getTenmaErrorMsgList();
        },
        showReqContent: function(str) {
            this.requestContent = str;
            $("#reqContentId").modal("show");
        },
        async getTenmaActionTypeConfig() {
            let res = await this.$api.insights.getTenmaActionTypeConfig();
            this.actionTypeConfig = res.data;
        },
        changeActionType: function() {
            this.page = 1;
            this.getTenmaErrorMsgList();
        },
        showErrMsg: function(data, event) {
            $(event.currentTarget)
                .tooltip({
                    placement: "top",
                    html: true,
                    title: data.inner_errmsg,
                    container: "body"
                })
                .tooltip("toggle");
        }
    }
};
</script>
