<template>
    <div>
        <el-row style="padding-top:20px">
            <el-form :inline="true" label-width="80px">
                <el-col :span="8">
                    <el-form-item label="计划ID">
                        <el-input v-model="adId" placeholder="默认空为整体"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item :label="(isExceptPsid ? '排' : '' )+'场景流'">
                        <el-select
                            v-model="psids"
                            multiple
                            clearable
                            placeholder="选择场景"
                            style="width:240px"
                        >
                            <template v-for="item in sceneFlow">
                                <template v-if="item.value > 0">
                                    <el-option
                                        :key="item.value"
                                        :value="item.value"
                                        :label="item.name"
                                        v-if="psids.indexOf(item.value) > -1"
                                    ></el-option>
                                    <el-option
                                        :value="item.value"
                                        :key="item.value"
                                        :label="item.name"
                                        v-else
                                    ></el-option>
                                </template>
                            </template>
                        </el-select>
                        <el-checkbox v-model="isExceptPsid">排除</el-checkbox>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="日期">
                        <el-date-picker
                            v-model="currentDate"
                            value-format="yyyy-MM-dd"
                            type="date"
                            :clearable="false"
                            placeholder="选择日期"
                            style="width:180px"
                        ></el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="时间">
                        <el-time-picker
                            style="width:200px"
                            is-range
                            v-model="time"
                            value-format="HH:mm"
                            format="HH:mm"
                            range-separator="~"
                            start-placeholder="开始时间"
                            end-placeholder="结束时间"
                            placeholder="选择时间范围"
                        ></el-time-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="8" align="left">
                    <el-form-item>
                        <el-button :disabled="isLoading" type="primary" @click="getMatrixData">
                            <span v-if="isLoading">正在加载数据</span>
                            <span v-else>查 询</span>
                        </el-button>
                        <el-button :disabled="isLoading" type="warning" @click="goodLuck">手气不错</el-button>
                        <el-button @click="showJsonData" type="success">查看json串</el-button>
                    </el-form-item>
                </el-col>
            </el-form>
        </el-row>

        <div>
            <h4>计划概览</h4>
            <el-divider></el-divider>
        </div>
        <div style="width: 100%;" class="record">
            <AdPlanStateTimeline :adId="adId" ref="adTimeline" />
        </div>

        <el-row id="ad-plan-base-id">
            <AdPlanBase :adPlan="adPlan" :customer="customer" :activeData="activeData" />
        </el-row>

        <div style="padding:40px 0 20px 0">
            <h4>精益矩阵</h4>
            <el-divider></el-divider>
        </div>
        <div class="matrix-layout">
            <el-row>
                <el-col align="center">
                    <el-button @click="showMatrixBy('psid')" type="primary" size="mini">分场景矩阵图</el-button>
                    <el-button @click="showMatrixBy('hour')" type="success" size="mini">分小时矩阵图</el-button>
                </el-col>
            </el-row>

            <LeanMatrix :theFunnelData="funnelData" @showMatrixFilter="showMatrixFilter"></LeanMatrix>
        </div>

        <AdPlanExtra :adPlan="adPlan" :customer="customer" :activeData="activeData" />

        <div style="padding:40px 0 20px 0">
            <h4>计划变更记录</h4>
            <el-divider></el-divider>
        </div>
        <AdUpdateRecord :adId="adId" ref="AdUpdateRecord" />

        <div style="padding:40px 0 20px 0">
            <h4>广告消耗曲线</h4>
            <el-divider></el-divider>
        </div>

        <AdConsume :adId="adId" ref="AdConsume" />

        <el-dialog title="详情" :visible.sync="matrixPopId" width="75%">
            <div align="center" style="padding:0 40px 0 0px">
                <section
                    v-for="(psMartix, psIdx) in totalPsData"
                    :key="psIdx"
                    style="padding-top:50px"
                >
                    <div>
                        <h4>{{ psMartix.name }}</h4>
                        <LeanMatrix :theFunnelData="psMartix" wikiNotShow="true"></LeanMatrix>
                    </div>
                </section>
            </div>
        </el-dialog>

        <el-dialog title="过滤详情" :visible.sync="matrixFilter" size="50%">
            <div class="text-center" style="padding:20px 20px">
                <div class="matrix-layout-item-tooltips">
                    <div class="item">
                        <p class="remain">剩余数:</p>
                        <span>{{modalData.cnt}}</span>
                    </div>
                    <div class="item">
                        <p class="filter">过滤数:</p>
                        <span>{{modalData.filter_cnt}}</span>
                    </div>
                    <div class="item">
                        <p class="filter-rate">过滤占比:</p>
                        <span>{{Number(modalData.filter_ratio * 100).toFixed(2)}}%</span>
                    </div>
                </div>
                <drawchart
                    chartId="matrixFilterId"
                    :chartData="strategyFilterData"
                    :showToolBox="false"
                />
            </div>
        </el-dialog>

        <el-drawer title="计划矩阵json数据" :visible.sync="adJson" direction="rtl" size="60%">
            <div style="width:100%;height:650px;overflow-y:scroll">
                <ShowJson v-if="adPlan" :showStr="JSON.stringify(adPlan)"></ShowJson>
            </div>
        </el-drawer>
    </div>
</template>

<script>
import sceneFlow from "@/config/SceneFlow.js";
import moment from "moment";
import lodash from "lodash";
import AdPlanStateTimeline from "@/Insight/View/AdPlanStateTimeline";
import AdUpdateRecord from "@/Insight/Ad/AdUpdateRecord";
import AdConsume from "@/Insight/Ad/AdConsume";

import LeanMatrix from "@/Insight/View/LeanMatrix";
import AdPlanBase from "@/Insight/Ad/AdPlanBase";
import AdPlanExtra from "@/Insight/Ad/AdPlanExtra";
export default {
    components: {
        AdPlanStateTimeline,
        AdUpdateRecord,
        AdConsume,
        LeanMatrix,
        AdPlanBase,
        AdPlanExtra
    },
    data() {
        return {
            /* 当前日期 */
            currentDate: "",
            time: [],
            adId: "",
            /* 矩阵数据 */
            funnelData: [],
            isLoading: false,
            activeData: {},

            psids: [],
            sceneFlow: sceneFlow,
            modalData: {},
            totalPsData: [],
            showHistoryBar: false,

            // 广告变更记录
            strategyFilterData: {
                data: {},
                title: "",
                xAxis: []
            },
            isExceptPsid: false,
            matrixPopId: false,
            matrixFilter: false,
            adJson: false,
            adPlan: {},
            customer: {}
        };
    },
    mounted() {
        this.currentDate = moment().format("YYYY-MM-DD");
        this.time = ["00:00", moment().format("HH:mm")];
        if (this.$route.query.ad_id > 0) {
            this.adId = this.$route.query.ad_id;
            this.getMatrixData();
        } else {
            this.goodLuck();
        }
    },
    methods: {
        // 矩阵群
        async matrixGroup(matrixType) {
            this.totalPsData = [];
            let data = {
                product: "sfst",
                adid: Number(this.adId),
                start_time: `${this.currentDate} ${this.time[0]}:00`,
                end_time: `${this.currentDate} ${this.time[1]}:00`
            };

            let url = "/space/lean_matrix/api_matrix2_psids";
            if (matrixType != undefined && matrixType == "hour") {
                url = "/space/lean_matrix/api_matrix2_ph";
            }
            let res = await this.$api.insights.getMatrixGroup(url, data);
            let groupData = res;
            if (groupData.errcode === 0) {
                let arr = [];
                groupData.data.forEach(item => {
                    for (const key in item) {
                        if (item.hasOwnProperty(key)) {
                            const oneData = item[key];
                            let onePsData = oneData;
                            onePsData.name = key;
                            arr.push(onePsData);
                        }
                    }
                });
                this.totalPsData = arr;
            }
        },
        showMatrixBy(matrixType) {
            this.matrixPopId = true;
            this.matrixGroup(matrixType);
        },
        showMatrixFilter(modalData) {
            this.modalData = modalData;
            this.matrixFilter = true;
            this.getHistoryData(this.adId, modalData);
        },
        async getHistoryData(adId, item) {
            this.showHistoryBar = true;
            if (!this.adId) {
                return;
            }
            let data = {
                product: "sfst",
                adid: Number(adId),
                code: item.code
            };
            data.psid = this.psid;

            let res = await this.$api.insights.getHistoryData(data);
            let hData = res;
            if (hData) {
                this.drawHistoryBar(item, hData.data);
            }
        },
        drawHistoryBar(item, data) {
            this.strategyFilterData = {
                title: item.remark,
                xAxis: lodash.map(data, "date"),
                data: {
                    数量: lodash.map(data, "cnt")
                }
            };
        },
        showJsonData() {
            this.adJson = true;
        },
        // 随机获取一个AdId
        async goodLuck() {
            let res = await this.$api.insights.getRandomSelectAdPlan();
            this.adId = res.data.ad_id;
            this.getMatrixData();
        },
        async getAdInfoData(adId) {
            let l = this.$loading({
                lock: false,
                text: "加载中",
                spinner: "el-icon-loading",
                background: "rgba(0, 0, 0, 0.7)",
                target: "#ad-plan-base-id"
            });
            let res = await this.$api.insights.getAdPlanInfoByAdid(adId);
            l.close()
            this.adPlan = res.data;
            if (
                this.adPlan.targeting_data.social_targeting.self_fans_target ==
                0
            ) {
                this.adPlan.targeting_data.social_targeting.fans_target_text =
                    "否";
            } else if (
                this.adPlan.targeting_data.social_targeting.self_fans_target ==
                1
            ) {
                this.adPlan.targeting_data.social_targeting.fans_target_text =
                    "不限";
            } else if (
                this.adPlan.targeting_data.social_targeting.self_fans_target ==
                2
            ) {
                this.adPlan.targeting_data.social_targeting.fans_target_text =
                    "是";
            }
            this.isLoading = false;
            this.getCustomer(this.adPlan.customer_id + "");
        },
        async getCustomer(custId) {
            let res = await this.$api.insights.getCustomer(custId);
            let tag = await this.$api.insights.getCustomerFinance(custId);
            if (res.data == null) {
                res.data = {
                    name: ""
                };
            }
            this.customer = this.$lodash.merge(res.data, tag.data);
        },
        // 获取计划分时基础数据
        async getAdActiveInfo(val) {
            let data = {
                product: "sfst",
                adid: Number(val),
                start_time: `${this.currentDate} ${this.time[0]}`,
                end_time: `${this.currentDate} ${this.time[1]}`
            };
            let res = await this.$api.insights.getAdInfo(data);

            if (Object.keys(res).length > 0) {
                this.activeData = res;
            } else {
                this.activeData = {};
            }
        },
        async getMatrixData() {
            // 加载整体数据
            this.funnelData = [];
            this.isLoading = true; //数据正在加载，控制加载图
            this.getAdInfoData(this.adId);
            this.getAdActiveInfo(this.adId);

            setTimeout(() => {
                this.$refs.adTimeline.getAdPlanStatTimeline();
                this.$refs.AdUpdateRecord.initAdPlanUpdateList(1);
                this.$refs.AdConsume.getAdConsumeList();
            }, 0);
            let data = {
                product: "sfst",
                start_time: `${this.currentDate} ${this.time[0]}:00`,
                end_time: `${this.currentDate} ${this.time[1]}:00`,
                adid: Number(this.adId),
                is_except_psid: this.isExceptPsid,
                psids: this.psids
            };
            let res = await this.$api.insights.getMatrixData(data);
            let mxData = res;
            if (mxData.data) {
                let totalCount = mxData.data[0].cnt;
                this.funnelData = mxData.data;
            }
        }
    }
};
</script>
