<script>
import debounce from "@/components/Widget/debounce";
import lodash from "lodash";

const PAGE_SIZE = 10;

export default {
    components: { debounce },
    data() {
        return {
            customerId: "",
            tableShow: false,
            tableData: {},
            isShowOriginData: false,
            foldData: {
                customer: false,
                customer_consume: false,
                customer_balance: false,
                customer_ad_plan: false,
                top10_ad_plan: false
            },
            cityLatlngList: {},
            customerConsumeList: [],
            customerBalance: {
                jc_balance: 0,
                st_balance: 0,
                today_consume: "0",
                bp_balance: 0,
                gap: 0
            },
            adPlanList: [],
            globalState: "",
            loadCustPlanState: "",
            customerConsumePage: 1,
            customerTotalPage: 0,
            showCustConsumeList: [],
            adPlanPaginator: "",
            top10AdPlanList: [],
            pageIndex: 1,
            showJson: false,
            total: 0,
            adPlanListPage : 1,
        };
    },
    mounted() {
        if (this.$route.query.customer_id > 0) {
            this.customerId = this.$route.query.customer_id;
            this.search();
        } else {
            this.randomSelectCustomer();
        }
    },
    methods: {
        search: function() {
            var reg = /^[0-9]+.?[0-9]*$/;
            var that = this;
            if (this.customerId == "") {
                this.$message("请输入广告id");
                return false;
            }
            if (!reg.test(this.customerId)) {
                this.$message("广告id不能含有非数字字母");
                return false;
            }

            this.tableShow = false;
            this.getCustomerBaseInfo();
            this.getCustomerConsumeList();
            this.getCustomerBalanceList();
            this.getCustomerAdPlanList(1);
            this.getTop10AdPlanList();
        },
        indexMethod(idx) {
            return (this.adPlanListPage - 1)  * 20 + idx + 1
        },
        async getCustomerBaseInfo() {
            this.tableData = {
                id : this.customerId
            }
            let res = await this.$api.insights.getCustomer(this.customerId);
            // console.log(res);
            if (res.errcode != 0) {
                this.$set(this, "tableShow", false);
                this.$message(data.errmsg);
                return;
            }
            if(res.data != null) {
                 this.$set(this, "tableData", res.data);
                 this.$set(this, "tableShow", true);
            } else {
                this.$set(this, "tableShow", true);
            }
            
        },
        async randomSelectCustomer() {
            this.tableShow = false;

            let res = await this.$api.insights.getRandomSelectAdPlan();
            this.$set(this, "customerId", res.data.customer_id);
            this.search();
        },
        switchData: function() {
            if (this.isShowOriginData) {
                this.isShowOriginData = false;
                //this.drawBar();
            } else {
                this.isShowOriginData = true;
            }
        },
        async getCustomerConsumeList() {
            this.customerConsumeList = [];

            this.showCustConsumeList = [];

            this.customerConsumePage = 1;

            let res = await this.$api.insights.getCustomerConsumeList(
                this.customerId
            );

            this.customerConsumeList = res.data;

            this.customerTotalPage =
                parseInt(this.customerConsumeList.length / PAGE_SIZE) + 1;

            this.setShowCustConsumeList();
        },
        setShowCustConsumeList: function() {
            let start = (this.customerConsumePage - 1) * PAGE_SIZE;

            let end =
                start + PAGE_SIZE >= this.customerConsumeList.length
                    ? this.customerConsumeList.length
                    : start + PAGE_SIZE;

            this.showCustConsumeList = this.customerConsumeList.slice(
                start,
                end
            );
        },
        getCustConsumeListByPage: function(page) {
            if (page == "next") {
                this.customerConsumePage =
                    this.customerConsumePage == this.customerTotalPage
                        ? 1
                        : this.customerConsumePage + 1;
            } else if (page == "prev") {
                this.customerConsumePage =
                    this.customerConsumePage == 1
                        ? 1
                        : this.customerConsumePage;
            } else {
                this.customerConsumePage = page;
            }

            this.setShowCustConsumeList();
        },
        async getCustomerBalanceList() {
            this.customerBalance.jc_balance = 0;
            this.customerBalance.bp_balance = 0;
            this.customerBalance.st_balance = 0;
            this.customerBalance.today_consume = 0;
            this.customerBalance.gap = 0;

            let res = await this.$api.insights.getCustomerBalanceList(
                this.customerId
            );
            if (res.errcode != 0) {
                //this.$bootbox.alert("暂时获取不到广告主余额信息");
                return;
            }
            this.customerBalance = res.data;
        },
        async getCustomerAdPlanList(page) {
            this.loadCustPlanState = "loading";

            this.adPlanList = [];

            let res = await this.$api.insights.getCustomerAdPlanList(
                this.customerId,
                page
            );
            this.loadCustPlanState = "";
            if (res.errcode != 0) {
                this.$message("暂时获取不到广告主计划信息");
                return;
            }
            this.total = res.data.total;
            this.adPlanList = res.data.list;
            this.adPlanPaginator = res.data.footer;
        },
        changePage(page) {
            // let page = $(e.target).data("page");
            this.adPlanListPage = page;
            this.getCustomerAdPlanList(page);
        },
        async getTop10AdPlanList() {
            this.top10AdPlanList = [];

            let res = await this.$api.insights.getTop10AdPlanList(
                this.customerId
            );
            if (res.errcode != 0) {
                this.$$message("暂时没有top10广告计划列表");
                return;
            }

            this.top10AdPlanList = res.data;
        }
    }
};
</script>
