<template>
    <div>
        <el-row :gutter="20">
            <el-col :span="12">
                <el-card class="box-card" style="height:630px">
                    <div slot="header" class="clearfix">
                        <strong>各类错误消息统计</strong>
                    </div>
                    <div>
                        <el-col :span="12" class="center-block metric-panel">
                            <p>消息总数</p>
                            <p class="metric-value h2" style="color: rgb(51,122,183)">
                                <strong>{{recvSumCount}}</strong>
                            </p>
                        </el-col>
                        <el-col :span="12" class="center-block metric-panel">
                            <p>错误消息总数</p>
                            <p class="metric-value h2" style="color: rgb(51,122,183)">
                                <strong>{{errMsgSumCount}}</strong>
                            </p>
                        </el-col>
                        <el-col :span="24">
                            <drawchart
                                chartId="msgCount"
                                :chartData="chartData"
                                @clickPiePart="clickPiePart"
                                data-type="bar"
                            />
                        </el-col>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="12">
                <TenmaSingleMsgStat :actionCode="actionType"></TenmaSingleMsgStat>
            </el-col>
            <el-col :span="24">
                <TenmaErrorMsgList :outActionType="actionType"></TenmaErrorMsgList>
            </el-col>
        </el-row>

       
    </div>
</template>

<script>
import TenmaErrorMsgList from "./TenmaErrorMsgList";
import TenmaSingleMsgStat from "./TenmaSingleMsgStat";
import lodash from "lodash";

export default {
    data() {
        return {
            actionType: 0,
            chartData: {
                data: {},
                title: "",
                xAxis: []
            },
            errMsgSumCount: 0,
            actionTypeConfig: {},
            recvSumCount: 0
        };
    },
    components: {
        TenmaErrorMsgList,
        TenmaSingleMsgStat
    },
    mounted() {
        this.getTenmaErrorMsgCount();
    },
    methods: {
        async getTenmaErrorMsgCount() {
            let res = await this.$api.insights.getTenmaErrorMsgCount();
            var data = res;

            this.chartData = data.data.chart_data;

            this.actionTypeConfig = data.data.action_type_config;

            this.actionType = data.data.max_error_action_type;
            this.errMsgSumCount = lodash.sum(
                this.chartData.data[Object.keys(this.chartData.data)[0]]
            );
            this.recvSumCount = data.data.all_recv_count;
        },
        clickPiePart: function(param) {
            this.actionType = this.actionTypeConfig[param.name];
        }
    }
};
</script>
