<template>
    <el-table stripe :data="list" height="500" border>
        <el-table-column prop="server_type" label="进程"></el-table-column>
        <el-table-column prop="property_type" label="模块"></el-table-column>
        <el-table-column prop="black_type" label="黑名单">
            <template slot-scope="scope">{{scope.row.black_type ? '是' : '否'}}</template>
        </el-table-column>
         <el-table-column prop="create_time" label="创建时间">
        </el-table-column>
        <el-table-column prop="expire_time" label="有效期">
            <template slot-scope="scope">{{scope.row.expire_time }}天</template>
        </el-table-column>
        <el-table-column prop="expire_at" label="过期时间">
        </el-table-column>
        <el-table-column prop="pandora_list" label="Uid名单"></el-table-column>
         <el-table-column prop="extend" label="扩展字段"></el-table-column>
    </el-table>
</template>

<script>
import moment from "moment"
export default {
    data() {
        return {
            list: []
        };
    },
    mounted() {
        this.getPandoraItems();
    },
    methods: {
        async getPandoraItems() {
            let res = await this.$api.insights.getPandoraItemList({
                server_type: "fisher"
            });
            res.data.forEach((item, i) => {
                 res.data[i].expire_at = moment(item.create_time).add(item.expire_time, 'days').format('YYYY-MM-DD HH:mm');
            });
            res.data = this.$lodash.sortBy(res.data, ['create_time']).reverse()
            this.list = res.data;
        }
    }
};
</script>
