<template>
    <el-row>
        <el-row>
            <el-form :inline="true" class="demo-form-inline">
                <el-col :span="8">
                    <el-form-item label="用户白名单UID">
                        <el-select v-model="uid" placeholder="请选择" filterable>
                            <el-option
                                v-for="( name, id) in users"
                                :key="id"
                                :label="id + ' ' + name"
                                :value="id"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>

                <el-col :span="8">
                    <el-form-item label="日期">
                        <el-date-picker v-model="date" type="date" placeholder="选择日期" :value-format="'yyyy-MM-dd'"></el-date-picker>
                    </el-form-item>
                </el-col>

                <el-form-item>
                    <el-button type="primary" @click="search">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="info" @click="showDebugList = true">潘多拉</el-button>
                </el-form-item>
                <el-form-item>
                    <a
                        href="http://faraday.biz.weibo.com/experiment3#/strategy/detail?strategy_set_id=84"
                        target="_blank"
                    >用户白名单策略配置</a>
                </el-form-item>
            </el-form>
        </el-row>

        <el-row>
            <loading :state="loadState" />

            <template v-for="(val , index) in newList">
                <el-card class="box-card" :key="index">
                    <el-row>
                        <el-col :span="4" class="pv-header">{{val.create_time}}</el-col>
                        <el-col :span="6">
                            pvid：
                            <strong>{{ val.pvid}}</strong>
                        </el-col>
                        <el-col :span="4" v-if="val.psid != ''">场景流：{{val.psid_name}}</el-col>
                        <el-col :span="4" style="font-size:14px">
                            投出广告
                             <el-tag effect="dark" :type="(val.winner > 0) ? 'success' : 'info'">{{val.winner}}</el-tag>
                            
                        </el-col>
                        <el-button
                            style="float: right; padding: 3px 0"
                            type="text"
                            @click="showDetail(val.req_list, val.pvid)"
                        >详细</el-button>
                    </el-row>
                </el-card>
            </template>
        </el-row>
        <el-row style="margin-top:10px;">
            <el-pagination
                @current-change="search"
                :current-page.sync="page"
                layout="total, prev, pager, next"
                :page-count="totalPage"
                align="center"
            ></el-pagination>
        </el-row>

        <el-drawer
            :title="drawerTitle"
            :visible.sync="showDrawer"
            direction="rtl"
            size="70%"
            :custom-class="'drawerscroll'"
        >
            <el-row style="overflow:scroll" :style="{'height' : drawerHeight}">
                <template v-for="(item, index) in currentDisplay">
                    <el-card class="box-card" style="margin:8px 7px;" :key="index">
                        <el-row slot="header" class="clearfix">
                            <strong>{{item.type}}</strong>
                            <template v-if="item.type == 'king'">
                                <a href="javascript:;" @click="getJson(item)">点击查看请求串</a>
                            </template>
                        </el-row>
                        <template v-if="item.type == 'king'">
                            <template v-for="(r, i) in item.king_req_display">
                                <p :key="i">
                                    <strong>{{r.name}}：</strong>
                                    {{r.text}}
                                </p>
                            </template>
                        </template>
                        <template v-else-if="item.type == 'fisher'">
                            <el-row
                                v-for="(item) in item.json.data"
                                style="margin:0 2px;padding:5px 0px"
                                :key="item.code"
                            >
                                <el-col :span="5" align="right">
                                    <el-tag type="success">{{item.remark}}({{item.code}})</el-tag>
                                </el-col>
                                <el-col :span="18">
                                    <template v-if="item.winner_detail != undefined">
                                        <template v-for="(winDetail, idx) in item.winner_detail">
                                            <span
                                                @mouseover="showMore($event, winDetail, true)"
                                                @mouseleave="showMore($event, winDetail, false)"
                                                class="adid"
                                            >
                                                <router-link
                                                    target="_blank"
                                                    style="color:#666;font-weight: bold;"
                                                    :to="{ path: '/insights/lean_matrix', query: { ad_id: winDetail.adid }}"
                                                >{{winDetail.adid}}</router-link>
                                            </span>
                                        </template>
                                    </template>
                                    <template v-else>
                                        <template v-for="(win, idx) in item.win">
                                            <span class="adid">
                                                <router-link
                                                    target="_blank"
                                                    style="color:#666;font-weight: bold;"
                                                    :to="{ path: '/insights/lean_matrix', query: { ad_id: win }}"
                                                >{{win}}</router-link>
                                            </span>
                                        </template>
                                    </template>
                                    <template v-for="(filt, idx) in item.filtered">
                                        <span
                                            @mouseover="showMore($event, filt, true)"
                                            @mouseleave="showMore($event, filt, false)"
                                            class="adid"
                                        >
                                            <s style="color:#999">
                                                <router-link
                                                    target="_blank"
                                                    :to="{ path: '/insights/lean_matrix', query: { ad_id: filt.adid }}"
                                                >{{filt.adid}}</router-link>
                                            </s>
                                        </span>
                                    </template>
                                </el-col>
                            </el-row>
                        </template>
                    </el-card>
                </template>
            </el-row>
        </el-drawer>

        <el-dialog title="Json串" :visible.sync="showJsonDialog">
            <ShowJson :showStr="moreJson"></ShowJson>
        </el-dialog>

        <el-drawer title="名单配置" :visible.sync="showDebugList" size="70%">
            <el-row style="padding:20px;">
                <el-button
                    type="danger"
                    @click="showAddDebugPerson = true"
                    style="margin-bottom:10px;"
                >添加配置</el-button>
                <DebugPersonList ref="DebugPersonList" />
            </el-row>

            <el-drawer
                title="添加配置"
                :append-to-body="true"
                size="40%"
                :visible.sync="showAddDebugPerson"
                :before-close="reGetDebugPersonList"
            >
                <el-row style="padding:20px;">
                    <AddDebugPerson @closeAddDebugPerson="reGetDebugPersonList" />
                </el-row>
            </el-drawer>
        </el-drawer>
    </el-row>
</template>

<script>
import moment from "moment";
import SceneFlow from "@/config/SceneFlow";
import LoginConf from "@/config/login_frequency";
import AddDebugPerson from "./AddDebugPerson.vue";
import DebugPersonList from "./DebugPersonList.vue";

export default {
    data() {
        return {
            uid: "",
            date: "",
            users: {},
            newList: {},
            totalPage: 0,
            page: 1,
            loadState: false,
            moreJson: "",
            hoverd: {},
            showDrawer: false,
            currentDisplay: [],
            drawerTitle: "",
            showJsonDialog: false,
            showDebugList: false,
            showAddDebugPerson: false,
            drawerHeight : '600px',
        };
    },
    components: {
        AddDebugPerson,
        DebugPersonList
    },
    mounted() {
        this.getUsers();

        this.drawerHeight = (document.documentElement.clientHeight - 150) + 'px'
        this.date = moment().format("YYYY-MM-DD");
        this.psidConf = this.$lodash.keyBy(SceneFlow, "value");
    },
    methods: {
        reGetDebugPersonList(done) {
            this.showAddDebugPerson = false;
            this.$refs["DebugPersonList"].getPandoraItems();
            done();
        },
        async search() {
            this.loadState = "loading";
            let obj = { uid: this.uid, date: this.date, page : this.page};
            this.newList = [];
            let res = await this.$api.insights.getLogData(obj);
            if (res.errcode == 1) {
                this.loadState = "no-data";
                return;
            }
            this.newList = this.formatRequestList(res.data.list);
            this.totalPage = res.data.total_page;
            this.loadState = "ok";
        },
        async getUsers() {
            let res = await this.$api.insights.getLogKeepUsers();
            this.users = res;

            if (Object.keys(res).length > 0) {
                if (this.users["2742705521"] != undefined) {
                    this.uid = "2742705521";
                    this.nickId = this.users["2742705521"];
                } else {
                    this.uid = Object.keys(res)[0];
                    this.nickId = res[Object.keys(res)[0]];
                }
            }
        },
        showDetail(data, title) {
            this.currentDisplay = data;
            this.drawerTitle = title;
            this.showDrawer = true;
        },
        jsonDecode(str) {
            try {
                return JSON.parse(str);
            } catch (e) {
                return {};
            }
        },
        getJson(msg) {
            this.moreJson = JSON.stringify(msg);

            this.showJsonDialog = true;
        },
        showMore(event, item, show) {
            if (show === false) {
                $(event.currentTarget).tooltip("destroy");
                this.hoverd[item.adid] = false;
                return;
            }
            if (
                this.hoverd[item.adid] != undefined &&
                this.hoverd[item.adid] == true
            ) {
                return;
            }
            this.hoverd[item.adid] = true;
            $(event.currentTarget)
                .tooltip({
                    placement: "top",
                    html: true,
                    title:
                        "adid:" +
                        item.adid +
                        "<br>price:" +
                        item.oprice +
                        "<br>rank_score:" +
                        item.rank_score
                    //container: 'body'
                })
                .tooltip("toggle");
        },
        formatKingReq(req) {
            if (Object.keys(req).length < 1) {
                return {};
            }
            let retData = [];
            retData.push({
                name: "年龄",
                text: req.user.age
            });
            retData.push({
                name: "设备",
                text: req.device.os + "、" + req.device.model
            });

            if (LoginConf[req.ext.login]) {
                retData.push({
                    name: "登录频次",
                    text: LoginConf[req.ext.login]
                });
            }
            retData.push({
                name: "com_ukv",
                text: req.ext.com_ukv
            });

            let pos = [];
            req.imp.forEach(item => {
                pos.push("第" + item.position + "位");
            });
            retData.push({
                name: "广告曝光位",
                text: pos.join("、")
            });
            return retData;
        },
        formatRequestList(list) {
            let fmtList = [];
            Object.keys(list).forEach(pvid => {
                let content = list[pvid];
                let tmpObj = {
                    pvid: pvid,
                    winner: 0,
                    psid: "",
                    psid_name: "",
                    create_time: ""
                };

                for (var i in content) {
                    let obj = this.jsonDecode(
                        content[i].trace.replace(/\n/g, "")
                    );
                    content[i].json = obj;
                    content[i].trace = "";
                    if (obj.winner != undefined) {
                        tmpObj.winner = obj.winner;
                    }
                    if (obj.psid != undefined) {
                        tmpObj.psid = obj.psid;
                        if (this.psidConf[tmpObj.psid] != undefined) {
                            tmpObj.psid_name = this.psidConf[tmpObj.psid].name;
                        } else {
                            tmpObj.psid_name = tmpObj.psid;
                        }
                    }
                    if (content[i].create_time > tmpObj.create_time) {
                        tmpObj.create_time = content[i].create_time;
                    }

                    if (content[i].type == "king") {
                        content[i].king_req_display = this.formatKingReq(
                            content[i].json
                        );
                    }
                }
                tmpObj.req_list = content;
                fmtList.push(tmpObj);
            });
            fmtList = this.$lodash.sortBy(fmtList, "create_time").reverse();
            for (var i in fmtList) {
                fmtList[i].req_list = this.$lodash.sortBy(
                    fmtList[i].req_list,
                    r => {
                        if (r.type == "king") {
                            return 0;
                        }
                        return 1;
                    }
                );
            }

            return fmtList;
        }
    }
};
</script>

<style lang="less">
.adid {
    display: inline-block;
    padding: 0 3px;
}
.pv-header {
    margin: 0 20px;
    font-size: 14px;
}
.m-p {
    float: left;
    font-size: 14px;
    margin: 10px 20px;
}
.p-title {
    padding: 0 10px;
    color: grey;
}
.p-con {
    background: #f66;
    border-radius: 5px;
    padding: 0 20px;
    color: white;
}
</style>
