<template>
    <div class="kepler-reserve-detail">
        <el-row>
            <el-card class="box-card">
                <div slot="header" class="clearfix">
                    <span>变更系数</span>
                </div>
                <div>
                    <el-form :inline="true" class="demo-form-inline" style="margin:10px 0 0 20px">
                        <el-form-item label="广告主ID:">
                            <el-input
                                v-model="custId"
                                placeholder="请输入广告主ID"
                                @keyup.enter.native="search"
                            ></el-input>
                        </el-form-item>
                        <el-form-item label="开始时间:">
                            <el-date-picker
                                v-model="startTime"
                                type="date"
                                placeholder="选择日期"
                                value-format="yyyy-MM-dd"
                            ></el-date-picker>
                        </el-form-item>
                        <el-form-item label="结束时间:">
                            <el-date-picker v-model="endTime" type="date" value-format="yyyy-MM-dd" placeholder="选择日期"></el-date-picker>
                        </el-form-item>
                        <el-form-item label="系数:">
                            <el-input
                                v-model="ratio"
                                placeholder="请输入变更系数"
                                @keyup.enter.native="search"
                            ></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="changeRatio">变更系数</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </el-card>
            <el-card style="margin-top:20px">
                <div slot="header" class="clearfix">
                    <span>查询</span>
                </div>
                <div>
                    <el-form :inline="true" class="demo-form-inline" style="margin:10px 0 0 20px">
                        <el-form-item label="广告主ID:">
                            <el-input
                                v-model="custSearchId"
                                placeholder="请输入广告主ID"
                                @keyup.enter.native="search"
                            ></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="search">查询</el-button>
                        </el-form-item>
                    </el-form>

                    <div class="row" style="margin-top:20px" v-show="detailList.custid">
                        <div class="col-md-12" style="overflow: scroll;">
                            <table class="table table-bordered table-hover table-responsive">
                                <thead>
                                    <tr class="info">
                                        <th>广告主ID</th>
                                        <th>系数</th>
                                        <th>开始时间</th>
                                        <th>结束时间</th>
                                        <th>更新时间</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>{{ detailList.custid }}</td>
                                        <td>{{ detailList.ratio }}</td>
                                        <td>{{ detailList.start_time }}</td>
                                        <td>{{ detailList.end_time }}</td>
                                        <td>{{ detailList.update_time }}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <loading :state="loadState" />

                    <div
                        class="modal fade"
                        tabindex="-1"
                        role="dialog"
                        aria-labelledby="myLargeModalLabel"
                        id="myModal"
                    >
                        <div class="modal-dialog modal-md" role="document">
                            <div class="modal-content">
                                <h3 style="margin-left: 30px;">{{ modalNotice }}</h3>
                            </div>
                        </div>
                    </div>
                </div>
            </el-card>
        </el-row>
    </div>
</template>

<script>
import * as CONST from "@/constant";
import dateFormat from "@/utils/dateFormat";

export default {
    data() {
        return {
            detailList: [],
            pageSize: 30,
            currentPage: 1,
            // 总页数
            totalPages: 0,
            loadState: "暂无数据",
            custId: "",
            custSearchId: "",
            originalData: {},
            startTime: "",
            endTime: "",
            ratio: "100",
            modalNotice: ""
        };
    },
    mounted() {
        this.startTime = dateFormat(new Date().getTime());
        this.endTime = dateFormat(new Date().getTime());
    },
    methods: {
        async search() {
            this.loadState = "loading";

            let res = await this.$api.insights.getRatio(
                parseInt(this.custSearchId)
            );
            let body = JSON.parse(res);
            console.log(body);
            if (body.errcode === 0) {
                this.loadState = "ok";
                this.detailList = body.data;
                return;
            }
            this.loadState = "暂无数据";
        },

        async changeRatio() {
            let data = {
                cust_id: parseInt(this.custId),
                ratio: parseInt(this.ratio),
                start_time: this.startTime,
                end_time: this.endTime
            };
            let res = await this.$api.insights.changeRatio(data);
            let body = JSON.parse(res);
            console.log(body);
            if (body.errcode == 0) {
                this.modalNotice = "变更系数成功";
            } else {
                this.modalNotice = "变更系数失败";
            }
            $("#myModal").modal("show");
        }
    }
};
</script>

