<template>
    <el-row>
        <el-form :inline="true">
            <el-form-item label="选择数据类型">
                <el-radio-group v-model="dataType">
                    <template v-for="(item, key, i) in dataTypeConf">
                        <el-radio :label="key" :key="i">{{item.name}}</el-radio>
                    </template>
                </el-radio-group>
            </el-form-item>
        </el-form>
        <el-row :gutter="30">
            <el-col :span="12">
                <p>
                    Redis实时数据
                    <i class="el-icon-loading" v-show="loading"></i>
                </p>
                <drawchart :chartData="chartData1" chartId="redisId" />
            </el-col>
            <el-col :span="12">
                <p>
                    实时请求数据
                    <i class="el-icon-loading" v-show="loading"></i>
                </p>
                <drawchart :chartData="chartData2" chartId="reqId" />
            </el-col>
        </el-row>
    </el-row>
</template>

<script>
import moment from "moment";

export default {
    props: ["adId", "timeRange"],
    data() {
        return {
            dataType: "imp_count",
            dataTypeConf: {
                imp_count: {
                    name: "曝光量",
                    ck_field: "cpl_imp"
                },
                cost: {
                    name: "消耗",
                    ck_field: "cpl_budget"
                },
                convert_count: {
                    name: "转化量",
                    ck_field: "cpl_convert"
                },
                cost_diff: {
                    name: "成本偏差",
                    ck_field: ""
                }
            },
            loading: false,
            cmData: {
                redis: [],
                clickhouse: [],
                ck_cost_diff: []
            }
        };
    },
    computed: {
        chartData1: function() {
            let tmpX = [];
            let yData = {};
            let yName = this.dataTypeConf[this.dataType].name;
            yData[yName] = [];
            if (this.cmData.redis == null) {
                return {
                    xAxis: tmpX,
                    data: yData
                };
            }
            this.cmData.redis.forEach((item, idx) => {
                tmpX.push(item.create_time.substr(10));
                yData[yName].push(item[this.dataType]);
            });
            return {
                xAxis: tmpX,
                data: yData
            };
        },
        chartData2: function() {
            let tmpX = [];
            let yData = {};
            let yName = this.dataTypeConf[this.dataType].name;
            let field = this.dataTypeConf[this.dataType].ck_field;
            yData[yName] = [];
            if (this.dataType == "cost_diff") {
                if (this.cmData.ck_cost_diff == null) {
                    return {
                        xAxis: tmpX,
                        data: yData
                    };
                }
                this.cmData.ck_cost_diff.forEach((item, idx) => {
                    tmpX.push(item.datetime.substr(10));
                    yData[yName].push(item.cost_diff);
                });
            } else {
                if (this.cmData.clickhouse == null) {
                    return {
                        xAxis: tmpX,
                        data: yData
                    };
                }
                this.cmData.clickhouse.forEach((item, idx) => {
                    tmpX.push(item.datetime.substr(10));
                    yData[yName].push(item[field]);
                });
            }

            return {
                xAxis: tmpX,
                data: yData
            };
        }
    },
    methods: {
        async getRedisReqData() {
            if (
                moment(this.timeRange[0])
                    .add(2, "days")
                    .isBefore(moment(this.timeRange[1]))
            ) {
                this.$message("选择的时间太长了，暂时只支持最长两天的时间范围");
                this.$emit("setLoading", false);
                return;
            }
            this.loading = true;
            this.cmData = {
                redis: [],
                clickhouse: [],
                ck_cost_diff: []
            };
            let res = await this.$api.insights.getOcpxCplCompare(
                this.adId,
                this.timeRange[0],
                this.timeRange[1]
            );
            this.loading = false;
            this.cmData = res.data;
        }
    }
};
</script>

