<template>
    <el-row>
         <p>今日最近50条</p>
        <el-table v-show="objType!=='total'" :data="objList" height="200px" border style="margin-bottom:30px;" id="cpl-object-list">
            <el-table-column :label="objectName" width="130">
                <template slot-scope="scope">
                    <el-link @click="inputObjVal(scope.row['object_value'])">{{scope.row['object_value']}}</el-link>
                </template>
            </el-table-column>
            <template v-for="(n, k, i) in dataTypeConf">
                <el-table-column :prop="k" :label="n" width="120" :key="i"></el-table-column>
            </template>
        </el-table>
        <el-form :inline="true" class="form">
            <el-col :span="10" :offset="1">
                <el-input
                    placeholder="请输入id"
                    v-model="objVal"
                    class="input-with-select"
                    :disabled="objType == 'total'"
                >
                    <template slot="prepend">
                        <el-select v-model="objType" @change="selectObjType">
                            <el-option label="广告计划" value="ad_id"></el-option>
                            <el-option label="广告主" value="customer_id"></el-option>
                            <el-option label="整体" value="total"></el-option>
                        </el-select>
                    </template>
                </el-input>
            </el-col>
            <el-col :span="2" :offset="1">
                <el-form-item>
                    <el-button type="primary" @click="queryAd" :loading="loading">查询</el-button>
                </el-form-item>
            </el-col>
            <el-col :span="2">
                <el-form-item>
                    <el-button v-if="objType!=='total'" type="warning" @click="randomOcpxId" :disabled="loading">手气不错</el-button>
                </el-form-item>
            </el-col>
        </el-form>
        <h3>回传量走势图</h3>
        <el-form class="showData">
            <el-form-item label="选择日期">
                <el-date-picker v-model="date" type="date" :value-format="'yyyy-MM-dd'"></el-date-picker>
            </el-form-item>
            <span>
                <el-link type="primary" @click="showDialog('line')">查看原数据</el-link>
            </span>
        </el-form>
        <el-form>
            <p>
                按小时走势
            </p>
            <el-radio-group v-model="radioType" >
                <el-radio v-for="(item, key, i) in dataTypeConf" :label="key" :key="i">{{item}}</el-radio>
            </el-radio-group>
        </el-form>
        <el-row id="line-hour">
            <drawchart :chartData="chartData3" chartId="cplTrend3" />
        </el-row>
         
        <el-form>
            <p>
                按天走势
            </p>
            <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAll">全选</el-checkbox>
            <el-checkbox-group v-model="checkType" @change="handleChecked">
                <el-checkbox v-for="(item, key, i) in dataTypeConf" :label="key" :key="i">{{item}}</el-checkbox>
            </el-checkbox-group>
        </el-form>
        <el-row id="line-day">
            <drawchart :chartData="chartData2" chartId="cplTrend2" />
        </el-row>
        <h3>回传量分布图</h3>
        <el-form class="showData">
             <el-form-item label="选择日期范围">
                <el-date-picker
                    v-model="timeRange"
                    type="daterange"
                    range-separator="至"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    :value-format="'yyyy-MM-dd'"
                ></el-date-picker>
            </el-form-item>
            <el-form-item label="选择维度" class="labelPie">
                <el-select v-model="dim_type">
                    <el-option
                    v-for="(val,key) in option"
                    :key="key"
                    :label="val"
                    :value="key"
                    :disabled="key === 'creative_id' && objType === 'total'">
                    </el-option>
                </el-select>
            </el-form-item>
             <span>
                <el-link type="primary" @click="showDialog('pie')">查看原数据</el-link>
            </span>
        </el-form>
       <drawpie :chartData="pieData" chartId="cplTrend1" id="pie-trend"/>
        <!-- 弹窗 -->
        <el-dialog :title="tableConf.title" :visible.sync="trendDialog" width="70%">
            <div v-for="(val,key) in tableList" :key="key">
                <h4>{{key}}</h4>
                <el-table :data=" Array.isArray(val) ? val :Object.values(val)" max-height="200px" border>
                    <el-table-column v-if="tableConf.hasDate" prop="t" label="时间" width="200"></el-table-column>
                    <template v-for="(n, k, i) in tableConf.header">
                        <el-table-column :prop="k" :label="n" :key="i"></el-table-column>
                    </template>
                </el-table>
            </div>
            <!-- <el-table :data="distributeData" height="400px" border>
                <el-table-column property="name" label="维度名" width="200"></el-table-column>
                <el-table-column property="send_total" label="发送量" width="200"></el-table-column>
            </el-table> -->
        </el-dialog>
    </el-row>
    
</template>

<script>
import moment from "moment"
import CplPostback from "./CplPostback";

export default {
    data() {
        return {
            objType: "ad_id",
            objVal: "",           
            loading: false,
            radioType: 'pv',
            checkType: ['pv'],
            checkAll: false,
            isIndeterminate: true,
            timeRange: [
                moment().subtract(7, "days").format("YYYY-MM-DD"),
                moment().format("YYYY-MM-DD")
            ],
            date: moment().format("YYYY-MM-DD"),
            dayList: {},
            hourList: {},  
            pieList: [],          
            dataTypeConf: {
                pv: "曝光量",
                bhv: "互动数",
                total_cpl: "线索收集数",
                ad_cpl: "广告线索收集数",
                mapping_cpl: "广告线索关联数",
                bhv_ratio: "互动率",
                cpl_ratio: "线索收集率",
                ad_cpl_ratio: "广告线索收集率",
                mapping_ratio: "关联成功率"
            },
            dim_type: 'source',
            option: {
                source:'来源',
                click_code:'互动行为',
                creative_id: '创意'
            },
            objList: [],
            loadingObj:{
                lock: false,
                text: "加载中",
                spinner: "el-icon-loading",
                background: "rgba(0, 0, 0, 0.7)"
            },
            trendDialog: false,
            tableList: {},
            tableConf: {}
        }
    },
    mounted() {
        this.selectObjType()
    },
    watch: {
        timeRange: function() {
            this.queryPie()
        },
        dim_type: function() {
            this.queryPie()
        },
        date: function() {
            this.queryTrend()
        }
    },
    components: {
        CplPostback
    },
    computed: {
        pieData: function() {
            let list = this.pieList, chartData = [],obj ={}
            if (list) {
                list.map(v => {
                   obj = v
                   obj['value'] = v['count'] 
                   delete obj['count']
                   chartData.push(obj)
                })
            }
            return {
                data: chartData 
            }
        },
        chartData2: function() {
            let list = this.dayList, chartData = {}
            if (list) {
                this.checkType.forEach(i => {
                    chartData[this.dataTypeConf[i]] = Object.values(list).map(v => v[i])
                })
            }
            return {
                xAxis: list? Object.keys(list): [],
                data: chartData 
            }
        },
        chartData3: function() {
            let tmpxAxis = [], chartData = {},list = this.hourList
            for (var i = 0; i < 24; i++) {
                if(i<10) {
                    i = `0${i}`
                }
                tmpxAxis.push(`${i}:00:00`)
            }
            for(var key in list) {
                chartData[key] = []
                tmpxAxis.forEach(h => {
                    if (list[key][h]) {
                        chartData[key].push(list[key][h][this.radioType])
                    } else {
                        chartData[key].push("")
                    }
                })
            }
            return {
                xAxis: tmpxAxis,
                data: chartData 
            }
        },
        objectName: function() {
            if (this.objType == "customer_id") {
                return "广告主"
            }
            return "广告"
        },
    },
    methods: {
        showDialog(val) {
            if(val === 'line') {
                this.tableConf.title="回传量趋势数据"
                this.tableConf.header = this.dataTypeConf
                this.tableConf.hasDate = true
                this.tableList = Object.assign(this.hourList,{'按天数据':this.dayList})
            } else{
                this.tableConf.title="回传量分布数据"
                this.tableConf.hasDate = false
                this.tableConf.header = {
                    name: '维度名',
                    value: '回传量'
                }
                this.tableList = {[`按${this.option[this.dim_type]}分布`]: this.pieList}
            }
            this.trendDialog = true
        },
        selectObjType() {
            this.objVal = ""
            if (this.objType !== "total") {
                this.getObjectList()
            } else{
                this.dim_type = 'source'
            }
        },
        inputObjVal(val) {
            this.objVal = val
            this.queryAd()
        },
        async getObjectList() {
            let l = this.$loading({
                ...this.loadingObj,
                target: "#cpl-object-list"
            })
            let res = await this.$api.insights.getOcpxCplList(this.objType)
            l.close()
            if (res.errcode == 0) {
                this.objList = res.data
            }
        },
        async randomOcpxId() {
            this.loading = true;
            let res = await this.$api.insights.randomOcpxObject('cpl');
            this.loading = false;
            this.objVal = res.data[this.objType];
            this.queryAd();
        },
        async queryPie() {
            if(!this.params) {
                return
            }
            let obj = {
                ...this.params,
                dim_type: this.dim_type,
                from: this.timeRange[0],
                to: this.timeRange[1]
            }
            let l = this.$loading({
                ...this.loadingObj,
                target: "#pie-trend"
            })
            let res = await this.$api.insights.getCplDistribute(obj)
            this.loading = false;
            l.close()
            if (res.data) {
                this.pieList = res.data
            }
        },
        async queryTrend() {
            if(!this.params) {
                return
            }
            let obj = {
                ...this.params,
                trend_type: 'hour',
                day: this.date
            }

            let l = this.$loading({
                ...this.loadingObj,
                target: "#line-hour"
            })
            let l1 = this.$loading({
                ...this.loadingObj,
                target: "#line-day"
            })
            let res1 = await this.$api.insights.getCplTrend(obj)
            l.close()
            this.loading = false
            if (res1.data) {
                this.hourList = res1.data
            }
            let res = await this.$api.insights.getCplTrend({...obj, trend_type: 'day'})
            l1.close()
            if (res.data) {
                this.dayList = res.data
            }
            
        },
        async queryAd() {
            if (!this.objVal.trim() && this.objType != "total") {
                this.$message(`请输入${this.objectName}id`)
                return
            }
            this.params = {
                object_type: this.objType,
                object_value: this.objVal
            }
            this.loading = true
            this.queryTrend()
            this.queryPie()
            
        },
        handleCheckAll(val) {
            this.checkType = val ? Object.keys(this.dataTypeConf) : [];
            this.isIndeterminate = false
        },
        handleChecked(value) {
            let checkedCount = value.length
            this.checkAll = checkedCount === this.checkType.length
            this.isIndeterminate = checkedCount > 0 && checkedCount < Object.keys(this.dataTypeConf).length
        }
        
    }
}
</script>
<style scoped>
    .form{
        overflow: hidden;
    }
    .labelPie{
        margin-left: 20px;
    }
    h3{
        border-bottom: 1px solid #DCDFE6;
        padding-bottom: 15px;
    }
    .el-checkbox-group{
        display: inline-block;
    }
    .input-with-select /deep/ .el-input-group__prepend {
        background-color: #fff;
        width: 130px;
    }
    .showData{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .showData /deep/ .el-form-item{
        min-width: 500px;
    }
</style>

