<template>
    <el-row>
        <h3>
            今日广告主消耗排行榜
            <span style="color: grey; font-size: 12px">(占整体消耗比例：{{percent}})</span>
        </h3>
        <el-divider></el-divider>

        <el-table
            :data="top10ConsumeCustomer"
            stripe
            style="width: 100%;margin-top:30px"
            v-show="loadState === 'ok'"
        >
            <el-table-column label="N.O." width="50">
                <template slot-scope="scope">{{(currentPage - 1) * 10 + scope.$index + 1}}</template>
            </el-table-column>
            <el-table-column label="消耗（元）">
                <template slot-scope="scope">
                    <strong style="color: red">{{scope.row.consume}}</strong>
                </template>
            </el-table-column>
            <el-table-column label="广告主" width="200px">
                <template slot-scope="scope">
                    <a
                        :href="'//weibo.com/u/' + scope.row.custid"
                        target="_blank"
                    >{{scope.row.cust_name || '无'}}</a>
                </template>
            </el-table-column>
            <el-table-column label="广告主ID">
                <template slot-scope="scope">
                    <router-link
                        :to="{ path: '/insights/customer', query: { customer_id: scope.row.custid }}"
                        target="_blank"
                    >{{ scope.row.custid }}</router-link>
                </template>
            </el-table-column>
            <el-table-column label="客户类型">
                <template slot-scope="scope">
                    <span style="color:grey">{{ scope.row.cust_type }}</span>
                </template>
            </el-table-column>
            <el-table-column label="行业">
                <template slot-scope="scope">{{scope.row.industry_name|| '无'}}</template>
            </el-table-column>
            <el-table-column label="粉丝数">
                <template slot-scope="scope">{{scope.row.followers_count || '无'}}</template>
            </el-table-column>
            <el-table-column label="身份">
                <template slot-scope="scope">{{scope.row.level_name || '无'}}</template>
            </el-table-column>
        </el-table>

        <el-pagination
            @current-change="changePage"
            :current-page.sync="currentPage"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :page-count="totalPages"
            align="center"
            style="margin:20px 0"
            v-show="loadState === 'ok'"
        ></el-pagination>
        <loading :state="loadState" />
    </el-row>
</template>

<script>
let totalData = [];
export default {
    data() {
        return {
            top10ConsumeCustomer: [],
            percent: "",
            pageSize: 10,
            currentPage: 1,
            // 总页数
            totalPages: 0,
            loadState: "暂无数据"
        };
    },
    mounted() {
        this.getTop10ConsumeCustomer();
    },
    methods: {
        async getTop10ConsumeCustomer() {
            this.loadState = "loading";
            let res = await this.$api.insights.getTop10ConsumeCustomer({
                product: "sfst",
                date: this.$moment().format("YYYYMMDD")
            });
            if (res.errcode === 0) {
                this.loadState = "ok";
                this.percent = res.percentage;
                let data = res.data;
                this.totalPages = Math.ceil(data.length / this.pageSize);
                for (let i = 0; i < this.totalPages; i++) {
                    totalData.push(
                        data.slice(i * this.pageSize, (1 + i) * this.pageSize)
                    );
                }
                this.top10ConsumeCustomer = totalData[0];
                return;
            }
            this.loadState = "暂无数据";
        },
        changePage(page) {
            this.currentPage = page;
            this.top10ConsumeCustomer = totalData[this.currentPage - 1];
        },

        search: function() {
            this.currentPage = 1;
            this.getTop10ConsumeCustomer();
        }
    }
};
</script>
