<template>
    <div
        style="margin-top: 5px;overflow-y:scroll; white-space: nowrap;padding-bottom: 10px;text-align: center;"
    >
        <template v-for="(item, index) in list">
            <div
                :style="{ 'width': item.progress_bar_wd, 'background' : item.ad_status_clr,'display' : 'inline-block',
                'position' : 'relative'}"
                :key="index"
            >
                <el-tooltip placement="top" :content="item.from + '~' + item.to">
                    <p
                        class="text-center"
                        style="padding-top:8px;font-size: 10px;cursor: pointer;"
                    >{{item.ad_status_text}}</p>
                </el-tooltip>
                <template v-for="(div,idx) in item.update_point">
                    <el-tooltip placement="top" :content="div.update_time + '更新'" :key="idx">
                        <div
                            style="width:5px;background:#bc82f0;position:absolute;height:100%;top:0;border-radius:15px 15px;}"
                            :style="{'left' : div.ratio + '%'}"
                        ></div>
                    </el-tooltip>
                </template>
            </div>
        </template>
    </div>
</template>

<script>
export default {
    props: ["adId", "date"],
    watch: {
        adId() {
            //this.getAdPlanStatTimeline();
        }
    },
    data() {
        return {
            list: []
        };
    },
    methods: {
        async getAdPlanStatTimeline() {
            let res = await this.$api.insights.getAdPlanStatTimeline(this.adId);
            if (res.data.length > 0) {
                this.list = res.data;
            } else {
                this.list = [];
            }
        }
    }
};
</script>
