 <template>
    <el-row>
        <el-row>
            <el-form label-width="80px">
                <el-form-item label="数据">
                    <el-radio-group v-model="chartTabType" @change="setChartLineData">
                        <el-radio-button label="ctr">CTR</el-radio-button>
                        <el-radio-button label="cvr">CVR</el-radio-button>
                        <el-radio-button label="pctr">PCTR</el-radio-button>
                        <el-radio-button label="pcvr">PCVR</el-radio-button>
                        <el-radio-button label="cpctr">校正CTR</el-radio-button>
                        <el-radio-button label="cpcvr">校正CVR</el-radio-button>
                        <el-radio-button label="ctr-obj">CTR - PCTR</el-radio-button>
                        <el-radio-button label="cvr-obj">CVR - PCVR</el-radio-button>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="日期">
                    <el-date-picker
                        v-model="date"
                        value-format="yyyy-MM-dd"
                        placeholder="选择日期"
                        @change="setChartLineData"
                    ></el-date-picker>
                </el-form-item>
                <el-form-item label="营销目标">
                    <el-radio-group v-model="promotionType" @change="setChartLineData">
                        <el-radio-button label>整体</el-radio-button>
                        <el-radio-button label="cpl">收集销售线索</el-radio-button>
                        <el-radio-button label="app">推广APP</el-radio-button>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="场景">
                    <el-radio-group v-model="psid" @change="setChartLineData">
                        <template v-for="(item, idx) in psidConf">
                            <el-radio-button :label="item.value" :key="idx">{{item.name}}</el-radio-button>
                        </template>
                    </el-radio-group>
                </el-form-item>
            </el-form>
        </el-row>
        <el-row>
            <drawchart
                chartId="adStatChartId"
                :chartData="adChartData"
                lengendPosition="top"
                percentItem="1"
            />
        </el-row>
    </el-row>
</template>

<script>
import moment from "moment";
import psidConf from "@/config/SceneFlow.js";
export default {
    data() {
        return {
            chartTabType: "pctr",
            statType: "total",
            adChartData: {
                title: "",
                data: {},
                xAixs: []
            },
            promotionType: "",
            psid: "0",
            date: "",
            psidConf : psidConf,
        };
    },
    mounted() {
        this.date = moment().format("YYYY-MM-DD");
        this.setChartLineData();
    },
    methods: {
        async setChartLineData() {
            this.adChartData = {
                title: "",
                data: {},
                xAixs: []
            };
            let res = await this.$api.insights.getOcpxAdChart(
                this.chartTabType,
                this.promotionType,
                this.date,
                this.psid,
            );
            if (res.errcode === 0) {
                this.adChartData = res.data;
            } else {
                this.$message("暂无数据");
            }
        }
    }
};
</script>